import React, {useEffect} from "react";
import Box from "@material-ui/core/Box";
import Grid from "@material-ui/core/Grid";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import {CustomTab, CustomTabs} from "../../components/materialize/CustomTab";
import tabSetProps from "../../components/materialize/smallElements/TabSetProps";
import Hidden from "@material-ui/core/Hidden";
import TabPanelBlocks from "./components/TabPanelBlocksCalendar";
import Copyright from "../../components/materialize/Copyright";
import useStylesDashboard from "../../css/useStyle/Dashboard.css";
import {useDispatch, useSelector} from "react-redux";
import {isBigScreenDrawerOpen} from "../../actions/effectsActions";

const Calendar = () => {
    const classes = useStylesDashboard();
    const [tabValue, setTabValue] = React.useState(0);
    const dispatch = useDispatch();
    const tabHandleChange = (event, newValue) => {
        setTabValue(newValue);
    };

    useEffect(() => {
        dispatch(isBigScreenDrawerOpen(true));
    },[dispatch])

    const screen = useSelector(state => state.screen );
    return (
        <main className={classes.content}>
            <div className={classes.appBarSpacer}/>
            <Box>
                <Grid container>
                    <Grid item xl={4} lg={4} md={4} xs={12} className={classes.calendarHeaderMenu}>
                        <AppBar position="static" className={classes.appBarTabs}>
                            <Toolbar className={screen.mainScreen === 'Calendar'? classes.toolbarPurpleTab : classes.toolbarWhite}>

                                <CustomTabs
                                    value={tabValue}
                                    onChange={tabHandleChange}
                                    indicatorColor="primary"
                                    textColor="primary"
                                    variant="scrollable"
                                    scrollButtons="auto"
                                    aria-label="scrollable auto tabs example"
                                >
                                    <CustomTab label="–ó–∞–∫–∞–∑—ã" {...tabSetProps(0)} />
                                    <CustomTab label="–§–∏–Ω–∞–Ω—Å—ã" {...tabSetProps(1)} />
                                    <CustomTab label="–ó–∞–¥–∞–Ω–∏—è" {...tabSetProps(2)} />
                                    <CustomTab label="–ó–∞–∫—É–ø–∫–∏" {...tabSetProps(3)} />
                                </CustomTabs>
                            </Toolbar>
                        </AppBar>
                        <div className={classes.greyLineMobOrders}></div>
                    </Grid>
                    <Hidden mdDown>
                        <Grid className={classes.adTopBlock}>
                            –ú–µ—Å—Ç–æ –ø–æ–¥ –±–∞–Ω–Ω–µ—Ä
                        </Grid>
                    </Hidden>
                </Grid>
            </Box>
            <TabPanelBlocks tabValue={tabValue}/>
            <Box pt={4}>
                <Copyright/>
            </Box>
        </main>
    )
}

export default Calendar;