import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import Switch from '@material-ui/core/Switch';
import DateInMobileBottomBlock from "../../../common/mobile/DateInMobileBottomBlock";

const TotalOrdersInfo = () => {
    const classes = useStyles();
    const [state, setState] = React.useState({
        checkedA: true,
        checkedB: true,
    });
    const handleSwitchChange = (event) => {
        setState({...state, [event.target.name]: event.target.checked});
    }
    return (
        <div className={classes.totalOrdersInfo}>
            <span className={classes.confirmed}><span className={classes.ordersNumberConfirmed}>30</span> –∑–∞–∫–∞–∑–æ–≤</span>
            <DateInMobileBottomBlock/>
            <span className={classes.notConfirmed}><span className={classes.notConfirmedSpan}>–ù–µ <br /> –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω—ã</span>
                    <span className={classes.ordersNumberNotConfirmed}>120</span>
                    <span className={classes.switch}>
                        <Switch
                            checked={state.checkedB}
                            onChange={handleSwitchChange}
                            color="primary"
                            name="checkedB"
                            inputProps={{'aria-label': 'primary checkbox'}}
                        />
                    </span>
            </span>
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    totalOrdersInfo: {
        display: 'flex',
        flexBasis: '50%',
        justifyContent: 'space-around',
        [theme.breakpoints.down('sm')]: {
            flexBasis: '91%',
        }
    },
    ordersNumberConfirmed: {
        color: '#9A157D',
        fontSize: '1.3rem'
    },
    switch: {
        display: 'none',
        [theme.breakpoints.down('sm')]: {
            display: 'block',
            width: '50px',
        }
    },
    confirmed: {
        fontWeight: '700',
        [theme.breakpoints.down('sm')]: {
            display: 'flex',
            flexWrap: 'wrap',
            alignItems: 'center',
            flexDirection: 'column',
            justifyContent: 'center',
            fontSize: '0.9rem'
        },
    },
    notConfirmedSpan: {
        marginRight: '10px',
        [theme.breakpoints.down('sm')]: {
            width: '81px',
            marginRight: '0',
            display: 'inline-block',
            textAlign: 'center',
            fontSize: '0.6rem'
        },
    },
    notConfirmed: {
        fontSize: '0.8rem',
        [theme.breakpoints.down('sm')]: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            background: '#e8dae5',
            margin: '10px 0',
            borderRadius: '10px',
            padding: '0',
        },
    },
    ordersNumberNotConfirmed: {
        fontSize: '1.1rem',
        color: '#747474',
        fontWeight: '700',
    }
}))

export default TotalOrdersInfo;