import React, {useEffect, useState} from "react";
import {makeStyles} from "@material-ui/core/styles";
import MaterialStoreOrdersListBlockLeft from './MaterialStoreOrdersListBlockLeft';
import PropTypes from "prop-types";

const MaterialStoreOrdersListBlock = ({showMobileOrders}) => {
  let [classesComputed, setClassesComputed] = useState('orderBlockCustomScrollBar');

  useEffect(() => {
    if (showMobileOrders) {
      setClassesComputed('orderBlockCustomScrollBarMobileShow orderBlockCustomScrollBarMobileShowMaterials');
    } else {
      setClassesComputed('orderBlockCustomScrollBar');
    }
  }, [showMobileOrders]);

  return (
      <div className={classesComputed}>
        <MaterialStoreOrdersListBlockLeft/>
      </div>
  )
}

const useStyles = makeStyles((theme) => ({

}))


MaterialStoreOrdersListBlock.propTypes = {
  showMobileOrders: PropTypes.bool.isRequired,
};

export default MaterialStoreOrdersListBlock;
