import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import AddOrderButton from "./AddOrderButton";
import OrderItem from "./OrderItem";
import {nanoid} from 'nanoid';
import {NavLink} from 'react-router-dom';

const OrdersListBlockLeft = () => {
    const classes = useStyles();
    const tortNumber = [1,2,3,4,5];
    return (
        <div className={classes.ordersListBlockLeft}>
             <NavLink exact to="/make_order" className={classes.router_link} > <AddOrderButton/></NavLink>
            {tortNumber.map(() => <OrderItem key={nanoid(5)} />)}
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    ordersListBlockLeft: {
        flexBasis: '47%',
        [theme.breakpoints.down('xs')]: {
            flexBasis: '100%'
        },
    },
    router_link: {
        textDecoration: 'none'
    }
}))

export default OrdersListBlockLeft;
