import TabPanel from "../../../components/materialize/TabPanel";
import Box from "@material-ui/core/Box";
import { withRouter } from 'react-router-dom'
import Grid from "@material-ui/core/Grid";
import ruLocaleW from "react-infinite-calendar/node_modules/date-fns/locale/ru";
import React, {useState} from "react";
import useStylesDashboard from "../../../css/useStyle/Dashboard.css";
import InfiniteCalendar, {Calendar, withRange} from 'react-infinite-calendar';
import '../../../css/components/react-infinite-calendar.css';
import {connect} from "react-redux";
import {setEndDate, setStartDate,setIsTodayButton} from "../../../actions/dateActions";
import Button from "@material-ui/core/Button";
import ChoiceDateButton from "../../../components/materialize/smallElements/ChoiceDateButton";
import OrdersRightBlock from "../../../components/blocks/orders/rightPanel/OrdersRightBlock";

const calendarConst = withRange(Calendar);

const TabPanelBlocksCalendar = ({tabValue, startDate, isTodayButton, endDate, setStartDate, setEndDate, setIsTodayButton}) => {
    const classes = useStylesDashboard();
    const [showDateChoiceButton, setShowDateChoiceButton] = useState(false);

    const onCalendarSelect = (e) => {
        if (e.eventType === 3 || !e.eventType) {
            setStartDate(new Date((e.start)));
            setEndDate(new Date((e.end)));
            setIsTodayButton(false);
            setShowDateChoiceButton(true);
            setTimeout(() => { setShowDateChoiceButton(false)}, 10000);
        }

    }

    return (
            <>
                <TabPanel value={tabValue} index={0} className={classes.calendarTabPanel}>
                    <Box>
                        <Grid container >
                            <Grid item xs={12} className={classes.calendarBlock}>
                                    <ChoiceDateButton active={showDateChoiceButton}>–í—ã–±—Ä–∞—Ç—å</ChoiceDateButton>
                                    <InfiniteCalendar
                                        Component={ isTodayButton ? withRange(Calendar) : calendarConst }
                                        width="100%"
                                        selected={{
                                            start: startDate,
                                            end: endDate
                                        }}
                                        onSelect={onCalendarSelect}
                                        displayOptions={{
                                            showHeader: true
                                        }}
                                        theme={{
                                            selectionColor: 'rgb(146, 118, 255)',
                                            textColor: {
                                                default: '#333',
                                                active: '#FFF'
                                            },
                                            weekdayColor: '#5D1049',
                                            headerColor: '#5D1049',
                                            floatingNav: {
                                                background: 'rgba(81, 67, 138, 0.96)',
                                                color: '#FFF',
                                                chevron: '#FFA726'
                                            }
                                        }}
                                        locale={{
                                            locale: ruLocaleW,
                                            headerFormat: 'DD MMMM',
                                            weekdays: ["–í—Å","–ü–Ω","–í—Ç","–°—Ä","–ß—Ç","–ü—Ç","–°–±"],
                                            MonthList: ["–Ø–Ω–≤","–§–µ–≤","–ú–∞—Ä—Ç","–ê–ø—Ä","–ú–∞–π","–ò—é–Ω—å","–ò—é–ª—è","–ê–≤–≥—É—Å—Ç–∞","–°–µ–Ω—Ç","–û–∫—Ç","–ù–æ—è","–î–µ–∫"],
                                            weekStartsOn: 1,
                                            todayLabel: {
                                                long: '–°–µ–≥–æ–¥–Ω—è',
                                                short: '–°–µ–≥.'
                                            }
                                        }}
                                    />
                                </Grid>
                            <OrdersRightBlock />
                        </Grid>
                    </Box>
                    <Button variant="outlined" color="primary"  onClick={() => {console.log("startDate", startDate);console.log("endDate", endDate)}}>
                        –£–∑–Ω–∞—Ç—å –æ—Ç—Ä–µ–∑–æ–∫
                    </Button>
                </TabPanel>
                <TabPanel value={tabValue} index={1}>
                    Item Two
                </TabPanel>
                <TabPanel value={tabValue} index={2}>
                    Item Three
                </TabPanel>
                <TabPanel value={tabValue} index={3}>
                    Item Four
                </TabPanel>
            </>
    )
}

const mapStateToProps = state => ({
    startDate: state.date.startDate,
    endDate:  state.date.endDate,
    isTodayButton: state.date.isTodayButton
});

export default withRouter(connect(
    mapStateToProps,
    {setEndDate,setStartDate,setIsTodayButton}
)(TabPanelBlocksCalendar));

