import {
    IS_BIG_SCREEN_DRAWER_OPEN,
    IS_INFO_SAVE,
    IS_SHOW_TOP_PANEL,
} from '../actions/types/EffectsTypes';

const initialState = {
    isBigScreenDrawerOpen: true,
    isInfoSave: false,
    isShowTopPanel: true,
};

const effectsReducer = (state = initialState, action) => {
    switch (action.type) {
        case IS_BIG_SCREEN_DRAWER_OPEN:
            return {
                ...state,
                isBigScreenDrawerOpen: action.payload,
            };
        case IS_INFO_SAVE:
            return {
                ...state,
                isInfoSave: action.payload,
            };
        case IS_SHOW_TOP_PANEL:
            return {
                ...state,
                isShowTopPanel: action.payload,
            };
        default:
            return state;
    }
};

export default effectsReducer;
