import TabPanel from "../../../components/materialize/TabPanel";
import Box from "@material-ui/core/Box";
import { withRouter } from 'react-router-dom'
import Grid from "@material-ui/core/Grid";
import React  from "react";
import '../../../css/components/react-infinite-calendar.css';
import {connect} from "react-redux";
import {setEndDate, setStartDate} from "../../../actions/dateActions";
import MaterialsRightBlock from "./MaterialsRightBlock";
import MaterialsLeftBlock from "./MaterialsLeftBlock";
import {makeStyles} from "@material-ui/core/styles";
import LeftBottomBlockSales from "./LeftBottomBlockSales";
import MaterialStoreRightBlock from './materialStore/MaterialStoreRightBlock';
import MaterialStoreLeftBlock from './materialStore/MaterialStoreLeftBlock';

const TabPanelBlocksIngredients = ({tabValue}) => {
    const classes = useStyles();

    return (
        <>
            <TabPanel className={classes.meterialLeftPanel} value={tabValue} index={0} >
                <Box>
                    <Grid container >
                        <Grid item xs={12} className={classes.meterialLeft}>
                            <MaterialsLeftBlock />
                            <LeftBottomBlockSales/>
                        </Grid>
                        <MaterialsRightBlock />
                    </Grid>
                </Box>
            </TabPanel>
            <TabPanel className={classes.meterialLeftPanel} value={tabValue} index={1}>
                <Box>
                  <Grid container >
                    <Grid item xs={12} className={classes.meterialLeft}>
                      <MaterialStoreLeftBlock />
                      <LeftBottomBlockSales/>
                    </Grid>
                    <MaterialStoreRightBlock />
                  </Grid>
                </Box>
            </TabPanel>
            <TabPanel value={tabValue} index={2}>
                Item Three Ingredients
            </TabPanel>
            <TabPanel value={tabValue} index={3}>
                Item Four Ingredients
            </TabPanel>
        </>
    )
}

const useStyles = makeStyles((theme) => ({
    meterialLeft: {
        maxWidth: '420px!important',
        position: 'relative',
        height: 'fit-content',
        padding: '2%'
    },
    meterialLeftPanel: {
        position: 'relative',
        height: '674px'
    }
}))

const mapStateToProps = state => ({
    startDate: state.date.startDate,
    endDate:  state.date.endDate
});

export default  withRouter (connect(
    mapStateToProps,
    {setEndDate,setStartDate}
)(TabPanelBlocksIngredients));

