import axios from 'axios';

const DeleteCategoryList = async (id, getCategories, handleCloseDelete ) => {
    try {
        // –∏–º–µ–Ω–Ω–æ —Ç–∞–∫ axios —Ç—Ä–µ–±—É–µ—Ç —á—Ç–æ–±—ã —á–µ—Ä–µ–∑ delete –±—ã–ª–æ
        let config = {
            headers: {
                Authorization: 'Bearer ' + localStorage.getItem('token'),
            },
        };

        axios.delete('/categories/' + id, config).then(() => {
            getCategories();
            handleCloseDelete()
        }).catch(er => {
            console.error(er);
        });
    } catch (e) {
        console.error(e);
    }




};

export default DeleteCategoryList;