import React, {useEffect} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import AddMaterialButton from './AddMaterialButton';
import MaterialItem from './MaterialItem';
import {useDispatch, useSelector} from 'react-redux';
import {
    addNewIngredients,
    deleteNewIngredients,
    getSavedIngredients,
} from '../../../actions/ingredients/ingredientActions';
import {nanoid} from 'nanoid';
import orderBy from 'lodash/orderBy';
import SpinnerColor
    from '../../../components/materialize/smallElements/SpinnerColor';
import axios from 'axios';

const OrdersListBlockLeft = () => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const newIngredientsArr = useSelector(
        state => state.ingredients.ingredientsList.newIngredients);
    const savedIngredientsArr = useSelector(
        state => state.ingredients.ingredientsList.savedIngredients);
    const currentIngredientsCategory = useSelector(
        state => state.ingredients.ingredientsList.currentIngredientsCategory);
    const loadingIngredients = useSelector(
        state => state.ingredients.ingredientsList.loadingIngredients);


    useEffect(() => {
        dispatch(getSavedIngredients(currentIngredientsCategory.id));
    }, [currentIngredientsCategory.id, dispatch]);

    const addLineFunc = () => {
        dispatch(addNewIngredients({
            category_id : currentIngredientsCategory.id,
            id: 0,
            nanoId: nanoid(5),
            title: '',
            weight: [100],
            price: [100],
            price_per_gram: 1,
            updated_at: new Date().toISOString(),
        }));
    };

    const deleteLineFunc = (num, savedIngredients, nanoId) => {
        if (savedIngredients) {
            try {
                // –∏–º–µ–Ω–Ω–æ —Ç–∞–∫ axios —Ç—Ä–µ–±—É–µ—Ç —á—Ç–æ–±—ã —á–µ—Ä–µ–∑ delete –±—ã–ª–æ
                let config = {
                    headers: {
                        Authorization: 'Bearer ' + localStorage.getItem('token'),
                    },
                    data: {
                        'ingredient_ids': [num],
                    },
                };

                axios.delete('/ingredient_amounts', config).then(response => {
                    dispatch(getSavedIngredients(currentIngredientsCategory.id));
                }).catch(er => {
                    console.error(er)
                });
            } catch (e) {
                console.error(e);
            }
        } else {
          // –µ—Å–ª–∏ —ç—Ç–æ –Ω–æ–≤—ã–π –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç
            dispatch(deleteNewIngredients(nanoId));
        }
    };

    return (
        <div className={classes.ordersListBlockLeft}>
            <AddMaterialButton addLine={addLineFunc}/>
            { orderBy(newIngredientsArr, ['nanoId'], ['desc'])
                                    .map(e => <MaterialItem key={e.nanoId} elem={e}
                                    deleteLine={deleteLineFunc} ingregientType='new'/>)
            }
            {loadingIngredients ? (<div className="fullWidthFullHeightCenter">
                <SpinnerColor />
            </div>)
                :
                (

                    orderBy(savedIngredientsArr, ['id'], ['desc'])
                      .map(m => <MaterialItem key={m.id} elem={m}
                                            deleteLine={deleteLineFunc}
                                               savedIngredients={true} ingregientType='old'/>)
                )
            }
        </div>
    );
};

const useStyles = makeStyles((theme) => ({
    ordersListBlockLeft: {
        flexBasis: '100%',
        [theme.breakpoints.down('xs')]: {
            flexBasis: '100%',
        },
    },
}));

export default OrdersListBlockLeft;
