import React, {useEffect, useState} from "react";
import {makeStyles} from "@material-ui/core/styles";
import {useDispatch} from "react-redux";
import {isBigScreenDrawerOpen} from "../../../actions/effectsActions";
import {isShowTopPanel} from "../../../actions/effectsActions";
import useWindowSize from "../../../hooks/useWindowSize";
import GoldArrowBack from "../../../components/common/Icons/GoldArrowBack";
import IconButton from "@material-ui/core/IconButton";
import {NavLink} from "react-router-dom";
import ButtonMore from "../../../components/common/Icons/ButtonMore";
import MakeOrderMakeClient from './MakeOrderMakeClient';
import MakeOrderClientCreated from './MakeOrderClientCreated';
import Collapse from '@material-ui/core/Collapse';
import AddMaterialButton from '../../Materials/components/AddMaterialButton';
import MakeOrderWrapper from './MakeOrderWrapper';
import {setScreen} from '../../../actions/screenAction';
import MakeOrderMakeOrder from "./MakeOrderMakeOrder";

const MakeOrderIndex = () => {

  const dispatch = useDispatch();
  const [openEdit, setOpenEdit] = useState(true);
  const [openClientDetailed, setOpenClientDetailed] = useState(false);
  const [showSaved, setShowSaved] = useState(false);
  const [showCreateOrder, setShowCreateOrder] = useState(true);
  const [showCreateOrderMobile, setShowCreateOrderMobile] = useState(false);
  const classes = useStyles({showCreateOrderMobile})();


  const windowSize = useWindowSize();

  const saveOrder = () => {

  }

  useEffect(() => {
    dispatch(setScreen({mainScreen: 'AddOrder'}));
    dispatch(isBigScreenDrawerOpen(false));
    if (windowSize < 668) {
      dispatch(isShowTopPanel(false));
    }
  }, [dispatch, windowSize])

  const isOpen = (open) => {
    setOpenEdit(open);
  }

  const isOpenClientDetailed = (open) => {
    setOpenClientDetailed(open);
    console.log('openClientDetailed', openClientDetailed);
  }

  const isSavedClientShow = (open) => {
    setShowSaved(open);
  }

  const showCreateOrderHandler = () => {
    setShowCreateOrder(!showCreateOrder)
  };

  const showCreateOrderHandlerMobile = () => {
    setShowCreateOrderMobile(!showCreateOrderMobile)
  }

  return (
    <div className={classes.makeRecipesTab}>
      <div>
        <div className={classes.topPurpleLine}>
          <div className={classes.leftlineBlock}>
            <NavLink exact to="/">
              <IconButton color="secondary">
                <GoldArrowBack/>
              </IconButton>
            </NavLink>
            <div className={classes.plusIcon}>+</div>
            –ö–∞–ª–µ–Ω–¥–∞—Ä—å
          </div>
          <div className={classes.rightlineBlock}>
            {(windowSize > 668) &&
            <div className={classes.rightlineCancelButton}>
              –û—Ç–º–µ–Ω–∏—Ç—å –∏–∑–º–µ–Ω–µ–Ω–∏—è
            </div>
            }
            <div className={classes.rightlineSaveButton} onClick={saveOrder}>
              –°–æ—Ö—Ä–∞–Ω–∏—Ç—å
              <div className={classes.moreButtonWrapper}>
                <ButtonMore className={classes.moreButton}/>
              </div>
            </div>
          </div>
        </div>
        <div className={classes.makeOrdersBlocksAll}>
          <div className={classes.makeOrdersBlocksFirst}>
            <div className={showSaved ? classes.showSavedShow : classes.showSavedHide}>
              <MakeOrderClientCreated mobileDetailed={false} isOpen={isOpen}
                                      isOpenClientDetailed={isOpenClientDetailed}/>
            </div>
            {(windowSize < 668) &&
            <>
              <div className={classes.mobile}>
                <Collapse in={openClientDetailed}>
                  <MakeOrderClientCreated mobileDetailed={true} isOpen={isOpen}
                                          isOpenClientDetailed={isOpenClientDetailed}/>
                </Collapse>
              </div>
            </>
            }
            <Collapse in={openEdit}>
              <MakeOrderMakeClient showSaved={isSavedClientShow}/>
            </Collapse>
          </div>
          <div className={classes.makeOrdersBlocksSecond}>
            <AddMaterialButton addLine={showCreateOrderHandler}/>
            {showCreateOrder && <MakeOrderWrapper/>}
          </div>
        </div>
      </div>
      { showCreateOrderMobile && <MakeOrderMakeOrder />}
      {(windowSize < 668) && <div className={classes.mobAddOrderButtonWrapper}><AddMaterialButton addLine={showCreateOrderHandlerMobile}/></div>}
    </div>
  );
};

const useStyles = props => makeStyles(() => {
  const styleObject = {
    makeRecipesTab: {
      width: "100%",
      marginTop: "65px",
      "@media (max-width:668px)": {
        marginTop: "0",
      },
    },
    moreButton: {},
    moreButtonWrapper: {
      marginLeft: "10px",
      "& .MoreVertIcon-root-72": {
        color: "#fff!important",
      },
      "@media (min-width:668px)": {
        display: "none",
      },
    },
    desktop: {},
    mobile: {
      "@media (min-width:668px)": {
        display: "none",
      },
    },
    topPurpleLine: {
      display: "flex",
      justifyContent: "space-between",
      backgroundColor: global.config.colors.purple,
      color: "#fff",
      padding: "6px 2%",
    },
    leftlineBlock: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    },
    rightlineBlock: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    },
    arrowIcon: {
      fill: "#fff",
      width: "20px",
      cursor: "pointer",
    },
    plusIcon: {
      marginLeft: "7px",
      marginRight: "7px",
    },
    rightlineCancelButton: {
      marginRight: "7px",
      cursor: "pointer",
      "@media (max-width:668px)": {},
    },
    rightlineSaveButton: {
      color: global.config.colors.purple,
      backgroundColor: "#fff",
      borderRadius: "20px",
      padding: "3px 12px",
      cursor: "pointer",
      "@media (max-width:668px)": {
        background: "none",
        color: "#fff",
        display: "flex",
      },
    },
    makeOrdersBlocksFirst: {
      display: "flex",
      flexBasis: '25%',
      minWidth: '385px',
      flexDirection: "column",
      paddingTop: "30px",
      "@media (max-width:668px)": {
        flexBasis: '100%',
        minWidth: '320px',
      },
    },
    makeOrdersBlocksSecond: {
      display: "flex",
      flexBasis: '45%',
      minWidth: '800px',
      flexDirection: "column",
      paddingTop: "30px",
      "@media (max-width:1200px)": {
        display: 'none'
      },
    },
    secondAndThirdWrapper: {
      display: "flex",
      flexBasis: "50%",
      "@media (max-width:1300px)": {
        flexWrap: "wrap",
      },
    },
    showSavedShow: {
      display: 'block'
    },
    showSavedHide: {
      display: 'none'
    },
    makeOrdersBlocksAll: {
      display: 'flex'
    },
    mobAddOrderButtonWrapper: {
      width: '95%',
      margin: 'auto'
    }
  }

  if(props.showCreateOrderMobile) {
    styleObject.makeOrdersBlocksAll = {
      display: 'none'
    }
  }

  return styleObject;
});

export default MakeOrderIndex;
