import {
    ADD_PRODUCT,
    EDIT_PRODUCT,
    DELETE_PRODUCT,
    ADD_STANDARDISATION,
    EDIT_PRODUCT_LOOSES,
    EDIT_TEST_EXIT,
    CHANGE_PRODUCT_SAVE_BUTTON_STATE,
    CHANGE_STANDARTISATION
} from '../../actions/types/Products/MakeProductTypes';

const initialState = {
    data: {
        ProductName: '',
        productTotalWeight: 0,
        ProducLooseWeight: 0,
        looses: 6,
        exit: 0,
        testExit: 0,
        priceKg: 0,
        standardisation: {
            enabled: false,
            d: 0,
            h: 0,
            coverWeight: 0,
        },
        products: [], // [{id: 1, name: 'milk',percent: 0,weight: 1000}]
        testProducts: [],
    },
    saveProductButtonActive: false
};


const calcPercent = (ProductsArray) => {
    let totalWeight = ProductsArray.reduce((sum, elem) => {
        return sum + parseInt(elem.weight);
    }, 0);
    let resultProducts = ProductsArray.map(item => {
        return {
            ...item,
            percent: Math.round((item.weight / totalWeight) * 100),
        };
    });

    return {
        totalWeight,
        resultProducts,
    };
};

const recountToNewWeight = (newWeight, ProductsArray) => {
    return ProductsArray.map((item) => {
        return  {
            ...item,
            weight: Number(((newWeight / 100) * item.percent).toFixed(0))
        }
    })
}
const calcPriceKg = (ProductsArray, exit) => {

    let priceByProduct = ProductsArray.map((item) => {
        if(!item.weight) return 0;
        return item.weight * item.price;
    });

    let totalSum = priceByProduct.reduce((sum, elem) => {
        return sum + parseInt(elem);
    }, 0);

    if(totalSum !== 0) {
        let totalOnegramPrice = totalSum / exit;
        return Number((totalOnegramPrice * 1000).toFixed(0));
    } else {
        return 0;
    }
};


const calcExit = (val, looses) => {
    
    if (looses !== 0) {
        return val - (val / 100 * looses);
    } else {
        return val;
    }
};

const makeProductReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_PRODUCT:
            let newProdArr = [...state.data.products , action.payload];
            const resultAdd = calcPercent(newProdArr);

            return {
                ...state,//—Å—Ç–∞—Ä–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ
                data: {
                    ...state.data,
                    exit: calcExit(resultAdd.totalWeight, state.data.looses),//–Ω–æ–≤—ã–π –≤—ã—Ö–æ–¥
                    testExit: calcExit(resultAdd.totalWeight, state.data.looses),
                    priceKg: calcPriceKg(resultAdd.resultProducts,
                      calcExit(resultAdd.totalWeight, state.data.looses)),
                    productTotalWeight: resultAdd.totalWeight,//–Ω–æ–≤—ã–π –≤–µ—Å
                    products: resultAdd.resultProducts, //–Ω–æ–≤—ã–π –≤—ã—Ö–æ–¥
                    testProducts: resultAdd.resultProducts,
                }

            };
        case EDIT_PRODUCT:
            const filterProducts = state.data.products.filter(
                (item) => item.nanoId !== (action.payload.nanoId));
            const newproducts = [...filterProducts, action.payload];
            const resultEdit = calcPercent(newproducts);

            return {
                ...state,
                data: {
                    ...state.data,
                    exit: calcExit(resultEdit.totalWeight, state.data.looses),
                    testExit:  calcExit(resultEdit.totalWeight, state.data.looses),
                    priceKg: calcPriceKg(resultEdit.resultProducts,
                      calcExit(resultEdit.totalWeight, state.data.looses)),
                    productTotalWeight: resultEdit.totalWeight,
                    products: resultEdit.resultProducts,
                    testProducts: resultEdit.resultProducts,
                }
            };
        case DELETE_PRODUCT:
            const resultproductsArr = state.data.products.filter(
                (elem) => elem.nanoId !== action.payload);
            const resultDelete = calcPercent(resultproductsArr);
            return {
                ...state,
                ...state,
                data: {
                    ...state.data,
                    exit: calcExit(resultDelete.totalWeight, state.data.looses),
                    testExit: calcExit(resultDelete.totalWeight, state.data.looses),
                    priceKg: calcPriceKg(resultDelete.resultProducts,
                      calcExit(resultDelete.totalWeight, state.data.looses)),
                    productTotalWeight: resultDelete.totalWeight,
                    products: resultDelete.resultProducts,
                    testProducts: resultDelete.resultProducts,
                }
            };
        case EDIT_PRODUCT_LOOSES:
            return {
                ...state,
                data: {
                    ...state.data,
                    exit: calcExit(state.data.productTotalWeight, action.payload),
                    testExit:  calcExit(state.data.productTotalWeight, action.payload),
                    priceKg: calcPriceKg(state.data.products,
                      calcExit(state.data.productTotalWeight, action.payload)),
                    looses: action.payload,
                }

            };
        case EDIT_TEST_EXIT:
            return {
                ...state,
                data: {
                    ...state.data,
                    testExit: action.payload,
                    testProducts: recountToNewWeight(action.payload, state.data.testProducts)
                }

            };
        case ADD_STANDARDISATION:
            return {
                ...state,
                data: {
                    ...state.data,
                    standardisation: action.payload,
                }
            };
        case CHANGE_PRODUCT_SAVE_BUTTON_STATE:
            return {
                ...state,
                saveProductButtonActive: action.payload
            };
        case CHANGE_STANDARTISATION:
            return {
                ...state,
                data: {
                    ...state.data,
                    standardisation: action.payload,
                }
            };
        default:
            return state;
    }
};

export default makeProductReducer;
