import React, {useEffect, useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import SpinnerOne from "./smallElements/SpinnerOne";
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import CloseIcon from '@material-ui/icons/Close';
import signin from '../../images/signin.svg';
import signinMob from '../../images/sing_in_mobile.svg';
import useWindowSize from "../../hooks/useWindowSize";
import axios from 'axios';
import {withRouter} from "react-router-dom";
import {connect, useSelector} from "react-redux";
import {registerSuccess, registerFail, loginSuccess, loginFail} from "../../actions/authAction";


function getModalStyle() {
    const top = 50;
    const left = 50;

    return {
        top: `${top}%`,
        left: `${left}%`,
        transform: `translate(-${top}%, -${left}%)`,
    };
}

const AccountLogin = ({ registerSuccess, registerFail, loginSuccess, loginFail, user }) => {

    const classes = useStyles();
    const windowSize = useWindowSize();
    // getModalStyle is not a pure function, we roll the style only on the first render
    const [modalStyle] = useState(getModalStyle);
    const [open, setOpen] = useState(true);
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [loginResult, setLoginResult] = useState('');
    const [registerResult, setRegisterResult] = useState('');
    const [loginSuccessClass, setLoginSuccessClass] = useState(false);
    const [authLogin, setAuthLogin] = useState(true);
    const [rememberPassword, setRememberPassword] = useState(false);
    const [registerSuccessClass, setRegisterSuccessClass] = useState(true);
    const [signupEmailError, setSignupEmailError] = useState(false);
    const [signupPassError, setSignupPassError] = useState(false);
    const [signinEmailError, setSigninEmailError] = useState(false);
    const [signinPassError, setSigninPassError] = useState(false);
    const [loading, setLoading] = useState(false);
    const [loginSuccussLoading, setLoginSuccussLoading] = useState(false);
    const screen = useSelector(state => state.screen );

    const handleOpen = () => {
        setOpen(true);
    };

    useEffect(() => {
        if(user.isAuthenticated) {
            setOpen(false);
        }
    },[])

    const handleClose = () => {
        if(user.isAuthenticated) {
            setOpen(false);
        }
    };

    const sendRememberPassword = async (e) => {
        e.preventDefault();

        let cleanedEmail = email.trim();
        if(!cleanedEmail) {
            setSigninEmailError("–ü–æ–ª–µ email –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º");
            return false;
        } else {
            setSigninEmailError(false);
        }

        setLoading(true);
        await axios.post('https://polar-tundra-51935.herokuapp.com/password ', {
            "user": {
                email:cleanedEmail,
            }
        });
    }

    const signIn = async (e) => {
        e.preventDefault();
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        let cleanedEmail = email.trim();
        let cleanedPass = password.trim();

        if(!cleanedEmail) {
            setSigninEmailError("–ü–æ–ª–µ email –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º");
            return false;
        } else {
            setSigninEmailError(false);
        }
        if(!cleanedPass) {
            setSigninPassError("–ü–∞—Ä–æ–ª—å –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º");
            return false;
        } else {
            setSigninPassError(false);
        }

        try {
            setLoading(true);
            const resp = await axios.post('https://polar-tundra-51935.herokuapp.com/login', {
                "user": {
                    email:cleanedEmail,
                    password:cleanedPass
                },
                config
            })

            if (resp.statusText === 'OK') {

                const token = resp.headers.authorization.replace('Bearer ', '');
                localStorage.setItem('token', token);
                setLoginResult("–£—Å–ø–µ—à–Ω–æ!");
                setLoginSuccessClass(true);
                setLoading(false);
                loginSuccess(resp.data);
                setTimeout(() => {
                    setLoginSuccussLoading(true);
                    setTimeout(() => {
                        setLoginSuccussLoading(false);
                        setOpen(false);
                    },2000)
                }, 2000)

            }

        } catch (e) {
            setLoginResult("–ù–µ –ø–æ–ª—É—á–∏–ª–æ—Å—å –∑–∞–ª–æ–≥–∏–Ω–∏—Ç—å—Å—è!");
            setLoginSuccessClass(false);
            setLoading(false);
            localStorage.removeItem('token');
            loginFail();
        }
    }

    const signUp = async (e) => {
        e.preventDefault();

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }
        let cleanedEmail = email.trim();
        let cleanedPass = password.trim();

        if(!cleanedEmail) {
            setSignupEmailError("–ü–æ–ª–µ email –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º");
            return false;
        } else {
            setSignupEmailError(false);
        }
        if(!cleanedPass) {
            setSignupPassError("–ü–∞—Ä–æ–ª—å –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º");
            return false;
        } else {
            setSignupPassError(false);
        }


        try {
            setLoading(true);
            const resp = await axios.post('https://polar-tundra-51935.herokuapp.com/signup', {
                "user": {
                    email:cleanedEmail,
                    password:cleanedPass
                }
            }, config)

            if (resp.data.email) {
                setRegisterResult("–í—ã –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–ª–∏—Å—å!")
                setRegisterSuccessClass(true)
                setLoading(false);
            }

        } catch (e) {
            setRegisterResult("–ù–µ –ø–æ–ª—É—á–∏–ª–æ—Å—å –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è!");
            setRegisterSuccessClass(false);
            setLoading(false);
        }
    }

    const bodyLogin = (
        <div style={modalStyle} className={classes.paper}>
            <IconButton className={classes.closeBtn} size="medium" aria-label="–∑–∞–∫—Ä—ã—Ç—å" onClick={handleClose}>
                <CloseIcon fontSize="small"/>
            </IconButton>
            <Container component="main" maxWidth="xs">
                <CssBaseline/>
                <div className={classes.paperSingIn}>
                    <Typography component="h2" variant="h5"> –ê–≤—Ç–æ—Ä–∏–∑—É–π—Ç–µ—Å—å </Typography>
                    <div className={loginSuccessClass ? classes.loginMessageSuccess : classes.loginMessageFail}>
                        {loginResult}
                    </div>
                    <form className={classes.form} noValidate>
                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="email"
                            label="–í–∞—à Email"
                            value={email}
                            name="email"
                            autoComplete="email"
                            autoFocus
                            onChange={(e) => {
                                setEmail(e.target.value)
                            }}
                        />
                        <span className={classes.fieldError}>{signinEmailError}</span>
                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            name="password"
                            label="–ü–∞—Ä–æ–ª—å"
                            type="password"
                            id="password"
                            value={password}
                            autoComplete="current-password"
                            onChange={(e) => {
                                setPassword(e.target.value)
                            }}
                        />
                        <span className={classes.fieldError}>{signinPassError}</span>
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            className={classes.submit}
                            onClick={signIn}
                        >
                            {loading ?  <SpinnerOne /> : '–í–æ–π—Ç–∏'}

                        </Button>
                        <Grid container>
                            <Grid item xs>
                                <Link className={classes.alink} href="#" variant="body2"  onClick={() => setRememberPassword(true)}>
                                    –ó–∞–±—ã–ª–∏ –ø–∞—Ä–æ–ª—å
                                </Link>
                            </Grid>
                            <Grid item>
                                <Link className={classes.alink} href="#" variant="body2" onClick={() => {setAuthLogin(false);setRememberPassword(false)}}>
                                    –ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è
                                </Link>
                            </Grid>
                        </Grid>
                    </form>
                </div>
            </Container>
        </div>
    );

    const bodyRememberPassword = (
        <div style={modalStyle} className={classes.paper}>
            <IconButton className={classes.closeBtn} size="medium" aria-label="–∑–∞–∫—Ä—ã—Ç—å" onClick={handleClose}>
                <CloseIcon fontSize="small"/>
            </IconButton>
            <Container component="main" maxWidth="xs">
                <CssBaseline/>
                <div className={classes.paperSingIn}>
                    <Typography component="h2" variant="h5"> –ó–∞–±—ã–ª –ø–∞—Ä–æ–ª—å </Typography>
                    <div className={loginSuccessClass ? classes.loginMessageSuccess : classes.loginMessageFail}>
                        {loginResult}
                    </div>
                    <form className={classes.form} noValidate>
                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="email"
                            label="–í–∞—à Email"
                            value={email}
                            name="email"
                            autoComplete="email"
                            autoFocus
                            onChange={(e) => {
                                setEmail(e.target.value)
                            }}
                        />
                        <span className={classes.fieldError}>{signinEmailError}</span>

                        <span className={classes.fieldError}>{signinPassError}</span>
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            className={classes.submit}
                            onClick={sendRememberPassword}
                        >
                            {loading ?  <SpinnerOne /> : '–ù–∞–ø–æ–º–Ω–∏—Ç—å'}

                        </Button>
                        <Grid container>
                            <Grid item xs>
                                <Link className={classes.alink} href="#" variant="body2" onClick={() => {setAuthLogin(true);setRememberPassword(false)}}>
                                    –í–æ–π—Ç–∏
                                </Link>
                            </Grid>
                            <Grid item>
                                <Link className={classes.alink} href="#" variant="body2" onClick={() => {setAuthLogin(false);setRememberPassword(false)}}>
                                    –ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è
                                </Link>
                            </Grid>
                        </Grid>
                    </form>
                </div>
            </Container>
        </div>
    );

    const bodyRegister = (
        <div style={modalStyle} className={classes.paper}>
            <IconButton className={classes.closeBtn} size="medium" aria-label="–∑–∞–∫—Ä—ã—Ç—å" onClick={handleClose}>
                <CloseIcon fontSize="small"/>
            </IconButton>
            <Container component="main" maxWidth="xs">
                <CssBaseline/>
                <div className={classes.paperSingIn}>
                    <Typography component="h2" variant="h5"> –ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è </Typography>
                    <div className={registerSuccessClass ? classes.loginMessageSuccess : classes.loginMessageFail}>
                        {registerResult}
                    </div>
                    <form className={classes.form} noValidate>
                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="email"
                            label="–í–∞—à Email"
                            value={email}
                            name="email"
                            autoComplete="email"
                            autoFocus
                            onChange={(e) => {
                                setEmail(e.target.value)
                            }}
                        />
                        <span className={classes.fieldError}>{signupEmailError}</span>
                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            name="password"
                            label="–ü–∞—Ä–æ–ª—å"
                            type="password"
                            id="password"
                            value={password}
                            autoComplete="current-password"
                            onChange={(e) => {
                                setPassword(e.target.value)
                            }}
                        />
                        <span className={classes.fieldError}>{signupPassError}</span>
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            className={classes.submit}
                            onClick={signUp}
                        >
                            {loading ?  <SpinnerOne /> : '–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è'}
                        </Button>
                        <Grid container>
                            <Grid item xs>
                                <Link className={classes.alink} href="#" variant="body2" onClick={() => setRememberPassword(true)}>
                                    –ó–∞–±—ã–ª–∏ –ø–∞—Ä–æ–ª—å
                                </Link>
                            </Grid>
                            <Grid item>
                                <Link className={classes.alink} href="#" variant="body2" onClick={() => {setAuthLogin(true); setRememberPassword(false)}}>
                                    –í–æ–π—Ç–∏
                                </Link>
                            </Grid>
                        </Grid>
                    </form>
                </div>
            </Container>
        </div>
    );
    return (
      <>
            <IconButton aria-label="delete" onClick={handleOpen}>
                {windowSize > 600 ? (<img src={signin} className={classes.signinBtn} alt='signin' />) : (
                    screen.mainScreen === 'Calendar' ?
                        <img src={signinMob} className={classes.signinBtn} alt='signin'/>
                        :
                        <img src={signin} className={classes.signinBtnSmall} alt='signin'/>
                    )}
            </IconButton>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
            >
                { loginSuccussLoading ? <SpinnerOne /> : rememberPassword ? bodyRememberPassword : authLogin ? bodyLogin : bodyRegister}
            </Modal>
        </>

    );
}

const useStyles = makeStyles((theme) => ({
    closeBtn: {
        position: 'absolute',
        top: '20px',
        right: '20px',
    },
    paper: {
        position: 'absolute',
        width: 400,
        backgroundColor: theme.palette.background.paper,
        border: '1px solid #ccc',
        boxShadow: theme.shadows[5],
        padding: '40px 10px 20px 10px',
        borderRadius: '8px',
    },
    loginMessageSuccess: {
        color: 'green',
        marginTop: '10px'
    },
    loginMessageFail: {
        color: 'red',
        marginTop: '10px'
    },
    paperSingIn: {
        marginTop: 0,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    alink: {
        color: '#000',
    },
    fieldError: {
        color: 'red'
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
        background: '#9A157D',
        textTransform: 'none',
        '&:hover': {
            background: '#9A157D',
            opacity: '0.9',
        },
    },
    signinBtn: {
        marginRight: '3%',
    },
    signinBtnSmall: {
        width: '31px'
    }
}));

const mapStateToProps = state => ({
    user: state.auth
});

export default withRouter(connect(
    mapStateToProps,
    {registerSuccess, registerFail, loginSuccess, loginFail}
)(AccountLogin));
