import React from "react";
import {makeStyles} from "@material-ui/core/styles";

const TotalBottomLine = () => {
    const classes = useStyles();
    return (
        <div className={classes.TotalBottomLine}>
            –≠—Ñ—Ñ–µ–∫—Ç–∏–≤–Ω–æ—Å—Ç—å <span className={classes.numbers}>20%</span>
            <span className={classes.nacenka}> –°–∫–∏–¥–∫–∞/–ù–∞—Ü–µ–Ω–∫–∞ <span className={classes.numbers}>-5%</span> </span>
            –¶–µ–Ω–∞ –∫–∏–ª–ª–æ–≥—Ä–∞–º–º–∞ <span className={classes.numbers}>1400</span>
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    TotalBottomLine: {
        fontSize: '0.8rem',
        color: '#000',
        fontWeight: '700',
        paddingTop: '1%',
        paddingBottom: '1%',
        borderBottom: '1px solid #d0cccc',
        [theme.breakpoints.down('xs')]: {
            display: 'flex',
            alignItems:'center',
            justifyContent: 'space-evenly'
        }
    },
    numbers: {
        fontSize: '1.3rem',
        color: '#EF3F34',
        fontWeight: '700',
        marginRight: '5%'
    },
    nacenka: {
        [theme.breakpoints.down('xs')]: {
            display: 'none',
        }
    }
}))

export default TotalBottomLine;