import {
  ADD_INGREDIENT,
  EDIT_INGREDIENT,
  DELETE_INGREDIENT,
  EDIT_LOOSES,
  EDIT_TEST_EXIT,
  EDIT_NEW_RECIPE,
} from '../types/recipes/MakeRecipeTypes';

export const editNewRecipe = (data) => async dispatch => {
  dispatch({
    type: EDIT_NEW_RECIPE,
    payload: data,
  });
};

export const addIngredients = (data) => async dispatch => {
    dispatch({
        type: ADD_INGREDIENT,
        payload: data,
    });
};

export const editIngredients = (data) => async dispatch => {
    dispatch({
        type: EDIT_INGREDIENT,
        payload: data,
    });
};

export const deleteIngredients = (id) => async dispatch => {
    dispatch({
        type: DELETE_INGREDIENT,
        payload: id,
    });
};

export const editLooses = (data) => async dispatch => {
    dispatch({
        type: EDIT_LOOSES,
        payload: data,
    });
};

export const editTestExit = (data) => async dispatch => {
    dispatch({
        type: EDIT_TEST_EXIT,
        payload: data,
    });
};
