import {
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT,
    SET_LOADING
} from "../actions/types/AuthTypes";

const initialState = {
    token: localStorage.getItem('token'),
    isAuthenticated: false,
    user: null,
    loading: true
};

export default function (state = initialState, action) {
    const { type, payload } = action;

    switch (type) {
        case REGISTER_SUCCESS:
            localStorage.setItem('token', payload.token);
            return {
                ...state,
                user: payload,
                isAuthenticated: false,
            };
        case REGISTER_FAIL:
            return {
                ...state,
                token: null,
                isAuthenticated: false,
            }
        case LOGIN_SUCCESS:
            return {
                ...state,
                user: payload,
                isAuthenticated: true,
                loading: false,
                token: localStorage.getItem('token'),
            };
        case LOGIN_FAIL:
            return {
                ...state,
                token: null,
                loading: false,
                isAuthenticated: false,
            }
        case LOGOUT:
            return {
                ...state,
                token: null,
                isAuthenticated: false,
                user: null
            };
        case SET_LOADING:
            return {
                ...state,
                loading: payload
            };
        default:
            return state;
    }
}