import {
  ADD_NEWMATERIALSTORE ,
  EDIT_NEWMATERIALSTORE ,
  DELETE_NEWMATERIALSTORE,
  GET_SAVED_MATERIALSTORE,
  SET_CURRENT_MATERIALSTORE_CATEGORY,
  SET_LOADING_MATERIALSTORE,
  DELETE_ALL_NEW_MATERIALSTORE,
  EDIT_OLDMATERIALSTORE,
  SET_SAVE_BUTTON
} from '../../actions/types/ingredients/materialStore/materialStoreTypes';

const initialState = {
  savedMaterialStore: [], //  —Ç–æ —á—Ç–æ —É–∂–µ —Å–æ—Ö—Ä–∞–Ω–µ–Ω—ã —É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  savedEditedMaterialStore: [],
  newMaterialStore: [], // —Ç–µ —á—Ç–æ –æ–Ω –Ω–æ–≤—ã–µ –≤–≤–æ–¥–∏—Ç
  currentMaterialStoreCategory: {
    title: '–£–ø–∞–∫–æ–≤–∫–∞',
    id: 10
  },
  loadingMaterialStore: false, //—Å–ø–∏–Ω–Ω–µ—Ä
  activeSaveButton: false // –µ—Å–ª–∏ –µ—Å—Ç—å —á—Ç–æ —Å–æ—Ö—Ä–∞–Ω—è—Ç—å, —Ç–æ –∫–Ω–æ–ø–∫–∞ –∞–∫—Ç–∏–≤–Ω–∞
};
// –º–∞—Å—Å–∏–≤—ã –º–∞—Ç–µ—Ä–∏–∞–ª–æ–≤ { title : '–ö–æ—Ä–æ–±–∫–∞', count: 2, material_type: "stock", price_cents: "1.0", (category_id: 1,-???)}

const materialStoreReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_NEWMATERIALSTORE:
      return {
        ...state,
        newMaterialStore: [...state.newMaterialStore, action.payload],
      };
    case EDIT_NEWMATERIALSTORE:
      const filterNewMaterialStore = state.newMaterialStore.filter(
          (item) => item.nanoId !== (action.payload.nanoId));
      return {
        ...state,
        newMaterialStore: [...filterNewMaterialStore , action.payload],
      };
    case DELETE_NEWMATERIALSTORE:
      if(action.payload) {
        const resultNewMaterialStore = state.newMaterialStore.filter(
            (item) => item.nanoId !== (action.payload));
        return {
          ...state,
          newMaterialStore: resultNewMaterialStore,
        };
      } else {
        //—É–¥–∞–ª–∏—Ç—å –≤—Å–µ
        return {
          ...state,
          newMaterialStore: [],
        };
      }
    case EDIT_OLDMATERIALSTORE:
      const filterSavedIMaterialStore = state.savedMaterialStore.filter(
          (item) => item.id !== (action.payload.id));
      const filterSavedEditedMaterialStore = state.savedEditedMaterialStore.filter(
          (item) => item.id !== (action.payload.id));
      return {
        ...state,
        savedMaterialStore: [...filterSavedIMaterialStore, action.payload],
        savedEditedMaterialStore: [...filterSavedEditedMaterialStore, action.payload],
      };
    case DELETE_ALL_NEW_MATERIALSTORE:
      return {
        ...state,
        newMaterialStore: [],
      }
    case SET_SAVE_BUTTON:
      return {
        ...state,
        activeSaveButton: action.payload,
      }
    case GET_SAVED_MATERIALSTORE:
      return {
        ...state,
        savedMaterialStore: action.payload,
      };
    case SET_CURRENT_MATERIALSTORE_CATEGORY:
      return {
        ...state,
        currentMaterialStoreCategory: action.payload,
      };
    case SET_LOADING_MATERIALSTORE:
      return {
        ...state,
        loadingMaterialStore: action.payload,
      };
    default:
      return state;
  }
};

export default materialStoreReducer;
