import {
    ADD_INGREDIENT,
    EDIT_INGREDIENT,
    DELETE_INGREDIENT,
    ADD_STANDARDISATION,
    EDIT_LOOSES,
    EDIT_TEST_EXIT,
    EDIT_NEW_RECIPE
} from '../../actions/types/recipes/MakeRecipeTypes';

const initialState = {
    title: '123',
    recipeTotalWeight: 0,
    recipeLooseWeight: 0,
    looses:0,
    exit: 0,
    testExit: 0,
    priceKg: 0,
    standardisation: {
        d: 0,
        h: 0,
        coverWeight: 0,
    },
    ingredients: [], // [{id: 1, name: 'milk',percent: 0,weight: 1000, price: '10'}]
    testIngredients: []
};

const calcPercent = (ingredientsArray) => {
    let totalWeight = ingredientsArray.reduce((sum, elem) => {
        return sum + parseInt(elem.weight);
    }, 0);
    let resultIngredients = ingredientsArray.map(item => {
        return {
            ...item,
            percent: Math.round((item.weight / totalWeight) * 100),
        };
    });

    return {
        totalWeight,
        resultIngredients,
    };
};

const recountToNewWeight = (newWeight, ingredientsArray) => {
    return ingredientsArray.map((item) => {
        return  {
            ...item,
            weight: Number(((newWeight / 100) * item.percent).toFixed(0))
        }
    })
}

const calcPriceKg = (ingredientsArray, exit) => {

    let priceByIngredient = ingredientsArray.map((item) => {
        if(!item.weight) return 0;
        return item.weight * item.price;
    });

    let totalSum = priceByIngredient.reduce((sum, elem) => {
        return sum + parseInt(elem);
    }, 0);

    console.log('totalSum', totalSum);
    if(totalSum !== 0) {
        let totalOnegramPrice = totalSum / exit;
        return Number((totalOnegramPrice * 1000).toFixed(0));
    } else {
        return 0;
    }
};

const calcExit = (val, looses) => {

    if (looses !== 0) {
        return val - (val / 100 * looses);
    } else {
        return val;
    }
};

const makeRecipeReducer = (state = initialState, action) => {
    switch (action.type) {
      case EDIT_NEW_RECIPE:
        return {
          ...action.payload
        }
        case ADD_INGREDIENT:
            let newIngArr = [...state.ingredients, action.payload];
            const resultAdd = calcPercent(newIngArr);

            return {
                ...state,
                exit: calcExit(resultAdd.totalWeight, state.looses),
                testExit: calcExit(resultAdd.totalWeight, state.looses),
                priceKg: calcPriceKg(resultAdd.resultIngredients,
                    calcExit(resultAdd.totalWeight, state.looses)),
                recipeTotalWeight: resultAdd.totalWeight,
                ingredients: resultAdd.resultIngredients,
                testIngredients: resultAdd.resultIngredients,
            };
        case EDIT_INGREDIENT:
            const filterIngredients = state.ingredients.filter(
                (item) => item.id !== (action.payload.id));
            const newIngredients = [...filterIngredients, action.payload];
            const resultEdit = calcPercent(newIngredients);

            return {
                ...state,
                exit: calcExit(resultEdit.totalWeight, state.looses),
                testExit:  calcExit(resultEdit.totalWeight, state.looses),
                priceKg: calcPriceKg(resultEdit.resultIngredients,
                    calcExit(resultEdit.totalWeight, state.looses)),
                recipeTotalWeight: resultEdit.totalWeight,
                ingredients: resultEdit.resultIngredients,
                testIngredients: resultEdit.resultIngredients,
            };
        case DELETE_INGREDIENT:
            const resultIngredientsArr = state.ingredients.filter(
                (elem) => elem.id !== action.payload);
            const resultDelete = calcPercent(resultIngredientsArr);
            return {
                ...state,
                exit: calcExit(resultDelete.totalWeight, state.looses),
                testExit: calcExit(resultDelete.totalWeight, state.looses),
                priceKg: calcPriceKg(resultDelete.resultIngredients,
                    calcExit(resultDelete.totalWeight, state.looses)),
                recipeTotalWeight: resultDelete.totalWeight,
                ingredients: resultDelete.resultIngredients,
                testIngredients: resultDelete.resultIngredients,
            };
        case EDIT_LOOSES:
            return {
                ...state,
                exit: calcExit(state.recipeTotalWeight, action.payload),
                testExit:  calcExit(state.recipeTotalWeight, action.payload),
                priceKg: calcPriceKg(state.ingredients,
                    calcExit(state.recipeTotalWeight, action.payload)),
                looses: action.payload,
            };

        case EDIT_TEST_EXIT:
            return {
                ...state,
                testExit: action.payload,
                testIngredients: recountToNewWeight(action.payload, state.testIngredients)
            };
        case ADD_STANDARDISATION:
            return {
                ...state,
                standardisation: action.payload,
            };
        default:
            return state;
    }
};

export default makeRecipeReducer;
