import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import SearchIcon from '@material-ui/icons/Search';

const SalesSearchField = () => {
  const classes = useStyles();
  return (
      <>
        <div className={classes.SummaryRightPanelSearchLine}>
          <input type="text" placeholder='–ü–æ–∏—Å–∫'
                 className={classes.searchField}/>
          <SearchIcon className={classes.searchIcon}/>
        </div>
        <SearchIcon className={classes.searchIconMobile}/>
      </>
  );
};

const useStyles = makeStyles((theme) => ({
  SummaryRightPanelSearchLine: {
    position: 'relative',
    flexBasis: '50%',
    [theme.breakpoints.down('xs')]: {
      display: 'none',
    },
  },
  searchIconMobile: {
    display: 'none',
    fill: '#DADADA',
    [theme.breakpoints.down('xs')]: {
      display: 'block',
      marginRight: '15px',
    },
  },
  searchField: {
    backgroundColor: 'rgba(218, 218, 218, 0.5)',
    border: 'none',
    height: '29px',
    margin: '10px 0',
    paddingLeft: '40px',
    width: '100%',
    maxWidth: '300px',
    '&:focus': {
      outline: 'none',
    },
  },

  searchIcon: {
    fill: '#fff',
    position: 'absolute',
    top: '13px',
    left: '10px',
  },
}));

export default SalesSearchField;
