import React, {useState} from "react";
import {makeStyles} from "@material-ui/core/styles";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import PurpleCheckbox from "../../../components/common/PurpleCheckbox";
import PlusRoundButton from "../../../components/common/PlusRoundButton";
import IconButton from '@material-ui/core/IconButton';
import TextFieldLite from "../../../components/common/TextFieldLite";
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import clock from '../../../images/clock1.svg'
import instrument from '../../../images/instrument1.svg'

const MakeProductThirdBlock = () => {
    const classes = useStyles();
    const [more, setMore] = useState(false);
    const [anchorEl, setAnchorEl] = useState(null);


    const handleCloseAddLineTask = () => {
        setAnchorEl(null);
    };

    const handleCloseAddLine = () => {
        setAnchorEl(null);
    };

    const handleCloseAddLineOperation = () => {
        setAnchorEl(null);
    };

    const handleClickAddLine = (event) => {
        setAnchorEl(event.currentTarget);
    };

    return (
        <div className={classes.MakeRecipeThirdBlock}>
            <div className={classes.blockInfo}>
                <h2>–¢—Ä—É–¥–æ–∑–∞—Ç—Ä–∞—Ç—ã</h2>
                <div>
                    –¢—Ä—É–¥–æ–∑–∞—Ç—Ä–∞—Ç—ã –Ω–∞ —Ä–µ—Ü–µ–ø—Ç—É—Ä—É
                </div>
            </div>
            <div className={classes.firstLine}>
                <TextFieldLite label='–í—Ä–µ–º—è –≤—ã–ø–æ–ª–Ω–µ–Ω–∏—è' required size="small" variant="outlined"
                           className={classes.workingTime}/>
                <TextFieldLite label="–ü–æ–≥—Ä–µ—à–Ω–æ—Å—Ç—å" required size="small" variant="outlined"
                           className={classes.workingFaults}/>
            </div>
            <div className={classes.moreChechboxWrapper}>
                <FormControlLabel
                    control={
                        <PurpleCheckbox
                            checked={more}
                            onChange={() => setMore(!more)}
                            name="checkedB"
                        />
                    }
                    label="–ü–æ–¥—Ä–æ–±–Ω–µ–µ"
                />
            </div>

            {more ? <div>
                <div className={classes.moreBlock}>

                    <div className={classes.moreBlockFirstLine}>
                        <div className={classes.moreBlockTaskLine}>
                            <TextFieldLite label='–ó–∞–¥–∞—á–∞1' size="small" variant="outlined"
                                       className={classes.task}/>
                            <TextFieldLite label="–í—Ä–µ–º—è" size="small" variant="outlined"
                                       className={classes.taskTime}/>
                        </div>
                        <div className={classes.moreBlockFirstLineOperation}>
                            <TextFieldLite label='–û–ø–µ—Ä–∞—Ü–∏—è' size="small" variant="outlined"
                                           className={classes.operation}/>
                            <TextFieldLite label="–í—Ä–µ–º—è" size="small" variant="outlined"
                                           className={classes.operationTime}/>
                        </div>
                    </div>



                    <div className={classes.clear}></div>
                </div>

                <IconButton aria-controls="simple-menu" onClick={handleClickAddLine} ><PlusRoundButton  /></IconButton>

                <Menu
                    id="simple-menu"
                    anchorEl={anchorEl}
                    keepMounted
                    open={Boolean(anchorEl)}
                    onClose={handleCloseAddLine}
                    >
                <MenuItem onClick={handleCloseAddLineTask}><img src={clock} className={classes.addLineIcon} alt='clock' />–î–æ–±–∞–≤–∏—Ç—å –∑–∞–¥–∞—á—É</MenuItem>
                <MenuItem onClick={handleCloseAddLineOperation}><img src={instrument} className={classes.addLineIcon} alt='instrument' />–î–æ–±–∞–≤–∏—Ç—å –æ–ø–µ—Ä–∞—Ü–∏—é</MenuItem>
            </Menu>
            </div> : ''}

        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    MakeRecipeThirdBlock: {
        flexBasis: '49%',
        border: '1px solid #a7a0a0',
        boxShadow: '0px 10px 15px -5px rgba(0,0,0,0.5)',
        padding: '1%',
        paddingTop: '0',
        textAlign: 'center',
        justifyContent: 'center',
        flexWrap: 'wrap',
        maxWidth: '380px',
        height: '70vh',
        '@media (max-width:1300px)': {
            flexBasis: '100%',
            maxWidth: 'initial',
            marginRight: '20px'
        },
        '@media (max-width:668px)': {
            border: 'none',
            boxShadow: 'none',
            margin: '0 5px'
        }
    },
    firstLine: {
        display: 'flex',
        justifyContent: 'space-between',
    },
    addLineIcon: {
        marginRight: '10px'
    },
    workingTime: {
        flexBasis: '50%'
    },
    clear: {
        clear: 'both'
    },
    task: {
        flexBasis: '70%'
    },
    operation: {
        flexBasis: '62%'
    },
    taskTime: {
        flexBasis: '22%'
    },
    operationTime: {
        flexBasis: '28%'
    },
    moreChechboxWrapper: {
        display: 'flex'
    },
    workingFaults: {
        flexBasis: '40%'
    },
    weightField: {},
    blockInfo: {
        textAlign: 'left',
        borderBottom: '1px solid #c1bdbdde',
        paddingBottom: '35px',
        marginBottom: '20px'
    },
    lineOne: {
        display: 'flex',
        justifyContent: 'space-between',
        fontWeight: 'bold',
        alignItems: 'center',
        marginBottom: '5px'
    },
    moreBlockFirstLine: {
        display: 'flex',
        justifyContent: 'space-between',
        marginBottom: '10px',
        flexWrap: 'wrap'
    },
    moreBlockTaskLine: {
        display: 'flex',
        justifyContent: 'space-between',
    },
    moreBlockFirstLineOperation: {
        display: 'flex',
        justifyContent: 'space-between',
        marginBottom: '10px',
        width: '80%',
        marginLeft: '20%',
        marginTop: '10px',
    },
    columnLine: {
        height: '2px',
        borderBottom: '1px dotted #000',
        flexBasis: '60%'
    },
    makeRecipesFirstBlockRight: {
        flexBasis: '75%'
    },
    moreBlock: {
        marginBottom: '10px'
    }
}))

export default MakeProductThirdBlock;
