import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import SearchField
  from '../../../components/blocks/orders/rightPanel/SearchField';
import TotalRecipesRightBlock from './TotalRecipesRightBlock';

const SummaryRightPanelSearchLine = () => {
  const classes = useStyles();
  return (
      <div className={classes.summaryRightPanelSearchLine}>
        <TotalRecipesRightBlock/>
        <div className={classes.searchLine}>
          <SearchField getDataUrl="recipes" label="–ü–æ–∏—Å–∫ –≤ —Ä–µ—Ü–µ–ø—Ç—É—Ä–∞—Ö"
                       noOptionsText="–Ω–µ—Ç —Ç–∞–∫–∏—Ö —Ä–µ—Ü–µ–ø—Ç—É—Ä"/>
        </div>
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  searchLine: {
    flexBasis: '50%',
    [theme.breakpoints.down('xs')]: {
      flexBasis: '99%',
    },
  },
  summaryRightPanelSearchLine: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    borderBottom: '1px solid #d0cccc',
    [theme.breakpoints.down('xs')]: {
      flexWrap: 'wrap',
    },
  },
}));

export default SummaryRightPanelSearchLine;
