import React from "react";
import {withStyles} from "@material-ui/core/styles";
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';


export const CustomTabsPurple = withStyles((theme) => ({
    indicator: {
        backgroundColor: "#5D1049",
        [theme.breakpoints.down('xs')]: {
            backgroundColor: "#5D1049",
        }
    },
}))(Tabs);

export const CustomTabPurple = withStyles((theme) => (
    {
        root: {
            textTransform: 'none',
            minWidth: 72,
            fontWeight: theme.typography.fontWeightRegular,
            marginRight: theme.spacing(2),
            color: global.config.colors.NotActiveElement,
            fontFamily: [
                '-apple-system',
                'BlinkMacSystemFont',
                '"Segoe UI"',
                'Roboto',
                '"Helvetica Neue"',
                'Arial',
                'sans-serif',
                '"Apple Color Emoji"',
                '"Segoe UI Emoji"',
                '"Segoe UI Symbol"',
            ].join(','),
            '&:hover': {
                color: global.config.colors.purple,
                opacity: 0.8,
            },
            '&$selected': {
                color: global.config.colors.purple,
                fontWeight: theme.typography.fontWeightMedium,
            },
            '&:focus': {
                color: global.config.colors.purple,
            },
        },
        selected: {},
    }))((props) => <Tab {...props} />);