import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import PlusRoundButton from '../../../components/common/PlusRoundButton';
import RecipeIngridientLine from './RecipeIngridientLine';
import {useDispatch, useSelector} from 'react-redux';
import {addIngredients, deleteIngredients, editNewRecipe} from '../../../actions/recipes/makeRecipeActions';
import TextFieldMedium from '../../../components/common/TextFieldMedium';
import PurpleCheckbox from '../../../components/common/PurpleCheckbox';
import IconButton from '@material-ui/core/IconButton';
import FormDeleteIcon from '../../../components/common/Icons/FormDeleteIcon';
import TextFieldLite from '../../../components/common/TextFieldLite';
import sortBy from 'lodash/sortBy';
import produce from "immer"

const MakeRecipeFirstBlockLeft = () => {
    const classes = useStyles();
    const [calibrCheckbox, setCalibrCheckbox] = useState(false);
    const ingredientsArr = useSelector(
        state => state.recipes.makeRecipe.ingredients); //lineNumberArr
    const dispatch = useDispatch();
    const newRecipe = useSelector(
      state => state.recipes.makeRecipe);

    const addLineFunc = () => {
      dispatch(addIngredients({id:ingredientsArr.length, name: '', percent: 100, weight: 100, price: 100 }));
    };

    const deleteLine = (num) => {
        dispatch(deleteIngredients(num))
    };

    const changeNewRecipeName = (event) => {
      const recipeWithNewTitle = produce(newRecipe, draft => {
        draft.title = event.target.value;
      })
      dispatch(editNewRecipe(recipeWithNewTitle))
    }

    return (
        <div className={classes.makeRecipesFirstBlockLeft}>
            <TextFieldMedium label="–ù–∞–∑–≤–∞–Ω–∏–µ" required size="small"
                             variant="outlined"
                             value={newRecipe.title}
                             onChange={changeNewRecipeName}
                             className={classes.recipeField}/>
            <div className={classes.dataBlock}>
                <div className={classes.dataBlockLeftCalibr}>
                    <FormControlLabel
                        control={
                            <PurpleCheckbox
                                checked={calibrCheckbox}
                                onChange={() => setCalibrCheckbox(
                                    !calibrCheckbox)}
                                name="checkedB"
                            />
                        }
                        label="–°—Ç–∞–Ω–¥–∞—Ä—Ç–∏–∑–∞—Ü–∏—è"
                    />
                </div>
                {calibrCheckbox ? <div className={classes.dataBlockRight}>
                    <TextFieldLite label="d" variant="outlined"
                                   defaultValue="20" size="small"
                                   className={classes.recipeD}/>
                    <TextFieldLite label="h" variant="outlined"
                                   defaultValue="10" size="small"
                                   className={classes.recipeH}/>
                    <TextFieldLite label="–≤–µ—Å –ø–æ–∫—Ä—ã—Ç–∏—è" variant="outlined"
                                   defaultValue="300" size="small"
                                   className={classes.recipeCoverWeight}/>
                </div> : ''}
            </div>

            {sortBy(ingredientsArr, [function(o) { return o.id; }]).map((elem, i) => <div key={elem.id}
                                                  className={classes.dataBlockLine}>
                <FormDeleteIcon className={classes.trashIcon}
                                onClick={() => deleteLine(elem.id)}/>
                                <RecipeIngridientLine className={classes.trashIcon2} elem={elem}/></div>)}

            <IconButton><PlusRoundButton addLine={addLineFunc}/></IconButton>
        </div>
    );
};

const useStyles = makeStyles((theme) => ({
    dataBlockLine: {
        display: 'flex',
        marginBottom: '5px',
        alignItems: 'center',
        marginTop: '-20px',
    },
    makeRecipesFirstBlockLeft: {
        flexBasis: '45%',
        minWidth: '335px',
        '@media (max-width:414px)': {
            minWidth: 'initial',
        },
    },
    recipeField: {
        width: '100%',
        marginBottom: '20px',
    },
    dataBlock: {
        display: 'flex',
        marginBottom: '15px',
        '@media (max-width:414px)': {
            flexWrap: 'wrap',
        },
    },
    trashIcon: {
        marginRight: '5px',
        cursor: 'pointer',

    },
    trashIcon2: {
      marginTop: '5px'
    },
    dataBlockLeftCalibr: {
        flexBasis: '45%',
        '@media (max-width:414px)': {
            flexBasis: '100%',
        },
    },
    dataBlockRight: {
        flexBasis: '50%',
        display: 'flex',
        '@media (max-width:414px)': {
            flexBasis: '100%',
        },
    },
    recipeD: {
        marginRight: '5px',
    },
    recipeH: {
        marginRight: '5px',
    },
    recipeWeight: {
        flexBasis: '50%',
    },
    recipeCoverWeight: {
        '& label': {
            top: '-10px',
        },
        '& input': {
            padding: '10.5px 4px',
        },
    },
}));

export default MakeRecipeFirstBlockLeft;
