import React, {useEffect, useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import TextFieldLite from '../../../components/common/TextFieldLite';
import FormDeleteIcon from '../../../components/common/Icons/FormDeleteIcon';
import {
  editNewIngredients,
  editOldIngredients,
} from '../../../actions/ingredients/ingredientActions';
import useComponentVisible from '../../../hooks/useComponentVisible';
import {useDispatch} from 'react-redux';
import {fromEvent, of} from 'rxjs';
import {
  catchError,
  debounceTime,
  distinctUntilChanged,
  pluck,
  switchMap,
} from 'rxjs/operators';
import {ajax} from 'rxjs/ajax';

const MaterialItem = ({elem, deleteLine, savedIngredients, ingregientType = 'new'}) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  let [materialHints, setMaterialHints] = useState([]);
  const {ref, isComponentVisible, setIsComponentVisible} = useComponentVisible(
      true);

  useEffect(() => {
    const click = fromEvent(document.getElementById('ingredientName'), 'input')
      .pipe(
            debounceTime(500),
            pluck('target', 'value'),
            distinctUntilChanged(),
            switchMap(searchWord => {
              return ajax.getJSON(
                  `${global.config.settings.apiBaseUrl}/ingredients?search_by_name=${searchWord.capitalize()}`,
                  {Authorization: 'Bearer ' + localStorage.getItem('token')}
                  )
              .pipe(
                  catchError( (error, caught) => of(`Error : ${error}`))
              )
            }),
        )
      .subscribe(resp => {
              if (resp) {
                setMaterialHints(resp);
                setIsComponentVisible(true);
              }
            },
        );
    return () => click.unsubscribe();
  }, [setIsComponentVisible]);

  const changeIngredientName = (event) => {
    if (event.target.value.length === 0) {
      setMaterialHints([]);
      setIsComponentVisible(false);
    }
    dispatch(editNewIngredients({
      ...elem,
      title: event.target.value,
    }));
  };

  const chooseIngredientTitle = (event) => {
    if (ingregientType === 'new') {
      dispatch(editNewIngredients({
        ...elem,
        id: parseInt(event.target.getAttribute('data-id')),
        title: event.target.innerText,
      }));
      setMaterialHints([]);
    }
  };

  const changeIngredientWeight = (event) => {
    if (ingregientType === 'new') {
      dispatch(editNewIngredients({
        ...elem,
        weight: event.target.value ?
            event.target.value.toString().match(/[0-9.]+/gi) :
            '',
      }));
    }

    if (ingregientType === 'old') {
      dispatch(editOldIngredients({
        ...elem,
        weight: event.target.value ?
            event.target.value.toString().match(/[0-9.]+/gi) :
            '',
      }));
    }
  };

  const changeIngredientPrice = (event) => {
    if (ingregientType === 'new') {
      dispatch(editNewIngredients({
        ...elem,
        price: event.target.value ?
            event.target.value.toString().match(/[0-9.]+/gi) :
            '',
      }));
    }

    if (ingregientType === 'old') {
      dispatch(editOldIngredients({
        ...elem,
        price: event.target.value ?
            event.target.value.toString().match(/[0-9.]+/gi) :
            '',
      }));
    }
  };

  return (
      <div className={classes.materialItem}>
        <div className={classes.materialNameFieldWrapper}>
          <TextFieldLite label="–ò–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç" variant="outlined"
                         id="ingredientName"
                         disabled={ingregientType === 'old'}
                         value={elem.title} onChange={changeIngredientName}
                         className={classes.ingredient}/>
          {materialHints.length > 0 && isComponentVisible ? (
              <div className={classes.materialHints} ref={ref}>
                <ul>
                  {materialHints.map(
                      (el) => <li className={classes.materialHintsLi}
                                  onClick={chooseIngredientTitle}
                                  data-id={el.id}
                                  key={el.id}>{el.title}</li>)}
                </ul>
              </div>
          ) : (<div></div>)}
        </div>
        <TextFieldLite label="–í–µ—Å" variant="outlined"
                       value={elem.weight} onChange={changeIngredientWeight}
                       className={classes.weight}/>
        <TextFieldLite label="–¶–µ–Ω–∞" variant="outlined"
                       value={elem.price} onChange={changeIngredientPrice}
                       className={classes.price}/>
        <div className={classes.date}>{new Date(elem.updated_at).toISOString()
          .slice(0, 10)}</div>
        <FormDeleteIcon
            onClick={() => deleteLine(elem.id, savedIngredients, elem.nanoId)}/>
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  materialItem: {
    marginTop: '10px',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  materialNameFieldWrapper: {
    position: 'relative',
  },
  materialHints: {
    position: 'absolute',
    top: '58px',
    background: '#fff',
    zIndex: '2',
    width: '100%',
    border: '1px solid #c2c0c0',
    borderTop: 'none',
  },
  materialHintsLi: {
    cursor: 'pointer',
  },
  date: {
    [theme.breakpoints.down('xs')]: {
      display: 'none',
    },
  },
  ingredient: {
    flexBasis: '40%',
    [theme.breakpoints.down('xs')]: {
      flexBasis: '46%',
    },
  },
  weight: {
    flexBasis: '12%',
    [theme.breakpoints.down('xs')]: {
      flexBasis: '16%',
    },
  },
  price: {
    flexBasis: '12%',
    [theme.breakpoints.down('xs')]: {
      flexBasis: '22%',
    },
  },
  trash: {
    cursor: 'pointer',
  },

}));

export default MaterialItem;
