import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import item from '../../../images/sale_item.png';

const SalesItem = ({ blur }) => {
    const classes = useStyles();
    return (
        <div className={ blur ? classes.SalesItemBlur :classes.SalesItem} >
            <div className={classes.left}>
                <img src={item} className={classes.itemImg} alt='item' />
            </div>
            <div className={classes.center}>
                <div className={classes.SaleItemName}>–ê–º–µ—Ä–∏–∫–∞–ª–æ—Ä</div>
                <div className={classes.price}>–ê–Ω—Ç–æ–Ω–Ω–∏–æ –ü–µ—Ç—Ç–∏</div>
            </div>
            <div className={classes.center}>
                <div className={classes.SaleItemOpt}>–†–µ–¥-–†–µ–¥</div>
            </div>
            <div className={classes.left}>
                <div className={classes.salePrice}>350p</div>
                <div className={classes.dateDiscount}><span className={classes.saleDate}>–î–æ 21.07.20</span> <span className={classes.saleDiscount}>-25%</span></div>
            </div>
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    SalesItem: {
        border: '1px solid #746871',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        padding: '1%',
        width: '310px',
        marginTop: '7px',
        borderRadius: '5px',
        fontSize: '0.8rem',
        [theme.breakpoints.down('xs')]: {
            maxWidth: 'initial',
        },
    },
    SalesItemBlur: {
        border: '1px solid #746871',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        padding: '1%',
        maxWidth: '360px',
        marginTop: '1%',
        borderRadius: '5px',
        fontSize: '0.8rem',
        opacity: '0.4',
        [theme.breakpoints.down('xs')]: {
            maxWidth: 'initial',
        },
    },
    center: {
        textAlign:'center',
    },
    left: {
        textAlign:'center',
    },
    itemImg: {
        border: '1px solid #8c8787',
        borderRadius: '5px',
        padding: '3px'
    },
    SaleItemName: {
        fontWeight: '700',
        fontSize: '0.9rem'
    },
    price: {
        fontSize: '0.7rem',
        color: '#9A157D'
    },
    productName: {
        fontSize: '0.7rem',
    },
    salePrice: {
        color: '#9A157D',
        fontSize: '1.1rem',
        fontWeight: '700',
    },
    dateDiscount: {
        color: '#FA1919',
        fontSize: '0.9rem',
        fontWeight: '700',
    },
    SaleItemOpt: {
        fontSize:'0.6rem'
    },
    saleDate: {
        fontSize:'0.6rem'
    }
}))

export default SalesItem;