import {
    GET_PRODUCTS,
    SET_LOADING,
    PRODUCTS_ERROR
} from "../actions/types/ProductTypes";

const initialState = {
    products: null,
    current: null,
    loading: true,
    error: null
};

const productReducer =  (state = initialState, action) => {

    switch (action.type) {
        case GET_PRODUCTS:
            return {
                ...state,
                products: action.payload,
                loading: false
            };
        case SET_LOADING:
            return {
                ...state,
                loading: true
            };
        case PRODUCTS_ERROR:
            console.error(action);
            return {
                ...state,
                error: action.payload
            };
        default:
            return state;
    }
}

export default productReducer;