import {
  ADD_NEWMATERIALSTORE ,
  EDIT_NEWMATERIALSTORE ,
  DELETE_NEWMATERIALSTORE,
  GET_SAVED_MATERIALSTORE,
  SET_CURRENT_MATERIALSTORE_CATEGORY,
  SET_LOADING_MATERIALSTORE,
  EDIT_OLDMATERIALSTORE,
  SET_SAVE_BUTTON,
  DELETE_ALL_NEW_MATERIALSTORE,
} from '../../types/ingredients/materialStore/materialStoreTypes.js';

import axios from 'axios';

export const setLoadingMaterialStore = (userData) => async dispatch => {
  dispatch({
    type: SET_LOADING_MATERIALSTORE,
    payload: userData
  });
};

export const addNewMaterialStore = (data) => async dispatch => {
  dispatch({
    type: ADD_NEWMATERIALSTORE,
    payload: data,
  });
};

export const editNewMaterialStore = (data) => async (dispatch, getState) => {
  dispatch({
    type: EDIT_NEWMATERIALSTORE,
    payload: data,
  });
  const state = getState();
  const changed = state.ingredients.materialStoreList.newMaterialStore.filter((item) => item.title !== '');
  if(changed.length > 0) {
    dispatch(setSaveButtonStatus(true));
  } else {
    dispatch(setSaveButtonStatus(false));
  }
};

export const editOldMaterialStore = (data) => async (dispatch, getState) => {
  dispatch({
    type: EDIT_OLDMATERIALSTORE,
    payload: data,
  });
  const state = getState();
  const changed = state.ingredients.materialStoreList.savedMaterialStore.filter((item) => item.title !== '');
  if(changed.length > 0) {
    dispatch(setSaveButtonStatus(true));
  } else {
    dispatch(setSaveButtonStatus(false));
  }
};

export const deleteNewMaterialStore = (id) => async dispatch => {
  dispatch({
    type: DELETE_NEWMATERIALSTORE,
    payload: id,
  });
};

export const setSaveButtonStatus = (data) => async dispatch => {
  dispatch({
    type: SET_SAVE_BUTTON,
    payload: data,
  });
};

export const deleteAllNewMaterialStore = () => async dispatch => {
  dispatch({
    type: DELETE_ALL_NEW_MATERIALSTORE,
  });
};

export const saveNewMaterialStore = (data, callbackSuccess, callbackError) => async dispatch => {
  try {
    callbackSuccess();

  } catch (e) {
    callbackError();
    console.error(e);
  }
};

export const saveOldMaterialStore = (data, callbackSuccess, callbackError) => async dispatch => {
  try {
    callbackSuccess();

  } catch (e) {
    callbackError();
    console.error(e);
  }
};

export const getSavedMaterialStore = (id = 10) => async dispatch => {

  try {
    dispatch(setLoadingMaterialStore(true));
    const savedMaterialStore = await axios.get('/materials',
        {params: {'category_id': id}});
    dispatch(setLoadingMaterialStore(false));
    dispatch({
      type: GET_SAVED_MATERIALSTORE,
      payload: savedMaterialStore.data,
    });
  } catch (err) {
    console.error(err.response.data);
  }

};

export const setCurrentMaterialStoreCategory = (el) => async dispatch => {
  dispatch({
    type: SET_CURRENT_MATERIALSTORE_CATEGORY,
    payload: el,
  });
};
