import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import {editProduct} from '../../../actions/products/makeProductActions';
import TextFieldLite from "../../../components/common/TextFieldLite";
import {useDispatch} from 'react-redux';

const ProductIngridientLine = ({elem}) => {
    const classes = useStyles();
    const dispatch = useDispatch();


    const changeIngredientName = (event) => {
        dispatch(editProduct({
            ...elem,
            name: event.target.value
        }))
    }

    const changeIngredientWeight = (event) => {
        dispatch(editProduct({
            ...elem,
            weight: parseInt(event.target.value)
        }))
    }

    return (
        <div className={classes.dataBlock}>
            <div className={classes.dataBlockLeft}>
                <TextFieldLite label="–ò–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç" required size="small" variant="outlined" defaultValue="" value={elem.name}
                           className={classes.ingredientField} onChange={changeIngredientName}/>
            </div>
            <div className={classes.dataBlockRight}>
                <div className={classes.percent}>{elem.percent}%</div>
                <TextFieldLite label="B–µ—Å" required variant="outlined" type="number" size="small" defaultValue=""
                           value={elem.weight} className={classes.recipeWeight} onChange={changeIngredientWeight}/>
            </div>
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    dataBlock: {
        display: 'flex',
        alignItems: 'center',
    },
    dataBlockLeft: {
        flexBasis: '50%'
    },
    dataBlockRight: {
        flexBasis: '50%',
        display: 'flex',
    },
    recipeD: {
        marginRight: '5px'
    },
    percent: {
        margin: '0 15px',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexBasis: '65%',
        fontSize: '1.2rem',
        color: global.config.colors.purple,
        fontWeight: '700'
    },
    ingredientField: {},
}))

export default ProductIngridientLine;
