import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import SearchField from "../../../../components/blocks/orders/rightPanel/SearchField";
import MaterialStoreTotalRightBlock from './MaterialStoreTotalRightBlock';

const MaterialStoreSummaryRightPanelSearchLine = () => {
  const classes = useStyles();
  return (
      <div className={classes.summaryRightPanelSearchLine}>
        <MaterialStoreTotalRightBlock />
        <div className={classes.searchLine}>
          <SearchField getDataUrl="materials" label="–ü–æ–∏—Å–∫ –≤ –º–∞—Ç–µ—Ä–∏–∞–ª–∞—Ö" noOptionsText="–Ω–µ—Ç —Ç–∞–∫–∏—Ö –º–∞—Ç–µ—Ä–∏–∞–ª–æ–≤"/>
        </div>
      </div>
  )
}

const useStyles = makeStyles((theme) => ({
  searchLine: {
    flexBasis: '50%',
    [theme.breakpoints.down('xs')]: {
      flexBasis: '95%',
      width: '95%'
    },
  },
  summaryRightPanelSearchLine: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    borderBottom: '1px solid #d0cccc',
    [theme.breakpoints.down('xs')]: {
      flexDirection: 'column'
    },
  }
}))

export default MaterialStoreSummaryRightPanelSearchLine;
