import Modal from '@material-ui/core/Modal';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Container from '@material-ui/core/Container';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';

function getModalStyle() {
    const top = 50;
    const left = 50;

    return {
        top: `${top}%`,
        left: `${left}%`,
        transform: `translate(-${top}%, -${left}%)`,
    };
}

const ModalAdd = ({open, handleClose, windowTitle, addFunction}) => {
    const classes = useStyles();
    const [modalStyle] = useState(getModalStyle);
    const [title, setTitle] = useState('');

    return (
        <Modal
            open={open}
            onClose={handleClose}
            aria-labelledby="simple-modal-title"
            aria-describedby="simple-modal-description"
        >
            <div style={modalStyle} className={classes.paper}>
                <IconButton className={classes.closeBtn} size="medium"
                            aria-label="–∑–∞–∫—Ä—ã—Ç—å" onClick={handleClose}>
                    <CloseIcon fontSize="small"/>
                </IconButton>
                <Container component="main" maxWidth="xs">
                    <div className={classes.paperSingIn}>
                        <div className={classes.header}> {windowTitle} </div>
                        <form className={classes.form}>
                            <TextField
                                variant="outlined"
                                margin="normal"
                                required
                                fullWidth
                                name="–ù–∞–∑–≤–∞–Ω–∏–µ"
                                label="–ù–∞–∑–≤–∞–Ω–∏–µ"
                                type="title"
                                id="title"
                                value={title}
                                size="small"
                                onChange={(event) => {
                                    setTitle(event.target.value);
                                }}
                            />
                            <Button
                                fullWidth
                                variant="contained"
                                color="primary"
                                className={classes.submit}
                                onClick={() => {
                                    setTimeout(() => {
                                        handleClose();
                                    }, 1000);
                                    return addFunction(title);
                                }}
                            >
                                –î–æ–±–∞–≤–∏—Ç—å

                            </Button>
                        </form>
                    </div>
                </Container>
            </div>
        </Modal>
    );
};

const useStyles = makeStyles((theme) => ({
    closeBtn: {
        position: 'absolute',
        top: '5px',
        right: '5px',
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
        background: '#9A157D',
        textTransform: 'none',
        '&:hover': {
            background: '#9A157D',
            opacity: '0.9',
        },
    },
    header: {
        textAlign: 'center',
        fontSize: '1.2rem',
    },
    paper: {
        position: 'absolute',
        width: 400,
        backgroundColor: theme.palette.background.paper,
        border: '1px solid #ccc',
        boxShadow: theme.shadows[5],
        padding: '20px 10px 20px 10px',
        borderRadius: '8px',
        [theme.breakpoints.down('xs')]: {
            width: '90%',
        },
    },
}));

export default ModalAdd;