import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import {useSelector} from 'react-redux';


const TotalRecipesRightBlock = () => {
    const classes = useStyles();

    const currentRecipesCategory = useSelector(
        state => state.recipes.recipesList.currentRecipesCategory);


    return (
        <div className={classes.totalOrdersInfo}>
            <span className={classes.totalNumbers}>
                    <span className={classes.materialType}>{currentRecipesCategory && currentRecipesCategory.title}</span>
                    <span className={classes.materialWeight}>300—Ä</span>
                    <span className={classes.recipes}>
                        <span className={classes.recipesNumber}>10</span> —Ä–µ—Ü–µ–ø—Ç—É—Ä
                    </span>
            </span>
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    totalOrdersInfo: {
        display: 'flex',
        flexBasis: '50%',
        justifyContent: 'space-around',
        [theme.breakpoints.down('sm')]: {
            flexBasis: '100%',
        }
    },
    recipesNumber: {
        color: '#9A157D',
        fontSize: '1.3rem',
        [theme.breakpoints.down('xs')]: {
            color: '#FA1919',
            fontSize: '1.3rem',
            marginRight: '5px'
        }
    },
    switch: {
        display: 'none',
        [theme.breakpoints.down('sm')]: {
            display: 'block'
        }
    },
    recipes: {
        fontWeight: '700',
        [theme.breakpoints.down('xs')]: {
            display: 'flex',
            alignItems: 'baseline',
            justifyContent: 'center'
        },
    },
    materialType: {
        fontWeight: 'bold',
        fontSize: '1.1rem',
        [theme.breakpoints.down('xs')]: {
            width: '100px',
            display: 'inline-block',
            textAlign: 'center',
            fontSize: '0.9rem',
        },
    },
    totalNumbers: {
        fontSize: '0.8rem',
        width: '100%',
        display: 'flex',
        justifyContent: 'space-evenly',
        alignItems: 'center',
        [theme.breakpoints.down('sm')]: {
            flexBasis: '90%',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
            margin: '0',
            borderRadius: '10px',
            padding: '0',
            position: 'relative',
            bottom: '-8px'
        },
    },
    materialWeight: {
        fontSize: '1.3rem',
        color: '#747474',
        fontWeight: '700',
        [theme.breakpoints.down('xs')]: {
            fontSize: '1rem',
            color: '#9A157D',
        }
    }
}))

export default TotalRecipesRightBlock;