import {
    SET_START_DATE,
    SET_END_DATE,
    SET_IS_TODAY_BUTTON
} from "./types/DateTypes.js";

export const setEndDate = (data) => dispatch =>  {
    dispatch({
        type: SET_END_DATE,
        payload: data
    });
};

export const setStartDate = (data) => dispatch =>  {
    dispatch({
        type: SET_START_DATE,
        payload: data
    });
};

export const setIsTodayButton = (data) => dispatch =>  {
    dispatch({
        type: SET_IS_TODAY_BUTTON,
        payload: data
    });
};
