import React, {useEffect, useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import {useSwipeable} from 'react-swipeable';
import useWindowSize from '../../../../hooks/useWindowSize';
import MaterialStoreSummaryRightPanelSearchLine
  from './MaterialStoreSummaryRightPanelSearchLine';

const MaterialStoreSummaryRightPanel = ({toggleBottomMenu}) => {
  const config = {
    delta: 10,                             // min distance(px) before a swipe starts
    preventDefaultTouchmoveEvent: false,   // preventDefault on touchmove, *See Details*
    trackTouch: true,                      // track touch input
    trackMouse: false,                     // track mouse input
    rotationAngle: 0,                      // set a rotation angle
  };
  const classes = useStyles();
  const windowSize = useWindowSize();
  let [classesComputed, setClassesComputed] = useState(
      classes.summaryRightPanelBlock);
  let [showMobileOrders, setShowMobileOrders] = useState(false);

  let handlers = useSwipeable({
    onSwipedUp: (eventData) => {
      setShowMobileOrders(true);
    },
    onSwipedDown: (eventData) => {
      setShowMobileOrders(false);
    },
    ...config,
  });

  useEffect(() => {
    if (showMobileOrders) {
      setClassesComputed(classes.showMobileOrdersClass);
      toggleBottomMenu(showMobileOrders);
    } else {
      setClassesComputed(classes.summaryRightPanelBlock);
      toggleBottomMenu(showMobileOrders);
    }
  }, [classes.showMobileOrdersClass, classes.summaryRightPanelBlock, showMobileOrders, toggleBottomMenu]);

  /* –ï—Å–ª–∏ —à–∏—Ä–∏–Ω–∞ —ç–∫—Ä–∞–Ω–∞ –±–æ–ª—å—à–µ - —Ç–æ —Å–≤–∞–π–ø—ã –Ω–∞ –Ω–∏–∂–Ω–µ–º –º–µ–Ω—é –Ω–µ —Å—Ä–∞–±–æ—Ç–∞—é—Ç */
  if (windowSize > 500) {
    handlers = [];
  }

  return (
      <div className={classesComputed} {...handlers} >
        <MaterialStoreSummaryRightPanelSearchLine/>
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  summaryRightPanelBlock: {
    border: '1px solid #a7a0a0',
    padding: '0 2%',
    borderTopRightRadius: '15px',
    borderTopLeftRadius: '15px',
    boxShadow: ' 0px 10px 15px -5px rgba(0,0,0,0.5)',
    [theme.breakpoints.down('xs')]: {
      position: 'fixed',
      width: '101vw',
      left: '-1px',
      bottom: '0',
      backgroundColor: '#fff',
      transition: 'top .20s',
      zIndex: '9',
      border: '1px solid #000',
    },
  },
  showMobileOrdersClass: {
    position: 'absolute',
    width: '101vw',
    left: '-1px',
    backgroundColor: '#fff',
    zIndex: '9',
    bottom: '50%',
    transition: 'top .20s',
    borderTopRightRadius: '15px',
    borderTopLeftRadius: '15px',
    [theme.breakpoints.down('xs')]: {
      border: '1px solid #000',
    },
  },
  customButtonFirst1: {
    marginLeft: '3%',
  },
}));

MaterialStoreSummaryRightPanel.propTypes = {
  showMobileOrders: PropTypes.bool,
};

export default MaterialStoreSummaryRightPanel;
