import React, {useEffect, useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Chip from '@material-ui/core/Chip';
import axios from 'axios';
import Avatar from '@material-ui/core/Avatar';

const SummaryRightPanelBlockButtons = () => {
  const classes = useStyles();
  const handleClick = () => {
    console.info('You clicked the icon.');
  };
  const [ProductCategories, setProductCategories] = useState([]);
  const getCategories = () => {
    try {
      //setLoading(true);
      axios.get('/categories',
          {
            params: {
              'direction_name': 'product',
            },
          }).then(data => {
        setProductCategories(data.data);
      });
    } catch (e) {
      console.error(e);
    }
  };

  useEffect(() => {
    getCategories();
  }, []);
  return (
      <div className={classes.summaryRightPanelBlockButtons}>
        {ProductCategories.map((el) =>
            <Chip variant="outlined" color='default'
                  avatar={<Avatar>{el.items_count}</Avatar>} size="medium"
                  label={el.title} onClick={handleClick}/>)}
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  summaryRightPanelBlockButtons: {
    paddingBottom: '1%',
    paddingTop: '1%',
    [theme.breakpoints.down('xs')]: {
      display: 'none',
    },
  },
}));

export default SummaryRightPanelBlockButtons;
