import {
    SET_CURRENT_DATE,
    SET_START_DATE,
    SET_END_DATE,
    SET_IS_TODAY_BUTTON
} from "../actions/types/DateTypes";

const initialState = {
    startDate: new Date(),
    endDate: new Date(),
    isTodayButton: false
};

const dateReducer = (state = initialState, action) => {

    switch (action.type) {
        case SET_CURRENT_DATE:
            return {
                ...state,
                startDate: action.payload.date,
            };
        case SET_START_DATE:
            return {
                ...state,
                startDate: action.payload,
            };
        case SET_END_DATE:
            return {
                ...state,
                endDate: action.payload,
            };
        case SET_IS_TODAY_BUTTON:
            return {
                ...state,
                isTodayButton: action.payload,
            };
        default:
            return state;
    }
}

export default dateReducer;