import React, {useState, useEffect} from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    NavLink,
} from 'react-router-dom';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import 'date-fns';
import '../../css/dashboard.scss';
import useStylesDashboard from '../../css/useStyle/Dashboard.css';
import TodayButton from './TodayButton';
import logo from '../../images/logo.svg';
import AccountLogin from './AccountLogin';
import CalendarMobIcon from '../../images/calendar_top_mob.svg';
import Calendar from '../../sreens/Calendar/Calendar';
import Ingregients from '../../sreens/Materials/Ingredients';
import Recipes from '../../sreens/Recipes/Recipes';
import Product from '../../sreens/Production/Product';
import {useDispatch, useSelector} from 'react-redux';
import styles from '../../css/modules/drawer.module.css';
import calendar from '../../images/calendar_left.svg';
import ListItemText from '@material-ui/core/ListItemText';
import {setScreen} from '../../actions/screenAction';
import ingredients from '../../images/ingredients_left.svg';
import recepies from '../../images/Recepies.svg';
import works from '../../images/works.svg';

import clock from '../../images/clock.svg';
import person from '../../images/person.svg';
import pastrynet from '../../images/pasrtynet.svg';
import SpinnerOne from './smallElements/SpinnerOne';
import {login} from '../../actions/authAction';
import MakeRecipeIndex from '../../sreens/Recipes/MakeRecipe';
import MakeProductIndex from '../../sreens/Production/MakeProduct';
import useWindowSize from '../../hooks/useWindowSize';
import MakeOrderIndex from '../../sreens/Orders/MakeOrder';

export default function Dashboard(props) {
    const loading = useSelector(state => state.auth.loading);
    const classes = useStylesDashboard();
    const {window} = props;
    const container = window !== undefined ?
        () => window().document.body :
        undefined;
    const [mobileOpen, setMobileOpen] = useState(false);
    const screen = useSelector(state => state.screen);
    const isBigScreenDrawerOpen = useSelector(
        state => state.effects.isBigScreenDrawerOpen);
    const isShowTopPanel = useSelector(state => state.effects.isShowTopPanel);
    const windowSize = useWindowSize();

    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(login());
    }, [dispatch]);

    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };

    const mainListItems = (
        <div>
            <div className={styles.navWrapper} onClick={() => {
                dispatch(setScreen({mainScreen: 'Calendar'}));
                setTimeout(() => {
                    setMobileOpen(false);
                }, 800);
            }}>
                <NavLink exact to="/" activeClassName="drawer_nav_selected"
                         className={styles.router_link}>
                    <img src={calendar} className={styles.drawer_icon} alt='calendar'/>
                    <ListItemText primary="–ö–∞–ª–µ–Ω–¥–∞—Ä—å"/>
                </NavLink>
            </div>
            <div className={styles.navWrapper} onClick={() => {
                dispatch(setScreen({mainScreen: 'Materials'}));
                setTimeout(() => {
                    setMobileOpen(false);
                }, 800);
            }}>
                <NavLink exact to="/ingredients"
                         activeClassName="drawer_nav_selected"
                         className={styles.router_link}>
                    <img src={ingredients} className={styles.drawer_icon} alt='ingredients'/>
                    <ListItemText primary="M–∞—Ç–µ—Ä–∏–∞–ª—ã"/>
                </NavLink>
            </div>
            <div className={styles.navWrapper} onClick={() => {
                dispatch(setScreen({mainScreen: 'Recipes'}));
                setTimeout(() => {
                    setMobileOpen(false);
                }, 800);
            }}>
                <NavLink exact to="/recipes"
                         activeClassName="drawer_nav_selected"
                         className={styles.router_link}>
                    <img src={recepies} className={styles.drawer_icon} alt='recepies'/>
                    <ListItemText primary="–†–µ—Ü–µ–ø—Ç—É—Ä—ã"/>
                </NavLink>
            </div>
            <div className={styles.navWrapper} onClick={() => {
                dispatch(setScreen({mainScreen: 'Calendar'}));
                setTimeout(() => {
                    setMobileOpen(false);
                }, 800);
            }}>
                <NavLink exact to="/production"
                         activeClassName="drawer_nav_selected"
                         className={styles.router_link}>
                    <img src={works} className={styles.drawer_icon} alt='works'/>
                    <ListItemText primary="–ü—Ä–æ–¥—É–∫—Ü–∏—è"/>
                    </NavLink>
            </div>
            <div className={styles.navWrapper}>
                <a href="#" className={styles.router_link}>
                    <img src={clock} className={styles.drawer_icon_clock} alt='clock'/>
                    <ListItemText primary="–ú–æ–∏ —Ç–æ—Ä—Ç—ã"/>
                </a>
            </div>
            <div className={styles.navWrapper}>
                <a href="#" className={styles.router_link}>
                    <img src={person} className={styles.drawer_icon_clock} alt='person' />
                    <ListItemText primary="–ö–ª–∏–µ–Ω—Ç—ã"/>
                </a>
            </div>
            <div className={styles.navWrapper}>
                <a href="#" className={styles.router_link}>
                    <img src={pastrynet} className={styles.drawer_icon} alt='pastrynet'/>
                    <ListItemText primary="Pastrynet"/>
                </a>
            </div>
        </div>
    );

    const mainRouterObject = (<Router>
        <CssBaseline/>
        {isShowTopPanel &&
        <AppBar position="absolute" className={isBigScreenDrawerOpen ?
            classes.appBarDrawerOpen :
            classes.appBarDrawerClose}>
            <Toolbar className={screen.mainScreen === 'Calendar' ?
                classes.toolbarPurple :
                classes.toolbarWhite}>
                <IconButton
                    edge="start"
                    color="inherit"
                    aria-label="open drawer"
                    onClick={handleDrawerToggle}
                    className={isBigScreenDrawerOpen ?
                        classes.menuButtonDrawerOpen :
                        classes.menuButtonDrawerClose}
                >
                    <MenuIcon className={screen.mainScreen === 'Calendar' ?
                        'mobilePurple' :
                        ''}/>
                </IconButton>
                <div className={screen.mainScreen === 'Calendar' ?
                    classes.title :
                    classes.titleLeftSide}>
                    <div className={classes.calendarTopMobile}>

                        {
                            {
                                'Calendar': <div
                                    className={classes.mobScreenNameHeader}><img
                                    src={CalendarMobIcon}
                                    className={classes.calendarMobIcon} alt=""/><span
                                    className={classes.calendarMobIconText}>–ö–∞–ª–µ–Ω–¥–∞—Ä—å</span>
                                </div>,
                                'Materials': <div
                                    className={classes.mobScreenNameHeaderButton}>
                                    <img src={ingredients}
                                         className={classes.calendarMobIconBut}
                                         alt="ingredients"/><span
                                    className={classes.calendarMobIconText}>–ú–∞—Ç–µ—Ä–∏–∞–ª—ã</span>
                                </div>,
                                'Recipes': <div
                                    className={classes.mobScreenNameHeaderButton}>
                                    <img src={recepies}
                                         className={classes.calendarMobIconBut}
                                         alt="recepies"/><span
                                    className={classes.calendarMobIconText}>–†–µ—Ü–µ–ø—Ç—ã</span>
                                </div>,
                                'production': <div
                                    className={classes.mobScreenNameHeaderButton}>
                                    <img src={works}
                                         className={classes.calendarMobIconBut}
                                         alt=""/><span
                                    className={classes.calendarMobIconText}>–ü—Ä–æ–¥—É–∫—Ü–∏—è</span>
                                </div>,
                            }[screen.mainScreen]
                        }
                    </div>

                    {screen.mainScreen === 'Calendar' ?
                        <TodayButton>–°–µ–≥–æ–¥–Ω—è</TodayButton> :
                        ''}

                </div>
                <AccountLogin/>
            </Toolbar>
        </AppBar>
        }

        <nav className={isBigScreenDrawerOpen ?
            classes.drawerOpen :
            classes.drawerClose} aria-label="mailbox folders">
            {isBigScreenDrawerOpen ? (
                windowSize < 1300 ? (
                    <Drawer
                        container={container}
                        className={classes.drawer}
                        variant="temporary"
                        open={mobileOpen}
                        onClose={handleDrawerToggle}
                        classes={{
                            paper: classes.drawerPaper,
                        }}
                        ModalProps={{
                            keepMounted: true, // Better open performance on mobile.
                        }}
                    >
                        <div className={classes.toolbarIcon}>
                            <IconButton onClick={handleDrawerToggle}>
                                <ChevronLeftIcon/>
                            </IconButton>
                        </div>
                        <Divider/>
                        <img src={logo} className={classes.drawer_logo} alt='logo'/>
                        <List>{mainListItems}</List>
                    </Drawer>
                ) : (<Drawer
                    className={classes.drawer}
                    variant="permanent"
                    anchor="left"
                    open={true}
                    classes={{
                        paper: classes.drawerPaper,
                    }}
                >
                    <div className={classes.toolbarIcon}>
                    </div>
                    <Divider/>
                    <img src={logo} className={classes.drawer_logo} alt='logo'/>
                    <List>{mainListItems}</List>
                </Drawer>)
            ) : (
                <Drawer
                    container={container}
                    className={classes.drawer}
                    variant="temporary"
                    open={mobileOpen}
                    onClose={handleDrawerToggle}
                    classes={{
                        paper: classes.drawerPaper,
                    }}
                    ModalProps={{
                        keepMounted: true, // Better open performance on mobile.
                    }}
                >
                    <div className={classes.toolbarIcon}>
                        <IconButton onClick={handleDrawerToggle}>
                            <ChevronLeftIcon/>
                        </IconButton>
                    </div>
                    <Divider/>
                    <img src={logo} className={classes.drawer_logo} alt='logo'/>
                    <List>{mainListItems}</List>
                </Drawer>
            )
            }
        </nav>
        <Switch>
            <Route exact path="/" component={Calendar}/>
            <Route exact path="/ingredients" component={Ingregients}/>
            <Route exact path="/recipes" component={Recipes}/>
            <Route exact path="/make_recipe" component={MakeRecipeIndex}/>
            <Route exact path="/make_order" component={MakeOrderIndex}/>
            <Route exact path="/production" component={Product}/>
            <Route exact path="/make_product" component={MakeProductIndex}/>
        </Switch>
    </Router>);

    return (
        <div className={loading ? classes.rootLoading : classes.root}>
            {loading ? <SpinnerOne/> : mainRouterObject}
        </div>
    );
}
