import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import {useSelector} from "react-redux";

const DateInMobileBottomBlock = () => {
    const classes = useStyles();
    let startDate, endDate;
    const date = useSelector(state => {
        return {
            startDate: state.date.startDate,
            endDate: state.date.endDate
        }
    });

    function convertDate(date_str) {

        let months = ["–Ø–Ω–≤–∞—Ä—è", "–§–µ–≤—Ä–∞–ª—è", "–ú–∞—Ä—Ç–∞", "–ê–ø—Ä–µ–ª—è", "–ú–∞—è", "–ò—é–Ω—è", "–ò—é–ª—è", "–ê–≤–≥—É—Å—Ç–∞", "–°–µ–Ω—Ç—è–±—Ä—è", "–û–∫—Ç—è–±—Ä—è", "–ù–æ—è–±—Ä—è", "–î–µ–∫–∞–±—Ä—è"];
        let temp_date = date_str.split("-");
        return {
            date: temp_date[2],
            month: months[Number(temp_date[1]) - 1]
        }
    }

    if (date.startDate.getTime() !== date.endDate.getTime()) {
        date.startDate = new Date(date.startDate.getTime()+1000*60*60*24);
        date.endDate = new Date(date.endDate.getTime()+1000*60*60*24);

        startDate = convertDate(date.startDate.toISOString().slice(0, 10));
        endDate = convertDate(date.endDate.toISOString().slice(0, 10));
    } else {
        date.startDate = new Date(date.startDate.getTime()+1000*60*60*24);
        startDate = convertDate(date.startDate.toISOString().slice(0, 10));
        endDate = false;
    }


    return (
        <div className={classes.dateInMobileBottomBlock}>
            <div className={classes.startDate}><span
                className={classes.startDateDate}>  {startDate.date}</span> {startDate.month}</div>
            {endDate && <div className={classes.endDate}><span
                className={classes.endDateDate}> {endDate.date}</span> {endDate.month}</div>}
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    dateInMobileBottomBlock: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column',
        flexWrap: 'wrap',
        fontSize: '0.7rem',
        fontWeight: 'bold',
        marginLeft: '7px',
        [theme.breakpoints.up('sm')]: {
            display: 'none',
        }
    },
    startDate: {
        position: "relative",
    },
    endDate: {

    },
    startDateDate: {

    },
    endDateDate: {
        fontSize: '0.9rem',
        height: '10px'
    }
}))

export default DateInMobileBottomBlock;
