import axios from 'axios';
import {
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT,
    SET_LOADING
} from "./types/AuthTypes.js";

export const login = () => async dispatch => {
    const token = localStorage.getItem('token');

    if(token) {
        try {

            const resp = await axios.get( 'authenticates/user');

            if (resp.data.id) {
                dispatch({
                    type: LOGIN_SUCCESS,
                    payload: resp.data
                });
            } else {
                dispatch({
                    type: LOGIN_FAIL,
                });
            }

        } catch (e) {
            dispatch({
                type: LOGIN_FAIL,
            });
        }
    } else {
        dispatch({
            type: LOGIN_FAIL,
        });
    }

};

export const setLoading = (userData) => async dispatch => {
    dispatch({
        type: SET_LOADING,
        payload: userData
    });
};


// Register User
export const registerSuccess = (userData) => async dispatch => {
    dispatch({
        type: REGISTER_SUCCESS,
        payload: userData
    });
};

export const registerFail = () => async dispatch => {
    dispatch({
        type: REGISTER_FAIL
    });
}

// Login User
export const loginSuccess = (userData) => async dispatch => {
    console.log('login success action')

    dispatch({
            type: LOGIN_SUCCESS,
            payload: userData
        });
};

export const loginFail = () => async dispatch => {
    console.log('login fail action')
    dispatch({
        type: LOGIN_FAIL
    });
}

