import {withStyles} from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import React from "react";

const TextFieldLite = withStyles((theme) => ({
  //////—Å–º–µ–Ω–∞ —Ü–≤–µ—Ç–∞ –∏ –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤ —Å –ª–∏–Ω–∏–µ–π –ø—Ä–∏ –≤–≤–æ–¥–µ
  root: {
    "& input:valid + fieldset": {
      //–≤–≤–æ–¥ –¥–∞–Ω–Ω—ã—Ö
      borderColor: global.config.colors.lightGray,
      borderWidth: 1,
    },
    "& input:invalid + fieldset": {
      //–∑–Ω–∞—á–µ–Ω–∏–µ –Ω–µ –≤–≤–µ–¥–µ–Ω–æ
      borderColor: global.config.colors.red,
      borderWidth: 1,
    },
    "& input:valid:focus + fieldset": {
      //–≤–≤–æ–¥
      borderLeftWidth: 6,
      padding: "4px !important", // –ø–∞—Ä–∞–º–µ—Ç—Ä—ã –ª–∏–Ω–∏–∏
      borderColor: global.config.colors.purple,
    },
    "& label.Mui-focused + div fieldset": {
      //–ø–æ–ª–µ –≤ —Ñ–æ–∫—É—Å–µ –±–µ–∑ —Ç–µ–∫—Å—Ç–∞
      borderColor: global.config.colors.purple + "!important",
    },
    "& label.Mui-focused": {
      //—è—Ä–ª—ã–∫ –≤ —Ñ–æ–∫—É—Å–µ
      color: global.config.colors.red,
    },
    "& label": {
      // —è–ª—ã–∫
      color: global.config.colors.purple,
    },
  }
}))((props) => {
  let customError = false;
  let customHelperText = '';
  let val = props.value;


  // –∑–¥–µ—Å—å –ø–æ—Ç–æ–º –±—É–¥—É—Ç —É—Å–ª–æ–≤–∏—è. –ü–æ–∫–∞ –ø—Ä–æ—Å—Ç—ã–µ —É—Å–ª–æ–≤–∏—è  - –ø—Ä–æ—Å—Ç–æ –¥–ª—è —Ç–µ—Å—Ç–∞
  switch(props.validType) {
    case('number'):
      if(isNaN(Number(val))) {
        customHelperText = '–û—à–∏–±–∫–∞';
        customError = true;
      }
      break;
    case('string'):
      break;
    default:
      break;

  }

  // –µ—Å–ª–∏ —É–∂–µ –ø–µ—Ä–µ–¥–∞—é—Ç—Å—è –Ω–µ –º–µ–Ω—è—Ç—å
  if (props && !props.validType) {
    props = {validType: 'string', ...props}
  }
  // –µ—Å–ª–∏ —É–∂–µ –ø–µ—Ä–µ–¥–∞—é—Ç—Å—è –Ω–µ –º–µ–Ω—è—Ç—å
  if (props && !props.error) {
    props = {error: customError, ...props}
  }
  // –µ—Å–ª–∏ —É–∂–µ –ø–µ—Ä–µ–¥–∞—é—Ç—Å—è –Ω–µ –º–µ–Ω—è—Ç—å
  if (props && !props.helperText) {
    props = {helperText: customHelperText, ...props}
  }

  delete props.validType

  return <TextField  {...props} />
});

export default TextFieldLite;
