import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import Autocomplete from '@material-ui/lab/Autocomplete'
import TextField from '@material-ui/core/TextField';


const CalendarSearchField = () => {
  const options = filling.map((option) => {
    const firstLetter = option.title[0].toUpperCase();
    return {
      firstLetter: /[0-9]/.test(firstLetter) ? '0-9' : firstLetter,
      ...option,
    };
  });
  return (
      <>
        <Autocomplete
            options={options.sort((a, b) => -b.firstLetter.localeCompare(a.firstLetter))}
            groupBy={(option) => option.firstLetter}
            getOptionLabel={(option) => option.title}
            blurOnSelect = 'touch'
            clearOnBlur
            style={{ width: 300 }}
            renderInput={(params) => (
                <TextField {...params} label="–ø–æ–∏—Å–∫ –ø–æ –∏–º–µ–Ω–∏" margin="normal" variant="outlined" size='small'
                />

            )}
        />

      </>
  )
}


const filling = [
  { title: '–ú–∞—Ä—É—Å—è', date: '28.02.20' },
  { title: '–ù–∞—Å—Ç—è', date: '28.02.20' },
  { title: '–í–∞–ª—è', date: '28.02.20' },
  { title: '–ú–∞—Ä—É—Å—è1', date: '28.02.20' },
  { title: '–ù–∞—Å—Ç—è1', date: '28.02.20' },
  { title: '–í–∞–ª—è', date: '28.02.20' },];

const useStyles = makeStyles((theme) => ({
  SummaryRightPanelSearchLine: {
    position: 'relative',
    flexBasis: '50%',
    [theme.breakpoints.down('xs')]: {
      display: 'none'
    },
  },
  searchIconMobile: {
    display: 'none',
    fill: '#DADADA',
    [theme.breakpoints.down('xs')]: {
      display: 'block',
      marginRight: '2px'
    },
  },
  searchField: {
    backgroundColor: 'rgba(218, 218, 218, 0.5)',
    border: 'none',
    height: '29px',
    margin: '10px 0',
    paddingLeft: '40px',
    width: '100%',
    maxWidth: '300px',
    '&:focus': {
      outline: 'none'
    },
  },

  searchIcon: {
    fill: '#fff',
    position: 'absolute',
    top: '13px',
    left: '10px'
  }
}))

export default CalendarSearchField;
