import React, {useEffect, useState} from "react";
import {makeStyles} from "@material-ui/core/styles";
import {useDispatch, useSelector} from "react-redux";
import {isBigScreenDrawerOpen} from "../../../actions/effectsActions";
import {isShowTopPanel} from "../../../actions/effectsActions";
import {CustomTabPurple, CustomTabsPurple} from "../../../components/materialize/CustomTabMobPurple";
import MakeProductFirstBlock from "./MakeProductFirstBlock";
import MakeProductSecondBlock from "./MakeProductSecondBlock";
import MakeProductThirdBlock from "./MakeProductThirdBlock";
import tabSetProps from "../../../components/materialize/smallElements/TabSetProps";
import TabPanel from "../../../components/materialize/TabPanel";
import useWindowSize from '../../../hooks/useWindowSize';
import GoldArrowBack from '../../../components/common/Icons/GoldArrowBack';
import IconButton from '@material-ui/core/IconButton';
import {NavLink} from "react-router-dom";
import {changeProductSaveButtonState} from "../../../actions/products/makeProductActions";
import store from "../../../store";
import Button from '@material-ui/core/Button';

const MakeProductIndex = () => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const [tabValue, setTabValue] = useState(0);
    const windowSize = useWindowSize();

    const saveProductButtonActive = useSelector(
      state => state.product.makeProduct.saveProductButtonActive);

    useEffect(() => {
        dispatch(isBigScreenDrawerOpen(false));
        if(windowSize < 668) {
            dispatch(isShowTopPanel(false));
        }
        sessionStorage.removeItem('saveProductStoreButton');

        return() => {
            store.dispatch(changeProductSaveButtonState(false))
        }
    }, [dispatch, windowSize])

    const tabHandleChange = (event, newValue) => {
        setTabValue(newValue);
    };
    return (
        <div className={classes.makeRecipesTab}>
            <div>
                <div className={classes.topPurpleLine}>
                    <div className={classes.leftlineBlock}>
                    <NavLink exact to="/production"  >
                       <IconButton color = "secondary">
                            <GoldArrowBack/>
                       </IconButton>
                    </NavLink>
                            <div className={classes.plusIcon}>+
                             </div>
                            –ù–∞—á–∏–Ω–∫–∞
                    </div>
                    <div className={classes.rightlineBlock}>
                        {/*<div
                          className={classes.rightlineCancelButton}>
                            –û—Ç–º–µ–Ω–∏—Ç—å –∏–∑–º–µ–Ω–µ–Ω–∏—è
                        </div>*/}
                        <Button  size = 'small' variant="outlined"
                                 disabled={saveProductButtonActive ? false : true}
                                 className={classes.rightlineSaveButton }
                                 onClick={() =>  saveProductButtonActive ? console.log("saveProduct"): ''}>–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</Button>
                    </div>
                </div>
                <div className={classes.makeRecipesBlocks}>
                    <MakeProductFirstBlock/>
                    <div className={classes.secondAndThirdWrapper}>
                        <MakeProductSecondBlock/>
                        <MakeProductThirdBlock/>
                    </div>
                </div>
            </div>

            <div className={classes.mobile}>
                <CustomTabsPurple
                    value={tabValue}
                    onChange={tabHandleChange}
                    indicatorColor="primary"
                    textColor="primary"
                    variant="scrollable"
                    scrollButtons="auto"
                    aria-label="scrollable auto tabs example"
                >
                    <CustomTabPurple label="–†–µ—Ü–µ–ø—Ç—É—Ä–∞" {...tabSetProps(0)} />
                    <CustomTabPurple label="–ü–µ—Ä–µ—Å—á—ë—Ç" {...tabSetProps(1)} />
                    <CustomTabPurple label="–¢—Ä—É–¥–æ–∑–∞—Ç—Ä–∞—Ç—ã" {...tabSetProps(2)} />
                </CustomTabsPurple>
                <TabPanel value={tabValue} index={0}>
                    <MakeProductFirstBlock/>
                </TabPanel>
                <TabPanel value={tabValue} index={1}>
                    <MakeProductSecondBlock/>
                </TabPanel>
                <TabPanel value={tabValue} index={2}>
                    <MakeProductThirdBlock/>
                </TabPanel>
            </div>
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    makeRecipesTab: {
        width: '100%',
        marginTop: '65px',
            '@media (max-width:668px)': {
                marginTop: '0',
            }
    },
    moreButton: {

    },
    moreButtonWrapper: {
        marginLeft: '10px',
        '& .MoreVertIcon-root-72': {
            color: '#fff!important'
        },
        '@media (min-width:668px)': {
            display: 'none'
        }
    },
    desktop: {

    },
    mobile: {
        '@media (min-width:668px)': {
            display: 'none'
        }
    },
    topPurpleLine: {
        display: 'flex',
        justifyContent: 'space-between',
        backgroundColor: global.config.colors.purple,
        color: '#fff',
        padding: '6px 2%'
    },
    leftlineBlock: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
    },
    rightlineBlock: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
    },
    arrowIcon: {
        fill: '#fff',
        width: '20px',
        cursor: 'pointer'
    },
    plusIcon: {
        marginLeft: '7px',
        marginRight: '7px',
    },
    rightlineCancelButton: {
        marginRight: '7px',
        cursor: 'pointer',
        '@media (max-width:668px)': {
            display: 'none',
        }
    },
    rightlineSaveButton: {
        background: global.config.colors.purple ,
        color:  '#fff',
        borderRadius: '13px',
        justifyContent: 'center',
        alignItems: 'center',
        fontWeight: '300',
        padding: '1px 10px',
        cursor: 'pointer',
        '&:hover': {
            background: '#fff',
            color:global.config.colors.gold,
        },
        [theme.breakpoints.down('xs')]: {
            display: 'flex',
        },
    },
    rightlineSaveButtonDisabled: {
        color: global.config.colors.purple,
        backgroundColor: '#fff',
        borderRadius: '20px',
        padding: '3px 12px',
        cursor: 'pointer',
        opacity: '0.6',
        '@media (max-width:668px)': {
            background: 'none',
            color: '#fff',
            display: 'flex'
        }
    },
    makeRecipesBlocks: {
        display: 'flex',
        paddingTop: '30px',
        '@media (max-width:668px)': {
            display: 'none'
        }
    },
    secondAndThirdWrapper: {
        display: 'flex',
        flexBasis: '50%',
        '@media (max-width:1300px)': {
            flexWrap: 'wrap'
        }
    }
}))

export default MakeProductIndex;
