import {
    ADD_PRODUCT,
    EDIT_PRODUCT,
    DELETE_PRODUCT,
    EDIT_PRODUCT_LOOSES,
    EDIT_TEST_EXIT,
    CHANGE_PRODUCT_SAVE_BUTTON_STATE, CHANGE_STANDARTISATION

} from '../types/Products/MakeProductTypes';

export const addProduct = (data) => async dispatch => {
    dispatch({
        type: ADD_PRODUCT,
        payload: data,
    });
};

export const editProduct = (data) => async dispatch => {
    dispatch({
        type: EDIT_PRODUCT,
        payload: data,
    });
};

export const deleteProduct = (id) => async dispatch => {
    dispatch({
        type: DELETE_PRODUCT,
        payload: id,
    });
};

export const editLooses = (data) => async dispatch => {
    dispatch({
        type: EDIT_PRODUCT_LOOSES,
        payload: data,
    });
};

export const editTestExit = (data) => async dispatch => {
    dispatch({
        type: EDIT_TEST_EXIT,
        payload: data,
    });
};

export const changeProductSaveButtonState = (data) => async dispatch => {
    dispatch({
        type: CHANGE_PRODUCT_SAVE_BUTTON_STATE,
        payload: data,
    });
};

export const changeStandartisation = (data) => async dispatch => {
    dispatch({
        type: CHANGE_STANDARTISATION,
        payload: data,
    });
};


