import React from "react";
import PropTypes from "prop-types";
import {makeStyles} from "@material-ui/core/styles";

const ChoiceDateButton = ({children, active}) => {
    const classes = useStyles();

    return (
        <div className={classes.button + ' ' + (active ? classes.active: '' )}>
            {children}
        </div>
    )
}

const useStyles = makeStyles(() => ({
    button: {
        backgroundColor: "#f4f4ec",
        transition: "opacity 0.25s linear",
        color: "#000",
        height: "30px",
        width: "100%",
        top: "109px",
        zIndex:"2",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        cursor: "pointer",
        position: "absolute",
        opacity: "0"
    },
    active: {
        opacity: "1"
    }
}));

ChoiceDateButton.propTypes = {
    children: PropTypes.string.isRequired
};


export default ChoiceDateButton;
