import React, {useEffect} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import AddMaterialButton from '../AddMaterialButton';
import MaterialStoreItem from './MaterialStoreItem';
import {useDispatch, useSelector} from 'react-redux';
import {
  addNewMaterialStore,deleteNewMaterialStore,getSavedMaterialStore
} from '../../../../actions/ingredients/materialStore/materialStoreActions';
import {nanoid} from 'nanoid';
import orderBy from 'lodash/orderBy';
import SpinnerColor
  from '../../../../components/materialize/smallElements/SpinnerColor';
import axios from 'axios';

const MaterialStoreOrdersListBlockLeft = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const newMaterialStoreArr = useSelector(
      state => state.ingredients.materialStoreList.newMaterialStore);
  const savedMaterialStoreArr = useSelector(
      state => state.ingredients.materialStoreList.savedMaterialStore);
  const currentMaterialStoreCategory = useSelector(
      state => state.ingredients.materialStoreList.currentMaterialStoreCategory);
  const loadingMaterialStore = useSelector(
      state => state.ingredients.materialStoreList.loadingMaterialStore);


  useEffect(() => {
    dispatch(getSavedMaterialStore(currentMaterialStoreCategory.id));
  }, [currentMaterialStoreCategory.id, dispatch]);

  const addLineFunc = () => {
    dispatch(addNewMaterialStore({
      category_id : currentMaterialStoreCategory.id,
      id: 0,
      nanoId: nanoid(5),
      title: '',
      count: [100],
      price: [100],
    }));
  };

  const deleteLineFunc = (num, savedMaterialStore, nanoId) => {
    if (savedMaterialStore) {
      try {
        // –∏–º–µ–Ω–Ω–æ —Ç–∞–∫ axios —Ç—Ä–µ–±—É–µ—Ç —á—Ç–æ–±—ã —á–µ—Ä–µ–∑ delete –±—ã–ª–æ
        let config = {
          headers: {
            Authorization: 'Bearer ' + localStorage.getItem('token'),
          },
          data: {
            'materials_ids': [num],
          },
        };

        axios.delete('/materials/'+num, config).then(response => {
          dispatch(getSavedMaterialStore(currentMaterialStoreCategory.id));
        }).catch(er => {
          console.error(er)
        });
      } catch (e) {
        console.error(e);
      }
    } else {
      // –µ—Å–ª–∏ —ç—Ç–æ –Ω–æ–≤—ã–π –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç
      dispatch(deleteNewMaterialStore(nanoId));
    }
  };

  return (
      <div className={classes.ordersListBlockLeft}>
        <AddMaterialButton addLine={addLineFunc}/>
        { orderBy(newMaterialStoreArr, ['nanoId'], ['desc'])
        .map(e => <MaterialStoreItem key={e.nanoId} elem={e}
                                deleteLine={deleteLineFunc} MaterialStoreType='new'/>)
        }
        {loadingMaterialStore ? (<div className="fullWidthFullHeightCenter">
              <SpinnerColor />
            </div>)
            :
            (

                orderBy(savedMaterialStoreArr, ['id'], ['desc'])
                  .map(m => <MaterialStoreItem key={m.id} elem={m}
                                           deleteLine={deleteLineFunc} MaterialStoreType='old'
                                                savedMaterialStore={true}/>)
            )
        }
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  ordersListBlockLeft: {
    flexBasis: '100%',
    [theme.breakpoints.down('xs')]: {
      flexBasis: '100%',
    },
  },
}));

export default MaterialStoreOrdersListBlockLeft;
