import React, {useEffect, useState} from "react";
import {makeStyles} from "@material-ui/core/styles";
import OrdersListBlockLeft from "./OrdersListBlockLeft";
import OrdersListBlockRight from "./OrdersListBlockRight";
import PropTypes from "prop-types";

const OrdersListBlock = ({showMobileOrders}) => {
    let [classesComputed, setClassesComputed] = useState('orderBlockCustomScrollBar');

    useEffect(() => {
        if (showMobileOrders) {
            setClassesComputed('orderBlockCustomScrollBarMobileShow');
        } else {
            setClassesComputed('orderBlockCustomScrollBar');
        }
    }, [showMobileOrders]);

    return (
        <div className={classesComputed}>
            <OrdersListBlockLeft/>
            <OrdersListBlockRight/>
        </div>
    )
}

const useStyles = makeStyles((theme) => ({

}))


OrdersListBlock.propTypes = {
    showMobileOrders: PropTypes.bool.isRequired,
};

export default OrdersListBlock;