import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import TextFieldLite from "../../../components/common/TextFieldLite";
import {useDispatch, useSelector} from 'react-redux';
import {editTestExit} from '../../../actions/recipes/makeRecipeActions';
import {nanoid} from 'nanoid';
import sortBy from 'lodash/sortBy';


const MakeRecipeSecondBlock = () => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const testIngredients = useSelector(
        state => state.recipes.makeRecipe.testIngredients);
    const testExit = useSelector(
        state => state.recipes.makeRecipe.testExit);


    const changeTestExit = (event) => {
        dispatch(editTestExit(event.target.value))
    }


    return (
        <div className={classes.MakeRecipeSecondBlock}>
            <div className={classes.blockInfo}>
                <h2>–ü–µ—Ä–µ—Å—á—ë—Ç</h2>
                <div>
                    –ü–µ—Ä–µ—Å—á—ë—Ç –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç–æ–≤ —Ä–µ—Ü–µ–ø—Ç—É—Ä—ã –Ω–∞ –Ω—É–∂–Ω—ã–π –≤–µ—Å
                </div>
            </div>
            <TextFieldLite label="–ù—É–∂–Ω—ã–π –≤–µ—Å" required  size="small" variant="outlined" value={testExit}
                           className={classes.weightField} onChange={changeTestExit} type='number' />

            <div className={classes.makeRecipesFirstBlockRight}>
                {sortBy(testIngredients, [function(o) { return o.id; }]).map((item) => {
                    return (
                        <div className={classes.lineOne} key={nanoid(5)}>
                            <div className='capital_first_letter'>{item.name === '' ? '–ë–µ–∑ –Ω–∞–∑–≤–∞–Ω–∏—è' : item.name}</div>
                            <div className={classes.columnLine}></div>
                            <div className={classes.columnTwo}>{item.weight}</div>
                        </div>
                    )
                })}
            </div>
        </div>
    );
};

const useStyles = makeStyles((theme) => {
    return {
        MakeRecipeSecondBlock: {
            flexBasis: '49%',
            border: '1px solid #a7a0a0',
            boxShadow: '0px 10px 15px -5px rgba(0,0,0,0.5)',
            padding: '1%',
            paddingTop: '0',
            textAlign: 'center',
            justifyContent: 'center',
            flexWrap: 'wrap',
            maxWidth: '380px',
            height: '70vh',
            marginRight: '20px',
            '@media (max-width:1300px)': {
                flexBasis: '100%',
                maxWidth: 'initial',
                marginBottom: '20px',
            },
            '@media (max-width:668px)': {
                border: 'none',
                boxShadow: 'none',
                margin: '0 5px',
            },
        },
        weightField: {},
        blockInfo: {
            textAlign: 'left',
            borderBottom: '1px solid #c1bdbdde',
            paddingBottom: '35px',
            marginBottom: '20px',
        },
        lineOne: {
            display: 'flex',
            justifyContent: 'space-between',
            fontWeight: 'bold',
            alignItems: 'center',
            marginBottom: '5px',
        },
        columnLine: {
            height: '2px',
            borderBottom: '1px dotted #000',
            flexBasis: '60%',
        },
        makeRecipesFirstBlockRight: {
            marginTop: '20px',
            flexBasis: '75%',
        },
    };
});

export default MakeRecipeSecondBlock;
