import {
    ADD_NEWINGREDIENT,
    DELETE_NEWINGREDIENT,
    EDIT_NEWINGREDIENT,
    EDIT_OLDINGREDIENT,
    GET_SAVED_INGREDIENTS,
    SET_CURRENT_INGREDIENTS_CATEGORY,
    SET_LOADING_INGREDIENTS,
    DELETE_ALL_NEWINGREDIENT,
    SET_SAVE_BUTTON
} from '../types/ingredients/ingredientTypes';
import axios from 'axios';



export const setLoadingIngredients = (userData) => async dispatch => {
    dispatch({
        type: SET_LOADING_INGREDIENTS,
        payload: userData
    });
};

export const addNewIngredients = (data) => async dispatch => {
    dispatch({
        type: ADD_NEWINGREDIENT,
        payload: data,
    });
};

export const editNewIngredients = (data) => async (dispatch, getState) => {
    dispatch({
        type: EDIT_NEWINGREDIENT,
        payload: data,
    });
    const state = getState();
    const changed = state.ingredients.ingredientsList.newIngredients.filter((item) => item.title !== '');
    if(changed.length > 0) {
      dispatch(setSaveButtonStatus(true));
    } else {
      dispatch(setSaveButtonStatus(false));
    }
};

export const editOldIngredients = (data) => async (dispatch, getState) => {
  dispatch({
    type: EDIT_OLDINGREDIENT,
    payload: data,
  });
  const state = getState();
  const changed = state.ingredients.ingredientsList.savedIngredients.filter((item) => item.title !== '');
  if(changed.length > 0) {
    dispatch(setSaveButtonStatus(true));
  } else {
    dispatch(setSaveButtonStatus(false));
  }
};

export const deleteNewIngredients = (id) => async dispatch => {
    dispatch({
        type: DELETE_NEWINGREDIENT,
        payload: id,
    });
};

export const setSaveButtonStatus = (data) => async dispatch => {
  dispatch({
    type: SET_SAVE_BUTTON,
    payload: data,
  });
};

export const deleteAllNewIngredients = () => async dispatch => {
  dispatch({
    type: DELETE_ALL_NEWINGREDIENT,
  });
};

export const saveNewIngredients = (data, callbackSuccess, callbackError) => async dispatch => {
    try {
        callbackSuccess();

    } catch (e) {
        callbackError();
        console.error(e);
    }
};

export const saveOldIngredients = (data, callbackSuccess, callbackError) => async dispatch => {
  try {
    callbackSuccess();

  } catch (e) {
    callbackError();
    console.error(e);
  }
};

export const getSavedIngredients = (id = 9) => async dispatch => {

    try {
        dispatch(setLoadingIngredients(true));
        const savedIngredients = await axios.get('/ingredient_amounts',
            {params: {'category_id': id}});
        dispatch(setLoadingIngredients(false));
        dispatch({
            type: GET_SAVED_INGREDIENTS,
            payload: savedIngredients.data,
        });
    } catch (err) {
        console.error(err.response.data);
    }

};

export const setCurrentIngredientsCategory = (el) => async dispatch => {
    dispatch({
        type: SET_CURRENT_INGREDIENTS_CATEGORY,
        payload: el,
    });
};
