import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import CalendarSearchField from "./CalendarSearchField";
import TotalOrdersInfo from "./TotalOrdersInfo";

const SummaryRightPanelSearchLine = () => {
    const classes = useStyles();
    return (
        <div className={classes.summaryRightPanelSearchLine}>
            <TotalOrdersInfo/>
            <CalendarSearchField/>
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    summaryRightPanelSearchLine: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        borderBottom: '1px solid #d0cccc',
    }
}))

export default SummaryRightPanelSearchLine;
