import React, {useEffect} from "react";
import Box from "@material-ui/core/Box";
import Grid from "@material-ui/core/Grid";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import {CustomTabIngredients, CustomTabsIngredients} from "./components/CustomTabIngredients";
import tabSetProps from "../../components/materialize/smallElements/TabSetProps";
import Hidden from "@material-ui/core/Hidden";
import TabPanelBlocksIngredients from "./components/TabPanelBlocksIngredients";
import Copyright from "../../components/materialize/Copyright";
import useStylesDashboard from "../../css/useStyle/Dashboard.css";
import {isBigScreenDrawerOpen} from "../../actions/effectsActions";
import {useDispatch} from "react-redux";

const Ingregients = () => {
    const classes = useStylesDashboard();
    const [tabValue, setTabValue] = React.useState(0);
    const dispatch = useDispatch();
    const tabHandleChange = (event, newValue) => {
        setTabValue(newValue);
    };

    useEffect(() => {
        dispatch(isBigScreenDrawerOpen(true));
    },[dispatch])

    return (
        <main className={classes.content}>
            <div className={classes.appBarSpacer}/>
            <Box>
                <Grid container>
                    <Grid item xl={4} lg={4} md={4} xs={12} className={classes.calendarHeaderMenu}>
                        <AppBar position="static" className={classes.appBarTabs}>
                            <Toolbar className={classes.toolbar}>

                                <CustomTabsIngredients
                                    value={tabValue}
                                    onChange={tabHandleChange}
                                    indicatorColor="primary"
                                    textColor="primary"
                                    variant="scrollable"
                                    scrollButtons="auto"
                                    aria-label="scrollable auto tabs example"
                                >
                                    <CustomTabIngredients label="–ò–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç—ã" {...tabSetProps(0)} />
                                    <CustomTabIngredients label="–ú–∞—Ç–µ—Ä–∏–∞–ª—ã" {...tabSetProps(1)} />
                                </CustomTabsIngredients>
                            </Toolbar>
                        </AppBar>
                        <div className={classes.greyLineMobOrdersLight}></div>
                    </Grid>
                    <Hidden mdDown>
                        <Grid className={classes.adTopBlock}>
                            –ú–µ—Å—Ç–æ –ø–æ–¥ –±–∞–Ω–Ω–µ—Ä
                        </Grid>
                    </Hidden>
                </Grid>
            </Box>
            <TabPanelBlocksIngredients tabValue={tabValue}/>
            <Box pt={4}>
                <Copyright/>
            </Box>
        </main>
    )
}

export default Ingregients;
