import React, {useState} from 'react';
import MakeOrderMakeOrderCreated from './MakeOrderMakeOrderCreated';
import MakeOrderMakeOrder from './MakeOrderMakeOrder';
import Collapse from '@material-ui/core/Collapse';

const MakeOrderWrapper = ({addLine}) => {
  const [openEdit, setOpenEdit] = useState(true);

  const isOpen = (open) => {
    setOpenEdit(open);
  }
  return (
      <>
        <MakeOrderMakeOrderCreated isOpen={isOpen}/>
        <Collapse in={openEdit}>
          <MakeOrderMakeOrder />
        </Collapse>
      </>
  )
}

export default MakeOrderWrapper
