import { SET_SCREEN, GET_SCREEN } from '../actions/types/ScreenTypes';

const initialState = {
    mainScreen: 'Calendar',
    subScreen: '',
};

const screenReducer =  (state = initialState, action) => {
    switch (action.type) {
        case GET_SCREEN:
            return state;
        case SET_SCREEN:
            return {
                ...state,
                mainScreen: action.payload.mainScreen,
                subScreen: action.payload.subScreen
            };
        default:
            return state;
    }
}

export default screenReducer;