import React, {useState} from "react";
import Grid from "@material-ui/core/Grid";
import MaterialStoreSummaryRightPanel from './MaterialStoreSummaryRightPanel'
import {makeStyles} from "@material-ui/core/styles";
import MaterialStoreOrdersListBlock from './MaterialStoreOrdersListBlock';

const MaterialStoreRightBlock = () => {
  const classes = useStyles();

  let [showMobileOrders, setShowMobileOrders] = useState(false);

  function toggleBottomMenu(status) {
    setShowMobileOrders(status)
  }

  return (
      <Grid item className={classes.materialsRightBlock}>
        <MaterialStoreSummaryRightPanel toggleBottomMenu={toggleBottomMenu}/>
        <MaterialStoreOrdersListBlock showMobileOrders={showMobileOrders}/>
      </Grid>
  )
}

const useStyles = makeStyles((theme) => ({
  materialsRightBlock: {
    padding: '0 1%',
    maxWidth: '840px',
    flex: 1
  },
  materialTypeButton: {
    display: 'flex',
    background: '#5D1049',
    color: '#fff',
    borderRadius: '13px',
    justifyContent: 'center',
    alignItems: 'center',
    fontWeight: '300',
    padding: '1px 10px',
    width:' 107px',
    float: 'right',
    marginTop: '15px',
    cursor: 'pointer',
    [theme.breakpoints.down('xs')]: {
      display: 'none',
    }
  },
  materialTypeButtonWrapper: {
    borderTop:'1px solid #c1bebe',
    marginTop: '20px'
  }
}))

export default MaterialStoreRightBlock;
