import DeleteIcon from '@material-ui/icons/Delete';
import {withStyles} from "@material-ui/core/styles";


 const FormDeleteIcon = withStyles ((theme) =>({
    root: {
        height: '30px',
        width: '30px',
        color: global.config.colors.darkGray,
        '&:hover': {
            cursor: 'pointer',
            color:global.config.colors.red,
          },
    }
    
}))(DeleteIcon);

export default FormDeleteIcon;