import {
  ADD_NEW_CLIENT,
  SHOW_WHATS_APP,
  SHOW_DELIVER
} from '../../actions/types/clients/makeClientTypes';

const initialState = {
  name: '',
  phone: '',
  address: '',
  socialNetworks: '',
  date: null,
  time: null,
  showWhatsApp: '',
  showDeliver : ''
};

const makeClientsReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_NEW_CLIENT:
      return {
        ...state,
        ...action.payload,
      };
    case SHOW_DELIVER:
      return {
        ...state,
        showDeliver: action.payload,
      };
    case SHOW_WHATS_APP:
      return {
        ...state,
        showWhatsApp: action.payload,
      };
    default:
      return state;
  }
};

export default makeClientsReducer;
