import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import TextFieldLite from '../../../components/common/TextFieldLite';
import {editLooses} from '../../../actions/recipes/makeRecipeActions';
import {useDispatch, useSelector} from 'react-redux';

const MakeRecipeFirstBlockRight = () => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const looses = useSelector(
        state => state.recipes.makeRecipe.looses);
    const exit = useSelector(
        state => state.recipes.makeRecipe.exit);
    const priceKg = useSelector(
        state => state.recipes.makeRecipe.priceKg);

    const handleEditLooses = (e) => {
        dispatch(editLooses(e.target.value));
    };

    return (
        <div className={classes.makeRecipesFirstBlockRight}>
            <div className={classes.leftBlock}>
                <div className={classes.lineOne}>
                    <div className={classes.columnOne}>–í—ã—Ö–æ–¥</div>
                    <div className={classes.columnLine}></div>
                    <div className={classes.columnTwo}>{exit}</div>
                </div>
                <div className={classes.lineOne}>
                    <div className={classes.columnOne}>–¶–µ–Ω–∞/–ö–≥</div>
                    <div className={classes.columnLine}></div>
                    <div className={classes.columnTwo}>{priceKg}</div>
                </div>
            </div>
            <div className={classes.rightBlock}>
                <TextFieldLite label="–ü–æ—Ç–µ—Ä–∏" required variant="outlined"
                               type="number" size="small" value={looses}
                               className={classes.recipeWeight}
                               onChange={handleEditLooses}/> <span
                className={classes.percent}>%</span>
            </div>
        </div>
    );
};

const useStyles = makeStyles((theme) => ({
    makeRecipesFirstBlockRight: {
        flexBasis: '48%',
        display: 'flex',
        justifyContent: 'space-between',
        '@media (max-width:414px)': {
            flexBasis: '43',
        },
    },
    percent: {
        display: 'flex',
        flexBasis: '30%',
        justifyContent: 'center',
        marginTop: '2px',
        fontSize: '25px',
    },
    recipeWeight: {
        flexBasis: '70%',
    },
    leftBlock: {
        flexBasis: '63%',
    },
    rightBlock: {
        display: 'flex',
        flexBasis: '34%',
    },
    recipeField: {},
    lineOne: {
        display: 'flex',
        justifyContent: 'space-between',
        fontWeight: 'bold',
        alignItems: 'center',
        marginBottom: '5px',
    },
    columnLine: {
        height: '2px',
        borderBottom: '1px dotted #000',
        flexBasis: '60%',
    },
    columnTwo: {
        color: global.config.colors.buttons,
    },
}));

export default MakeRecipeFirstBlockRight;
