import React, {useEffect, useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import PlusRoundButton from '../../../components/common/PlusRoundButton';
import IconButton from '@material-ui/core/IconButton';
import {isShowTopPanel} from '../../../actions/effectsActions';
import {useDispatch} from 'react-redux';
import useWindowSize from '../../../hooks/useWindowSize';
import {setCurrentRecipesCategory} from '../../../actions/recipes/recipesListActions';
import axios from 'axios';
import ModalAdd from '../../../components/common/ModalAdd';
import AddCategory from '../../../components/common/AddCategory';
import {
    StyledMenu,
    StyledMenuItem,
} from '../../../components/materialize/StyledCustomMenu';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import DeleteIcon from '@material-ui/icons/Delete';
import ListItemText from '@material-ui/core/ListItemText';
import DialogModal from '../../../components/common/DialogModal';
import TransitionUp from '../../../components/common/TransitionUp';
import ButtonMore from '../../../components/common/Icons/ButtonMore';
import DeleteCategoryList from '../../../components/common/DeleteCategoryList';

const Transition = TransitionUp;

const RecipesLeftBlock = () => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const windowSize = useWindowSize();
    const [open, setOpen] = useState(false);
    const [openDelete, setOpenDelete] = useState(false);
    const [anchorEl, setAnchorEl] = useState(null);
    const [clickedCategory, setClickedCategory] = useState(null);
    let [recipeCategoriesFirst, setRecipeCategoriesFirst] = useState([]);

    const getCategories = () => {
        try {
            //setLoading(true);
            axios.get('/categories',
                {
                    params: {
                        'direction_name': 'product',
                    },
                }).then(data => {
                setRecipeCategoriesFirst(data.data[0].children);
            });
        } catch (e) {
            console.error(e);
        }
    };

    useEffect(() => {
        if (windowSize < 668) {
            dispatch(isShowTopPanel(true));
        }
        getCategories();
    }, [dispatch, windowSize]);

    const setCategory = (el) => {
        dispatch(setCurrentRecipesCategory(el));
    };

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleOpenDelete = () => {
        setOpenDelete(true);
    };

    const handleCloseDelete = () => {
        setOpenDelete(false);
    };

    const handleClickDeletePrompt = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleCloseDeletePrompt = () => {
        setAnchorEl(null);
    };

    const addFunction = (title) => {
        AddCategory({
            title: title,
            parent_id: 1,
            direction_name: 'recipe',
        }, function() {
            getCategories();
            handleCloseDeletePrompt();
        });
    };

    return (
        <div className={classes.RecipesLeftBlock}>
            {recipeCategoriesFirst && recipeCategoriesFirst.map((el, index) =>
                <div className={classes.lineOne} key={el.id}
                     onClick={() => setCategory({title: el.title, id: el.id})}>
                    <div className={classes.columnOne}>{el.title}</div>
                    <div className={classes.columnLine}></div>
                    <div className={classes.columnTwo}>{el.count ?
                        el.count :
                        0}</div>
                    {/*–ø–æ–∫–∞–∑—ã–≤–∞—Ç—å —Ç—Ä–∏ —Ç–æ—á–∫–∏(–≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç—å —É–¥–∞–ª–µ–Ω–∏—è —Ç–æ–ª—å–∫–æ –¥–ª—è –∫–∞—Ç–µ–≥–æ—Ä–∏–π –Ω–∞—á–∏–Ω–∞—è —Å —Ç—Ä–µ—Ç—å–µ–π)*/}
                    {index > 2 ?
                        <ButtonMore onClick={(event) => {
                            setClickedCategory(el.id);
                            return handleClickDeletePrompt(event);
                        }}/> :
                        <div className={classes.emptySpace}></div>}
                </div>,
            )}
            {recipeCategoriesFirst.length < 6 ?
                <IconButton
                    onClick={handleOpen}><PlusRoundButton/></IconButton> :
                ''}
            <ModalAdd open={open} handleClose={handleClose}
                      addFunction={addFunction}
                      windowTitle="–ù–æ–≤—ã–π —Ä–∞–∑–¥–µ–ª"/>
            <StyledMenu
                TransitionComponent={Transition}
                id="customized-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleCloseDeletePrompt}
                onClick={handleCloseDeletePrompt}
            >
                <StyledMenuItem onClick={handleOpenDelete}>
                    <ListItemIcon color="primary">
                        <DeleteIcon fontSize="small"/>
                    </ListItemIcon>
                    <ListItemText primary="–£–¥–∞–ª–∏—Ç—å –∫–∞—Ç–µ–≥–æ—Ä–∏—é"/>
                </StyledMenuItem>
                {/*<StyledMenuItem>
                     <ListItemIcon>
                        <ReceiptSharp fontSize="small"/>
                     </ListItemIcon>
                     <ListItemText primary="–ü–µ—Ä–µ–∏–º–µ–Ω–æ–≤–∞—Ç—å –∫–∞—Ç–µ–≥–æ—Ä–∏—é"/>
                 </StyledMenuItem> */}
            </StyledMenu>

            <DialogModal open={openDelete} Transition={Transition}
                         handleCloseNo={handleCloseDelete}
                         handleCloseYes={() => DeleteCategoryList(clickedCategory,
                             getCategories, handleCloseDelete)}
                         text="–£–¥–∞–ª–∏—Ç—å —ç—Ç—É –∫–∞—Ç–µ–≥–æ—Ä–∏—é?"/>
        </div>
    );
};

const useStyles = makeStyles((theme) => ({
    RecipesLeftBlock: {
        marginBottom: '40px',
        [theme.breakpoints.down('xs')]: {
            height: '100vh',
        },
    },
    lineOne: {
        display: 'flex',
        justifyContent: 'space-between',
        fontWeight: 'bold',
        alignItems: 'center',
        marginBottom: '5px',
        cursor: 'pointer',
    },
    columnOne: {
        width: '30%',
    },
    columnLine: {
        height: '2px',
        borderBottom: '1px dotted #000',
        flexBasis: '50%',
        cursor: 'pointer',
    },
    router_link: {
        textDecoration: 'none',
    },
    emptySpace: {
        width: '23px',
    },

}));

export default RecipesLeftBlock;
