import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import ButtonMore from '../../../components/common/Icons/ButtonMore';
import IconButton from '@material-ui/core/IconButton';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import DeleteIcon from '@material-ui/icons/Delete';
import ReceiptSharp from '@material-ui/icons/ReceiptSharp';
import useWindowSize from '../../../hooks/useWindowSize';
import {StyledMenu} from '../../../components/materialize/StyledCustomMenu';
import {StyledMenuItem} from '../../../components/materialize/StyledCustomMenu';
import DialogModal from '../../../components/common/DialogModal';
import TransitionUp from '../../../components/common/TransitionUp';

const Transition = TransitionUp;

const ProductItem = () => {
    const classes = useStyles();
    const windowSize = useWindowSize();
    const [anchorEl, setAnchorEl] = useState(null);
    const [open, setOpen] = useState(false);

    const handleClickDeletePrompt = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleCloseDeletePrompt = () => {
        setAnchorEl(null);
    };

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    /* –ï—Å–ª–∏ —à–∏—Ä–∏–Ω–∞ —ç–∫—Ä–∞–Ω–∞ –±–æ–ª—å—à–µ - —Ç–æ —Å–≤–∞–π–ø—ã –Ω–µ —Å—Ä–∞–±–æ—Ç–∞—é—Ç */
    if (windowSize > 500) {
    }

    return (
        <div>

            <div className={classes.materialItem}>
                <div className={classes.itemName}>–≠—Å—Ç–µ—Ä—Ö–∞–∑–∏</div>
                <div className={classes.weightPrice}>100—Ä/–∫–≥</div>

                <IconButton onClick={handleClickDeletePrompt}>
                    <ButtonMore/>
                </IconButton>

            </div>

            <StyledMenu
                TransitionComponent={Transition}
                id="customized-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleCloseDeletePrompt}
                onClick={handleCloseDeletePrompt}
            >

                <StyledMenuItem onClick={handleOpen}>
                    <ListItemIcon color="primary">
                        <DeleteIcon fontSize="small"/>
                    </ListItemIcon>
                    <ListItemText primary="–£–¥–∞–ª–∏—Ç—å –Ω–∞—á–∏–Ω–∫—É"/>
                </StyledMenuItem>
                <StyledMenuItem>
                    <ListItemIcon>
                        <ReceiptSharp fontSize="small"/>
                    </ListItemIcon>
                    <ListItemText primary="–ü–µ—Ä–µ–Ω–µ—Å—Ç–∏ –≤ –¥—Ä—É–≥–æ–π —Ä–∞–∑–¥–µ–ª"/>
                </StyledMenuItem>
            </StyledMenu>
            <DialogModal open={open} Transition={Transition}
                         handleCloseNo={handleClose} handleCloseYes={() => {}} text="–£–¥–∞–ª–∏—Ç—å —ç—Ç—É –Ω–∞—á–∏–Ω–∫—É?"/>
        </div>
    );
};

const useStyles = makeStyles((theme) => ({

    materialItem: {
        border: '1px solid #CDA85A',
        display: 'flex',
        justifyContent: 'space-around',
        alignItems: 'center',
        color: '#720D5D',
        fontSize: '2rem',
        height: '45px',
        marginTop: '4px',
        cursor: 'pointer',
        borderRadius: '5px',
        maxWidth: '360px',
        [theme.breakpoints.down('xs')]: {
            position: 'relative',
            left: 0,
            maxWidth: 'initial',
        },
    },
    itemName: {
        fontSize: '1rem',
        color: '#9A157D',
        fontWeight: 'bold',
    },
    weightPrice: {
        fontSize: '0.8rem',
        color: '#746871',
    },

}));

export default ProductItem;
