
import Box from "@material-ui/core/Box";
import { withRouter } from 'react-router-dom'
import Grid from "@material-ui/core/Grid";
import React from "react";
import '../../../css/components/react-infinite-calendar.css';
import {connect} from "react-redux";
import {setEndDate, setStartDate} from "../../../actions/dateActions";
import ProductsRightBlock from "./ProductsRightBlock";
import ProductLeftBlock from "./ProductLeftBlock";
import {makeStyles} from "@material-ui/core/styles";
import LeftBottomBlockSales from "../../Materials/components/LeftBottomBlockSales";

const TabPanelBlocksProducts = ({tabValue}) => {
    const classes = useStyles();

    return (
        <>
            
                <Box className={classes.meterialLeftPanel} value={tabValue} index={0}>
                    <Grid container >
                        <Grid item xs={12} className={classes.recipesLeft}>
                            <ProductLeftBlock />
                            <LeftBottomBlockSales />
                        </Grid>
                        <ProductsRightBlock />
                    </Grid>
                </Box>
            
        </>
    )
}

const useStyles = makeStyles((theme) => ({
    recipesLeft: {
        maxWidth: '420px!important',
        position: 'relative',
        height: 'fit-content',
        padding: '2%'
    },
    meterialLeftPanel: {
        position: 'relative',
        height: '674px'
    }
}))

const mapStateToProps = state => ({
    startDate: state.date.startDate,
    endDate:  state.date.endDate
});

export default  withRouter (connect(
    mapStateToProps,
    {setEndDate,setStartDate}
)(TabPanelBlocksProducts));

