import React from "react";
import {makeStyles} from "@material-ui/core/styles";


const PlusRoundButton = ({addLine}) => {
    const classes = useStyles();
    return (
        <>
            <div className={classes.plusButton} onClick={() => addLine ? addLine() : ''}> +</div>
        </>
    )
}

const useStyles = makeStyles((theme) => ({
    plusButton: {
        border: '1px solid #767676',
        height: '25px',
        width: '25px',
        backgroundColor: '#d9d7d7',
        borderRadius: '50%',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        fontSize: '2rem',
        cursor: 'pointer',
        color: '#767676',
        //marginTop: '20px',
        boxShadow: '0 0 6px rgba(0,0,0,0.5)',
        clear: 'both'
    },
}))

export default PlusRoundButton;