import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import SearchField
  from '../../../components/blocks/orders/rightPanel/SearchField';
import TotalProductsRightBlock from './TotalProductsRightBlock';

const SummaryRightPanelSearchLine = () => {
  const classes = useStyles();
  return (
      <div className={classes.summaryRightPanelSearchLine}>
        <TotalProductsRightBlock/>
        <div className={classes.searchLine}>
          <SearchField getDataUrl="products" label="–ü–æ–∏—Å–∫ –≤ –Ω–∞—á–∏–Ω–∫–∞—Ö"
                       noOptionsText="–Ω–µ—Ç —Ç–∞–∫–∏—Ö –Ω–∞—á–∏–Ω–æ–∫"/>
        </div>
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  searchLine: {
    flexBasis: '50%',
    [theme.breakpoints.down('xs')]: {
      flexBasis: '99%',
    },
  },
  summaryRightPanelSearchLine: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    borderBottom: '1px solid #d0cccc',
    [theme.breakpoints.down('xs')]: {
      flexWrap: 'wrap',
    },
  },
}));

export default SummaryRightPanelSearchLine;
