import {
    SET_CURRENT_RECIPES_CATEGORY,
    SET_LOADING_RECIPES,
} from '../../actions/types/recipes/RecipesListTypes';

const initialState = {
    currentRecipesCategory: {
        title: '–ö–æ—Ä–∂–∏',
        id: 4,
        parent_id: 1,
    },
    loadingRecipes: false, //—Å–ø–∏–Ω–Ω–µ—Ä
};

const recipesListReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_CURRENT_RECIPES_CATEGORY:
            return {
                ...state,
                currentRecipesCategory: action.payload,
            };
        case SET_LOADING_RECIPES:
            return {
                ...state,
                loadingRecipes: action.payload,
            };
        default:
            return state;
    }
};

export default recipesListReducer;