import {makeStyles} from "@material-ui/core/styles";

const drawerWidth = 240;
const useStylesDashboard = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    rootLoading: {
        background: '#dfdfdf',
        height: '100vh',
        width: '100vw',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
    },
    adTopBlock: {
      flex:1,
      border: '1px solid red'  ,
    },
    calendarHeaderMenu: {
        minWidth: '420px',
    },
    greyLineMobOrders: {
        display: 'none',
        [theme.breakpoints.down('xs')]: {
            display: 'block',
            borderBottom: '1px solid #575656',
            position: 'relative',
            top: '-5px',
        },
    },
    greyLineMobOrdersLight: {
        display: 'none',
        [theme.breakpoints.down('xs')]: {
            display: 'block',
            borderBottom: '1px solid #dedede',
            position: 'relative',
            top: '-5px',
        },
    },
    toolbarPurple: {
        paddingRight: 4, // keep right padding when drawer closed
        paddingLeft: 4,
        [theme.breakpoints.down('xs')]: {
            backgroundColor: global.config.colors.purple,
        },
    },
    toolbarPurpleTab: {
        paddingRight: 4, // keep right padding when drawer closed
        paddingLeft: 4,
        [theme.breakpoints.down('xs')]: {
            backgroundColor: global.config.colors.purple,
            position: 'fixed',
            top: '57px',
            zIndex: '2',
            width: '100%'
        },
    },
    toolbarWhite: {
        paddingRight: 4, // keep right padding when drawer closed
        paddingLeft: 4,
    },
    calendarTopMobile: {
        display: 'none',
        [theme.breakpoints.down('xs')]: {
            display: 'flex',
            flexShrink: 0,
            alignItems: 'center'
        },
    },
    mobScreenNameHeader: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
    },
    mobScreenNameHeaderButton : {
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'center',
        border: '1px solid #5D1049',
        borderRadius: '5px',
        padding: '7px'
    },
    calendarMobIcon: {
        marginRight: '12px',
    },
    calendarMobIconBut: {
        width: '22px',
        marginRight: '5px'
    },
    calendarMobIconText: {
        color: 'red'
    },
    drawerOpen: {
        height: '100vh',
        [theme.breakpoints.up('lg')]: {
            width: drawerWidth,
            flexShrink: 0,
        },
    },
    drawerClose: {
        height: '100vh',
    },
    calendarHeader: {
        marginLeft: '9%',
        marginBottom: '0',
        display: 'flex',
        justifyContent: 'space-between',
        paddingRight: '10%'
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
        width: 200,
    },
    calendarIcon: {
        cursor: 'pointer'
    },
    signin_logo: {
        marginRight: '3%',
        cursor: 'pointer'
    },
    toolbarIcon: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        padding: '0 8px',
        ...theme.mixins.toolbar,
    },
    appBarDrawerOpen: {
        zIndex: theme.zIndex.drawer + 1,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        [theme.breakpoints.up('lg')]: {
            marginLeft: drawerWidth,
            width: `calc(100% - ${drawerWidth}px)`,
        },
        backgroundColor: "#fff",
        boxShadow: "none",
        left: "0",
        [theme.breakpoints.down('xs')]: {
            borderBottom: '1px solid #000',
        }
    },
    appBarDrawerClose: {
        zIndex: theme.zIndex.drawer + 1,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        backgroundColor: "#fff",
        boxShadow: "none",
        left: "0",
        [theme.breakpoints.down('xs')]: {
            borderBottom: '1px solid #000',
        }
    },
    appBarTabs: {
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        backgroundColor:'#FAFAFA',
        boxShadow: "none",
        left: "0",
        maxWidth: "420px",
    },
    appBarShift: {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButtonDrawerOpen: {
        marginRight: 36,
        [theme.breakpoints.up('lg')]: {
            display: 'none',
        },
    },
    menuButtonDrawerClose: {
        marginRight: 36,
    },
    menuButtonHidden: {
        display: 'none',
    },
    title: {
        flexGrow: 1,
        color: 'purple',
        display: 'flex',
        [theme.breakpoints.down('xs')]: {
            justifyContent: 'space-around',
            alignItems: 'center'
        },
    },
    titleLeftSide: {
        [theme.breakpoints.down('xs')]: {
            flexBasis: '70%'
        },
    },
    drawerPaper: {
        position: 'relative',
        whiteSpace: 'nowrap',
        width: drawerWidth,
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    drawer_logo: {
        width: '114px',
        height: '91px',
        display: 'block',
        margin: '0 auto',
        marginTop: '10px'
    },
    drawerPaperClose: {
        overflowX: 'hidden',
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        width: theme.spacing(7),
        [theme.breakpoints.up('sm')]: {
            width: theme.spacing(9),
        },
    },
    calendarBlock: {
        backgroundColor: '#800080',
        color: '#fff',
        maxWidth: '420px!important',
        position: 'relative',
        height: 'fit-content'
    },
    calendarTabPanel: {
        [theme.breakpoints.down('sm')]: {
            position: 'fixed',
            top: '106px',
            width: '100%'
        }
    },
    calendarDateChoice: {
        backgroundColor: "#e6cee6",
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
        maxWidth: '420px'
    },
    calendarIconSVG: {
        fill: '#eba40a'
    },
    appBarSpacer: theme.mixins.toolbar,
    content: {
        flexGrow: 1,
        height: '100vh',
        overflow: 'auto',
        [theme.breakpoints.down('xs')]: {
            overflow: 'hidden',
        }
    },
    container: {
        paddingTop: theme.spacing(4),
        paddingBottom: theme.spacing(4),
    },
    paper: {
        padding: theme.spacing(2),
        display: 'flex',
        overflow: 'auto',
        flexDirection: 'column',
    },
    fixedHeight: {
        height: 240,
    },
}));

export default useStylesDashboard;