import React, {useEffect} from "react";
import Box from "@material-ui/core/Box";
import Grid from "@material-ui/core/Grid";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Hidden from "@material-ui/core/Hidden";
import TabPanelBlocksProducts from "./components/TabPanelBlocksProducts";
import Copyright from "../../components/materialize/Copyright";
import useStylesDashboard from "../../css/useStyle/Dashboard.css";
import {isBigScreenDrawerOpen} from "../../actions/effectsActions";
import {useDispatch} from "react-redux";

const Product = () => {
    const classes = useStylesDashboard();
    const [tabValue, setTabValue] = React.useState(0);
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(isBigScreenDrawerOpen(true));
    },[dispatch])

    return (
        <main className={classes.content}>
            <div className={classes.appBarSpacer}/>
            <Box>
                <Grid container>
                    <Grid item xl={4} lg={4} md={4} xs={12} className={classes.calendarHeaderMenu}>
                        <AppBar position="static" className={classes.appBarTabs}>
                            <Toolbar className={classes.toolbar}>

                                
                            </Toolbar>
                        </AppBar>
                        <div className={classes.greyLineMobOrders}/>
                    </Grid>
                    <Hidden mdDown>
                        <Grid className={classes.adTopBlock}>
                            –ú–µ—Å—Ç–æ –ø–æ–¥ –±–∞–Ω–Ω–µ—Ä
                        </Grid>
                    </Hidden>
                </Grid>
            </Box>
            <TabPanelBlocksProducts tabValue={tabValue}/>
            <Box pt={4}>
                <Copyright/>
            </Box>
        </main>
    )
}

export default Product;
