import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import {useDispatch, useSelector} from 'react-redux';
import {
  getSavedIngredients,
  deleteAllNewIngredients,
  setSaveButtonStatus, saveNewIngredients, saveOldIngredients,
} from '../../../actions/ingredients/ingredientActions';
import Snackbar from '@material-ui/core/Snackbar';
import Alert from '../../../components/common/Alert';
import Button from '@material-ui/core/Button';

const TotalMaterialsRightBlock = () => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const currentIngredients = useSelector(
        state => state.ingredients.ingredientsList);
    const [open, setOpen] = useState(false);
    const [openError, setOpenError] = useState(false);
    const currentIngredientsCategory = useSelector(
        state => state.ingredients.ingredientsList.currentIngredientsCategory);
    const activeSaveButton = useSelector(
        state => state.ingredients.ingredientsList.activeSaveButton);

    const saveIngredientsHandler = (currentIngredients) => {

        const ingredientsNewToSave = currentIngredients.newIngredients.filter(el => {
            return el.title.trim() !== '';
        }).map((el) => ({
            category_id: el.category_id,
            id: el.id,
            title: el.title,
            weight: el.weight[0]? parseFloat(el.weight[0]) : 0,
            price: el.price[0]? parseFloat(el.price[0]): 0,
        }));

        const objectIngredientsNewToSave = {
            ingredient_amounts: ingredientsNewToSave,
        };

        dispatch(saveNewIngredients(objectIngredientsNewToSave, handleOpen, handleOpenError ));
        if(currentIngredients.savedEditedIngredients.length > 0) {

          const ingredientsOldToSave = currentIngredients.savedEditedIngredients.map(el => {
            const weight = Array.isArray(el.weight) ? el.weight[0]? parseFloat(el.weight[0]): 0 : el.weight;
            const price = Array.isArray(el.price) ? el.price[0]? parseFloat(el.price[0]): 0 : el.price;

            return {
              id: el.id,
              price: price,
              weight:  weight,
              //price_per_gram: el.price_per_gram,
              //title: el.title, // title –Ω–µ –º–µ–Ω—è–µ—Ç—Å—è, –µ–≥–æ –Ω–µ –ø–æ—Å—ã–ª–∞–µ–º
              //updated_at: el.updated_at
            }
          })

          const objectIngredientsOldToSave = {
            ingredient_amounts: ingredientsOldToSave,
          };
          dispatch(saveOldIngredients(objectIngredientsOldToSave, handleOpen, handleOpenError ));

        }
        dispatch(setSaveButtonStatus(false))
    };

    // –µ—Å–ª–∏ —É—Å–ø–µ—à–Ω–æ –≤—ã–ø–æ–ª–Ω—è–µ—Ç—Å—è
    const handleOpen = () => {
        setOpen(true);
        //–µ—Å–ª–∏ –≤—Å—ë —É—Å–ø–µ—à–Ω–æ –ø–µ—Ä–µ–∑–∞–ø—Ä–æ—Å–∏–º —Å–æ—Ö—Ä–∞–Ω—ë–Ω–Ω—ã–µ –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç—ã
        dispatch(getSavedIngredients(currentIngredientsCategory.id));
        //–≤—Å–µ –Ω–æ–≤—ã–µ –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç—ã –æ—á–∏—â–∞–µ–º
        dispatch(deleteAllNewIngredients());
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleOpenError = () => {
      setOpenError(true);
    };

    const handleCloseError = () => {
      setOpenError(false);
    };


    return (
        <div className={classes.totalOrdersInfo}>
            <span className={classes.totalNumbers}>
                    <span className={classes.materialType}>{  currentIngredientsCategory && currentIngredientsCategory.title}</span>
                    <Button  size='medium' variant='contained'
                             disabled={activeSaveButton ? false : true}
                             className={classes.materialTypeButton }
                          onClick={() =>  activeSaveButton ? saveIngredientsHandler(currentIngredients) : ''}>–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</Button>
            </span>

          <div className={classes.creationAlert}>
            <Snackbar open={open} autoHideDuration={2000} onClose={handleClose} className={classes.creationAlertSnackbar}>
              <Alert  onClose={handleClose} severity="success">
                –£—Å–ø–µ—à–Ω–æ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–æ
              </Alert>
            </Snackbar>
            <Snackbar open={openError} autoHideDuration={2000} onClose={handleCloseError} className={classes.creationAlertSnackbar}>
              <Alert onClose={handleCloseError} severity="error">
                –ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫
              </Alert>
            </Snackbar>
          </div>
        </div>
    );
};

const useStyles = makeStyles((theme) => ({
    totalOrdersInfo: {
        display: 'flex',
        flexBasis: '50%',
        justifyContent: 'space-around',
        [theme.breakpoints.down('sm')]: {
            flexBasis: '86%',
            width: '100%',
        },
    },
    recipesNumber: {
        color: '#9A157D',
        fontSize: '1.3rem',
    },
    switch: {
        display: 'none',
        [theme.breakpoints.down('sm')]: {
            display: 'block',
        },
    },
    creationAlert: {
      [theme.breakpoints.down('xs')]: {
        position: 'absolute',
        bottom: '-25vh'
      },
    },
    creationAlertSnackbar: {
      [theme.breakpoints.down('xs')]: {
        position: 'relative',
      },
    },
    recipes: {
        fontWeight: '700',
        [theme.breakpoints.down('sm')]: {
            display: 'flex',
            flexWrap: 'wrap',
            alignItems: 'center',
            flexDirection: 'column',
            justifyContent: 'center',
        },
    },
    materialType: {
        fontWeight: 'bold',
        fontSize: '1.1rem',
        [theme.breakpoints.down('sm')]: {
            width: '100px',
            display: 'inline-block',
            textAlign: 'center',
            fontSize: '0.9rem',
        },
    },
    materialTypeButton: {
        background: global.config.colors.purple,
        color: '#fff',
        borderRadius: '13px',
        justifyContent: 'center',
        alignItems: 'center',
        fontWeight: '300',
        padding: '1px 10px',
        cursor: 'pointer',
        '&:hover': {
          background: global.config.colors.purple,
          color:global.config.colors.gold,
        },
        [theme.breakpoints.down('xs')]: {
            display: 'flex',
        },
    },
    totalNumbers: {
        fontSize: '0.8rem',
        width: '100%',
        display: 'flex',
        justifyContent: 'space-evenly',
        alignItems: 'center',
        [theme.breakpoints.down('sm')]: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
            marginTop: '5px',
            borderRadius: '10px',
            padding: '0 5%',
        },
    },
    materialWeight: {
        fontSize: '1.3rem',
        color: '#747474',
        fontWeight: '700',
    },
}));

export default TotalMaterialsRightBlock;
