import { createMuiTheme } from "@material-ui/core/styles";
import RalewayWoff2 from '../../css/fonts/raleway/Raleway-Regular.woff';

const raleway = {
    fontFamily: 'Raleway',
    fontStyle: 'normal',
    fontDisplay: 'swap',
    fontWeight: 400,
    src: `
    local('Raleway'),
    local('Raleway-Regular'),
    url(${RalewayWoff2}) format('woff')
  `
};

const theme = createMuiTheme({
    typography: {
        fontFamily: 'Raleway',
    },
    overrides: {
        MuiCssBaseline: {
            '@global': {
                '@font-face': [raleway],
            },
        },
    },
    breakpoints: {
        values: {
            xs: 0,
            tt: 414,
            sm: 600,
            md: 960,
            lg: 1280,
            lgs: 1300,
            xl: 1920,
        },
    },

});

export default theme;