import { combineReducers } from 'redux';
import productReducer from './productReducer';
import dateReducer from "./dateReducer";
import authReducer from "./authReducer";
import screenReducer from "./screenReducer";
import effectsReducer from "./effectsReducer";
import makeRecipeReducer from './recipes/makeRecipeReducer';
import makeProductReducer from './Products/makeProductReducer';
import ingredientsReducer from './ingredients/ingredientsReducer';
import recipesListReducer from './recipes/recipesListReducer';
import productListReducer from './Products/ProductListReducer';
import materialStoreReducer from './ingredients/materialStoreReducer';
import makeClientsReducer from './clients/makeClientReducer';

export default combineReducers({
    products: productReducer,
    date: dateReducer,
    auth: authReducer,
    screen: screenReducer,
    effects: effectsReducer,
    recipes: combineReducers({
        makeRecipe: makeRecipeReducer,
        recipesList: recipesListReducer
    }),
    product: combineReducers({
        makeProduct: makeProductReducer,
        productList: productListReducer,
    }),
    ingredients: combineReducers({
        ingredientsList: ingredientsReducer,
        materialStoreList: materialStoreReducer
    }),
    clients: combineReducers({
      makeClient: makeClientsReducer,
    })
});
