import {makeStyles} from '@material-ui/core/styles';
import React from 'react';
import {withRouter} from 'react-router-dom';
import {connect} from "react-redux";
import {setEndDate, setStartDate, setIsTodayButton} from "../../actions/dateActions";
import PropTypes from "prop-types";


const TodayButton = ({setStartDate, setEndDate, setIsTodayButton, children}) => {
    const classes = useStyles();

    //–ù–∞–∑–∞–¥ –≤–æ–∑–≤—Ä–∞—â–∞–µ–º —ç—Ç–æ —Å–æ—Å—Ç–æ—è–Ω–∏–µ - —ç—Ç–æ —Å–≤—è–∑–∞–Ω–æ —Å –∑–∞–º–æ—Ä–æ—á–∫–∞–º–∏ —Å –ø—Ä—ã–≥–∞–Ω–∏–µ–º –¥–∞—Ç—ã –≤ –∫–∞–ª–µ–Ω–¥–∞—Ä–µ (–æ–Ω —Ü–µ–Ω—Ç—Ä–∏—Ä—É–µ—Ç –≤—ã–±—Ä–∞–Ω–Ω—É—é –¥–∞—Ç—É –∞–≤—Ç–æ–º–∞—Ç–æ–º–±
    // –∞ –Ω–∞–º —ç—Ç–æ–≥–æ –Ω–µ –Ω–∞–¥–æ)
    const setIsTodayButtonBack = () => {
        setTimeout(() => {
            setIsTodayButton(false);
        }, 500)
    };

    const returnStartDate = () => {
        setStartDate(new Date());
        setEndDate(new Date());
        setIsTodayButton(true);
        setIsTodayButtonBack();
    }

    return (
        <div variant="outlined" color="primary" onClick={returnStartDate} className={classes.button}>
            {children}
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    button: {
        marginLeft: '10px',
        fontSize: '0.875rem',
        minWidth: '64px',
        boxSizing: 'border-box',
        fontFamily: 'Raleway',
        fontWeight: '500',
        lineHeight: '1.75',
        borderRadius: '4px',
        textTransform: 'uppercase',
        padding: '5px 15px',
        color: 'blue',
        borderColor: 'blue',
        border: '1px solid grey',
        display: 'inline-flex',
        [theme.breakpoints.down('xs')]: {
            color: '#fff',
            borderColor: '#fff'
        }
    }
}));


TodayButton.propTypes = {
    children: PropTypes.string.isRequired,
    setEndDate: PropTypes.any.isRequired,
    setStartDate: PropTypes.any.isRequired,
};

export default withRouter(connect(
    null,
    {setStartDate, setEndDate, setIsTodayButton}
)(TodayButton));
