import React, {useEffect, useState} from 'react';
import Box from "@material-ui/core/Box";
import Grid from "@material-ui/core/Grid";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import {CustomTabIngredients, CustomTabsIngredients} from "../Materials/components/CustomTabIngredients";
import tabSetProps from "../../components/materialize/smallElements/TabSetProps";
import Hidden from "@material-ui/core/Hidden";
import TabPanelBlocksIngredients from "./components/TabPanelBlocksRecipes";
import Copyright from "../../components/materialize/Copyright";
import useStylesDashboard from "../../css/useStyle/Dashboard.css";
import {isBigScreenDrawerOpen} from "../../actions/effectsActions";
import {useDispatch} from "react-redux";
import axios from 'axios';
import {nanoid} from 'nanoid';

const Recipes = () => {
    const classes = useStylesDashboard();
    const [tabValue, setTabValue] = React.useState(0);
    const dispatch = useDispatch();
    const tabHandleChange = (event, newValue) => {
        setTabValue(newValue);
    };
    let [recipeCategories, setRecipeCategories] = useState([]);

    useEffect(() => {
        dispatch(isBigScreenDrawerOpen(true));

        try {
            //setLoading(true);
            axios.get('/categories',
                {params: {'direction_name':'product'
                    }}).then(data => {
                setRecipeCategories(data.data);
            });
        } catch (e) {
            console.error(e);
        }
    },[dispatch])

    return (
        <main className={classes.content}>
            <div className={classes.appBarSpacer}/>
            <Box>
                <Grid container>
                    <Grid item xl={4} lg={4} md={4} xs={12} className={classes.calendarHeaderMenu}>
                        <AppBar position="static" className={classes.appBarTabs}>
                            <Toolbar className={classes.toolbar}>

                                <CustomTabsIngredients
                                    value={tabValue}
                                    onChange={tabHandleChange}
                                    indicatorColor="primary"
                                    textColor="primary"
                                    variant="scrollable"
                                    scrollButtons="auto"
                                    aria-label="scrollable auto tabs example"
                                >
                                    {recipeCategories.map((el, ind) => <CustomTabIngredients label={el.title} key={nanoid(3)} {...tabSetProps({ind})} />)}
                                </CustomTabsIngredients>
                            </Toolbar>
                        </AppBar>
                        <div className={classes.greyLineMobOrders}></div>
                    </Grid>
                    <Hidden mdDown>
                        <Grid className={classes.adTopBlock}>
                            –ú–µ—Å—Ç–æ –ø–æ–¥ –±–∞–Ω–Ω–µ—Ä
                        </Grid>
                    </Hidden>
                </Grid>
            </Box>
            <TabPanelBlocksIngredients tabValue={tabValue}/>
            <Box pt={4}>
                <Copyright/>
            </Box>
        </main>
    )
}

export default Recipes;