import { IS_BIG_SCREEN_DRAWER_OPEN, IS_INFO_SAVE,IS_SHOW_TOP_PANEL } from "./types/EffectsTypes";


export const isBigScreenDrawerOpen = (data) => async dispatch => {
    dispatch({
        type: IS_BIG_SCREEN_DRAWER_OPEN,
        payload: data
    });
}

export const isInfoSave = (data) => async dispatch => {
    dispatch({
        type: IS_INFO_SAVE,
        payload: data
    });
}

export const isShowTopPanel = (data) => async dispatch => {
    dispatch({
        type: IS_SHOW_TOP_PANEL,
        payload: data
    });
}