import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import SearchField from "../../../components/blocks/orders/rightPanel/SearchField";
import TotalMaterialsRightBlock from "./TotalMaterialsRightBlock";

const SummaryRightPanelSearchLine = () => {
    const classes = useStyles();
    return (
        <div className={classes.summaryRightPanelSearchLine}>
            <TotalMaterialsRightBlock/>
            <div className={classes.searchLine}>
              <SearchField getDataUrl="ingredient_amounts" label="–ü–æ–∏—Å–∫ –≤ –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç–∞—Ö" noOptionsText="–Ω–µ—Ç —Ç–∞–∫–∏—Ö –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç–æ–≤"/>
            </div>
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    searchLine: {
        flexBasis: '50%',
        [theme.breakpoints.down('xs')]: {
            flexBasis: '95%',
            width: '95%'
        },
    },
    summaryRightPanelSearchLine: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        borderBottom: '1px solid #d0cccc',
        [theme.breakpoints.down('xs')]: {
            flexDirection: 'column'
        },
    }
}))

export default SummaryRightPanelSearchLine;
