import React, {useEffect, useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import TextFieldLite from '../../../../components/common/TextFieldLite';
import FormDeleteIcon from '../../../../components/common/Icons/FormDeleteIcon';
import {editNewMaterialStore, editOldMaterialStore} from '../../../../actions/ingredients/materialStore/materialStoreActions';
import useComponentVisible from '../../../../hooks/useComponentVisible';
import {ajax} from 'rxjs/ajax';
import {fromEvent, of} from 'rxjs';
import {
  catchError,
  debounceTime,
  distinctUntilChanged,
  pluck,
  switchMap,
} from 'rxjs/operators';
import {useDispatch} from 'react-redux';

const MaterialStoreItem = ({elem, deleteLine, savedMaterialStore, MaterialStoreType = 'new'}) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  let [materialHints, setMaterialHints] = useState([]);
  const {ref, isComponentVisible, setIsComponentVisible} = useComponentVisible(
      true);

  useEffect(() => {
    const click = fromEvent(document.getElementById('MaterialStoreName'), 'input')
      .pipe(
            debounceTime(500),
            pluck('target', 'value'),
            distinctUntilChanged(),
            switchMap(searchWord => {
              return ajax.getJSON(
                  `${global.config.settings.apiBaseUrl}/materials?search_by_name=${searchWord.capitalize()}`,
                  {Authorization: 'Bearer ' + localStorage.getItem('token')}
              )
              .pipe(
                  catchError( (error, caught) => of(`Error : ${error}`))
              )
            }),
        )
      .subscribe(resp => {
              if (resp) {
                setMaterialHints(resp);
                setIsComponentVisible(true);
              }
            },
        );
    return () => click.unsubscribe();
  }, [setIsComponentVisible]);

  const changeMaterialStoreName = (event) => {
    if (event.target.value.length === 0) {
      setMaterialHints([]);
      setIsComponentVisible(false);
    }
    dispatch(editNewMaterialStore({
      ...elem,
      title: event.target.value,
    }));
  };

  const chooseMaterialStoreTitle = (event) => {
    if (MaterialStoreType === 'new') {
      dispatch(editNewMaterialStore({
        ...elem,
        id: parseInt(event.target.getAttribute('data-id')),
        title: event.target.innerText,
      }));
      setMaterialHints([]);
    }
  };


  const changeMaterialStoreCount = (event) => {
    if (MaterialStoreType === 'new') {
      dispatch(editNewMaterialStore({
        ...elem,
        count: event.target.value ?
            event.target.value.toString().match(/[0-9.]+/gi) :
            '',
      }));
    }

    if (MaterialStoreType === 'old') {
      dispatch(editOldMaterialStore({
        ...elem,
        count: event.target.value ?
            event.target.value.toString().match(/[0-9.]+/gi) :
            '',
      }));
    }
  };

  const changeMaterialStorePrice = (event) => {
    if (MaterialStoreType === 'new') {
      dispatch(editNewMaterialStore({
        ...elem,
        price: event.target.value ?
            event.target.value.toString().match(/[0-9.]+/gi) :
            '',
      }));
    }

    if (MaterialStoreType === 'old') {
      dispatch(editOldMaterialStore({
        ...elem,
        price: event.target.value ?
            event.target.value.toString().match(/[0-9.]+/gi) :
            '',
      }));
    }
  };

  return (
      <div className={classes.materialItem}>
        <div className={classes.materialNameFieldWrapper}>
          <TextFieldLite label="–ú–∞—Ç–µ—Ä–∏–∞–ª" variant="outlined"
                         disabled={MaterialStoreType === 'old'}
                         id="MaterialStoreName"
                         value={elem.title}
                         onChange={changeMaterialStoreName}
                         className={classes.ingredient}/>
          {materialHints.length > 0 && isComponentVisible ? (
              <div className={classes.materialHints} ref={ref}>
                <ul>
                  {materialHints.map(
                      (el) => <li className={classes.materialHintsLi}
                                  onClick={chooseMaterialStoreTitle}
                                  data-id={el.id}
                                  key={el.id}>{el.title}</li>)}
                </ul>
              </div>
          ) : (<div></div>)}
        </div>
        <TextFieldLite label="–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ" variant="outlined"
                       value={elem.count} onChange={changeMaterialStoreCount}
                       className={classes.count}/>
        <TextFieldLite label="–¶–µ–Ω–∞/—à—Ç" variant="outlined"
                       value={elem.price}
                       onChange={changeMaterialStorePrice}
                       className={classes.price}/>

        <FormDeleteIcon onClick={() => deleteLine(elem.id, savedMaterialStore,
            elem.nanoId)}/>
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  materialItem: {
    marginTop: '10px',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  materialNameFieldWrapper: {
    position: 'relative',
  },
  materialHints: {
    position: 'absolute',
    top: '58px',
    background: '#fff',
    zIndex: '2',
    width: '100%',
    border: '1px solid #c2c0c0',
    borderTop: 'none',
  },
  materialHintsLi: {
    cursor: 'pointer',
  },
  date: {
    [theme.breakpoints.down('xs')]: {
      display: 'none',
    },
  },
  ingredient: {
    flexBasis: '40%',
    [theme.breakpoints.down('xs')]: {
      flexBasis: '46%',
    },
  },
  count: {
    flexBasis: '20%',
    [theme.breakpoints.down('xs')]: {
      flexBasis: '16%',
    },
  },
  price: {
    flexBasis: '20%',
    [theme.breakpoints.down('xs')]: {
      flexBasis: '22%',
    },
  },
  trash: {
    cursor: 'pointer',
  },

}));

export default MaterialStoreItem;
