import ArrowBack from '@material-ui/icons/ArrowBack.js';
import {withStyles} from "@material-ui/core/styles";



 const GoldArrowBack = withStyles ((theme) =>({
    root: {
        color: '#fff',
        '&:hover': {
            color:global.config.colors.gold,
          },
    },
   
}))(ArrowBack);

export default GoldArrowBack;