import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import PurpleCheckbox from '../../../components/common/PurpleCheckbox';
import TextFieldLite from '../../../components/common/TextFieldLite';
import Slider from '@material-ui/core/Slider';
import truck from '../../../images/truck.svg';
import whatsapp from '../../../images/whatsapp_small.svg';
import phone from '../../../images/phone_small.svg';
import DownArrow from '../../../components/common/DownArrow';
import {useSelector} from 'react-redux';
import InputAdornment from '@material-ui/core/InputAdornment';
import Input from '@material-ui/core/Input';
import Link from '@material-ui/core/Link';

const MakeOrderClientCreated = ({
    mobileDetailed,
    isOpen,
    isOpenClientDetailed,
}) => {
    const classes = useStyles({mobileDetailed})();
    const [confirmClient, setConfirmClient] = useState(false);
    const client = useSelector(
        state => state.clients.makeClient);
    
    const whatsApp = useSelector(
        state => state.clients.makeClient.showWhatsApp);
    const deliver = useSelector(
        state => state.clients.makeClient.showDeliver);
    
    const [value, setValue] = useState('');
    
    const handleSliderChange = (event, newValue) => {
        setValue(newValue);
    };
    const reroute = () => window.open(client.socialNetworks);
    const handleInputChange = (event) => {
        setValue(event.target.value === '' ? '' : Number(event.target.value));
    };
    
    const handleBlur = () => {
        if (value < 0) {
            setValue(0);
        }
        else {
            if (value > 100) {
                setValue(100);
            }
        }
    };
    
    /*  const changeReminder = (event, newValue) => {
     setReminder(newValue);
     };*/
    
    return (
        <div className={classes.makeClientCreatedBlock}>
            <div className={classes.lineOne}>
                {!mobileDetailed && <h3 className={classes.clientNameLine}>
                    {client.name}
                </h3>}
                <div className={classes.confirmedBlock}>
                    –ü–æ–¥—Ç–≤–µ—Ä–∂–¥—ë–Ω
                    <PurpleCheckbox
                        checked={confirmClient}
                        onChange={() => setConfirmClient(!confirmClient)}
                        name="confirmClient"
                    />
                </div>
            </div>
            <div className={classes.lineTwo}>
                <div className={classes.lineTwoFirstColumn}>
                    <div className={classes.lineTwoFirstColumnDiv}>
                        <img src={whatsapp} className={whatsApp ?
                            classes.whatsappIcon :
                            classes.whatsappIconDisabled} alt="whatsapp"/>
                        <img src={phone} className={classes.phoneIcon}
                             alt="—Ç–µ–ª–µ—Ñ–æ–Ω"/>
                        <span>
               {client.phone}
              </span>
                    </div>
                    <div className={classes.lineTwoFirstColumnDivSecond}>
                        <Link href="#" onClick={reroute} color="secondary">
                            –ü–µ—Ä–µ–ø–∏—Å–∫–∞
                        </Link>
                    </div>
                </div>
                <div className={classes.lineTwoSecondColumn}>
                    <div className={classes.lineTwoSecondColumnDiv}>
                        {client.date && JSON.parse(client.date).human}
                    </div>
                    <div className={classes.lineTwoSecondColumnDivSecond}>
                        {client.time && JSON.parse(client.time).human}
                    </div>
                </div>
                <div className={classes.lineTwoThirdColumn}>
                    <TextFieldLite disabled={!confirmClient}
                                   label="–ü–û"
                                   required size="small"
                                   variant="outlined"
                                   type="number"/>
                </div>
            </div>
            <div className={classes.lineThree}>
                <div className={classes.lineThreeFirstColumn}>
                    <div className={classes.reminderLabel}>
                        <div>–ù–∞–ø–æ–º–Ω–∏—Ç—å</div>
                    </div>
                    <Slider color="secondary"
                            className={classes.slider}
                            max={30}
                            min={0}
                            step={3}
                            value={typeof value === 'number' ? value : 0}
                            onChange={handleSliderChange}
                            aria-labelledby="input-slider"
                    />
                </div>
                <div className={classes.lineThreeSecondColumn}>
                    <Input
                        color="secondary"
                        endAdornment={<InputAdornment
                            position="end">–î–Ω.</InputAdornment>}
                        value={value}
                        margin="dense"
                        onChange={handleInputChange}
                        onBlur={handleBlur}
                        inputProps={{
                            step: 3,
                            min: 0,
                            max: 30,
                            type: 'number',
                            'aria-labelledby': 'input-slider',
                        }}
                    />
                </div>
            </div>
            <div className={classes.lineFour}>
                <div className={classes.truckImgWrapper}>
                    <img src={truck} className={deliver ?
                        classes.truckIcon :
                        classes.truckIconDisabled} alt="–¥–æ—Å—Ç–∞–≤–∫–∞"/>
                </div>
                <div className={classes.addressWrapper}>
                    <span>{client.address}</span>
                </div>
                
                
                <div className={classes.arrowBlock}>
                    <DownArrow isOpen={isOpen} initialState={true}/>
                </div>
                {!mobileDetailed &&
                <div className={classes.arrowBlockShowClientDetail}>
                    <DownArrow isOpen={isOpenClientDetailed}
                               initialState={false}/>
                </div>
                
                }
            </div>
        </div>
    );
};

const useStyles = props => makeStyles(() => {
    const styleObject = {
        makeClientCreatedBlock: {
            border: '1px solid #a7a0a0',
            boxShadow: '0px 10px 15px -5px rgba(0,0,0,0.5)',
            padding: '3%',
            paddingTop: '0',
            flexWrap: 'wrap',
            maxWidth: '380px',
            height: 'fit-content',
            marginRight: '20px',
            marginLeft: '15px',
            '@media (max-width:1300px)': {
                flexBasis: '100%',
                maxWidth: 'initial',
                marginBottom: '20px',
            },
            '@media (max-width:668px)': {
                position: 'relative',
            },
        },
        lineOne: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
        },
        Checkbox: {
            marginRight: '20px',
        },
        lineTwo: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
        },
        lineThree: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
            marginTop: '10px',
            marginLeft: '5px',
        },
        lineFour: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
            marginTop: '10px',
        },
        lineFourFirstColumn: {
            paddingRight: '25px',
        },
        lineTwoFirstColumn: {
            flexBasis: '40%',
            color: '#9A157D',
            '@media (max-width:668px)': {
                flexBasis: '60%',
            },
        },
        lineTwoSecondColumn: {
            flexBasis: '30%',
            color: '#FA1919',
            fontWeight: 'bold',
        },
        truckIconDisabled: {
            width: '20px',
            display: 'none',
        },
        lineTwoThirdColumn: {
            flexBasis: '25%',
        },
        lineThreeFirstColumn: {
            flexBasis: '90%',
        },
        truckIcon: {
            width: '20px',
        },
        whatsappIconDisabled: {
            paddingRight: '2px',
            display: 'none',
        },
        lineThreeSecondColumn: {
            flexBasis: '45%',
            marginLeft: '80px',
        },
        whatsappIcon: {
            paddingRight: '2px',
            width: '15px',
        },
        lineTwoSecondColumnDivSecond: {
            marginBottom: '20px',
            color: '#9A157D',
            flexBasis: '65%',
        },
        instagramIcon: {
            paddingRight: '5px',
        },
        phoneIcon: {
            paddingRight: '5px',
        },
        lineTwoFirstColumnDiv: {
            marginBottom: '7px',
            display: 'flex',
            alignItems: 'center',
            '@media (max-width:668px)': {
                marginBottom: '0',
            },
        },
        lineTwoFirstColumnDivSecond: {
            display: 'flex',
            alignItems: 'center',
        },
        lineTwoSecondColumnDiv: {
            marginBottom: '7px',
        },
        reminderLabel: {
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: '11px',
            fontWeight: 'bold',
        },
        arrowBlock: {
            display: 'flex',
            justifyContent: 'flex-end',
        },
        slider: {
            color: global.config.colors.purple,
            '&:hover': {
                color: global.config.colors.red,
            },
        },
        confirmedBlock: {},
        clientNameLine: {},
        truckImgWrapper: {},
        addressWrapper: {},
        arrowBlockShowClientDetail: {
            '@media (min-width:668px)': {
                display: 'none',
            },
        },
    };
    
    // –µ—Å–ª–∏ —ç—Ç–æ –Ω–µ –¥–µ—Ç–∞–ª—å–Ω–æ–µ —Ä–∞—Å–∫—Ä—ã–≤–∞—é—â–µ–µ—Å—è –º–æ–± –æ–∫–Ω–æ, –Ω–æ –ø–æ—á—Ç–∏ –≤—Å—ë —Å–∫—Ä—ã–≤–∞—Ç—å
    if (!props.mobileDetailed) {
        styleObject.truckImgWrapper = {
            ...styleObject.truckImgWrapper,
            '@media (max-width:668px)': {
                display: 'none',
            },
        };
        
        styleObject.addressWrapper = {
            ...styleObject.addressWrapper,
            '@media (max-width:668px)': {
                display: 'none',
            },
        };
        
        styleObject.confirmedBlock = {
            ...styleObject.confirmedBlock,
            '@media (max-width:668px)': {
                display: 'none',
            },
        };
        
        styleObject.lineThreeSecondColumn = {
            ...styleObject.lineThreeSecondColumn,
            '@media (max-width:668px)': {
                display: 'none',
            },
        };
        
        styleObject.clientNameLine = {
            ...styleObject.clientNameLine,
            '@media (max-width:668px)': {
                marginTop: '7px',
                marginBottom: '3px',
            },
        };
        
        styleObject.arrowBlock = {
            ...styleObject.arrowBlock,
            '@media (max-width:668px)': {
                display: 'none',
            },
        };
        
        styleObject.lineTwoFirstColumnDivSecond = {
            ...styleObject.lineTwoFirstColumnDivSecond,
            '@media (max-width:668px)': {
                display: 'none',
            },
        };
        
        styleObject.lineThreeFirstColumn = {
            ...styleObject.lineThreeFirstColumn,
            '@media (max-width:668px)': {
                display: 'none',
            },
        };
        
        styleObject.lineTwoThirdColumn = {
            ...styleObject.lineTwoThirdColumn,
            '@media (max-width:668px)': {
                display: 'none',
            },
        };
        
        styleObject.lineTwoSecondColumn = {
            ...styleObject.lineTwoSecondColumn,
            '@media (max-width:668px)': {
                display: 'none',
            },
        };
        
        styleObject.lineTwoFirstColumn = {
            ...styleObject.lineTwoFirstColumn,
            '@media (max-width:668px)': {
                flexBasis: '60%',
            },
        };
        
        styleObject.lineThree = {
            ...styleObject.lineThree,
            '@media (max-width:668px)': {
                flexBasis: '60%',
            },
        };
        
        styleObject.lineFour = {
            ...styleObject.lineFour,
            '@media (max-width:668px)': {
                position: 'absolute',
                top: '20%',
                right: '15px',
            },
        };
    }
    
    return styleObject;
    
});

export default MakeOrderClientCreated;
