import {ADD_NEW_CLIENT, SHOW_WHATS_APP, SHOW_DELIVER} from '../types/clients/makeClientTypes';

export const addClient = (userData) => async dispatch => {
  dispatch({
    type: ADD_NEW_CLIENT,
    payload: userData
  });
};

export const showWhatsApp = (data) => async dispatch => {
  dispatch({
    type: SHOW_WHATS_APP,
    payload: data,
  });
};

export const showDeliver = (data) => async dispatch => {
  dispatch({
    type: SHOW_DELIVER,
    payload: data,
  });
};
