import {
    SET_CURRENT_PRODUCT_CATEGORY,
    SET_LOADING_PRODUCTS,
    GET_SAVED_PRODUCTS,
} from '../types/Products/ProductListTypes';

import axios from 'axios';

export const getSavedProduct = (id = 4) => async dispatch => {

    try {
        dispatch(setLoadingProducts(true));
        const savedProducts = await axios.get('/product',
            {params: {'category_id': id}});
        dispatch(setLoadingProducts(false));
        dispatch({
            type: GET_SAVED_PRODUCTS,
            payload: savedProducts.data,
        });
    } catch (err) {
        console.error(err.response.data);
    }

};

export const setLoadingProducts = (userData) => async dispatch => {
    dispatch({
        type: SET_LOADING_PRODUCTS,
        payload: userData,
    });
};

export const setCurrentProductCategory = (el) => async dispatch => {
    dispatch({
        type: SET_CURRENT_PRODUCT_CATEGORY,
        payload: el,
    });
};