import React, {useState} from 'react';
import styles from '../../css/components/downArrow.module.css';

const DownArrow = ({isOpen, initialState = true}) => {
  const [open, setOpen] = useState(initialState);

  const toggleClass = () => {
    setOpen(!open);
    isOpen(!open)
  }

  return (
      <div className={styles.arrowBlock}>
        <div className={styles.shadowBlock}/>
        <a href='#' className={styles.arrowIcon} onClick={toggleClass}>
          <span className={`${styles.leftBar} ${open ? styles.leftBarOpen : ''}`}/>
          <span className={`${styles.rightBar} ${open ? styles.rightBarOpen : ''}`}/>
        </a>
      </div>
  )
}

export default DownArrow;
