import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import SalesSearchField from "./SalesSearchField";
import SalesItem from "./SalesItem";
import {nanoid} from 'nanoid';


const LeftBottomBlockSales = () => {
    const classes = useStyles();
    const salesNumber = [1,2,3,4];

    return (
        <>
            <div className={classes.summaryRightPanelBlock}>
                <div className={classes.textLine}><span className={classes.discount}>10-75%</span> –°–∫–∏–¥–∫–∏ –Ω–∞ –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç—ã
                    –æ—Ç –ø–∞—Ä—Ç–Ω—ë—Ä–æ–≤
                </div>
                <div className={classes.searchLine}><SalesSearchField/></div>
            </div>
            <div className={classes.salesWrapper}>
                {salesNumber.map(() => <SalesItem key={nanoid(5)} />)}
            </div>
        </>
    )
}

const useStyles = makeStyles((theme) => ({
    discount: {
        color: '#EF3F34',
        fontSize: '1.3rem',
        fontWeight: '700',
        marginRight: '1%'
    },
    textLine: {
        fontSize: '0.8rem',
        textAlign: 'center',
        borderBottom: '1px solid #000',
        paddingBottom: '7px',
        marginTop: '10px',
        marginBottom: '5px',
        width: '92%',
    },
    searchLine: {
        flexBasis: '92%'
    },
    summaryRightPanelBlock: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
        border: '1px solid #a7a0a0',
        padding: '0 2%',
        borderTopRightRadius: '15px',
        borderTopLeftRadius: '15px',
        boxShadow: ' 0px 10px 15px -5px rgba(0,0,0,0.5)',
        [theme.breakpoints.down('xs')]: {
            display: 'none'
        }
    },
    showMobileOrdersClass: {
        position: 'fixed',
        width: '101vw',
        left: '-1px',
        backgroundColor: '#fff',
        zIndex: '9',
        top: '32%',
        transition: 'bottom .20s',
        borderTopRightRadius: '15px',
        borderTopLeftRadius: '15px',
    },
    customButtonFirst1: {
        marginLeft: "3%"
    },
    salesWrapper: {
        display:"flex",
        justifyContent:"center",
        flexWrap:"wrap",
        marginTop: '5px',
        [theme.breakpoints.down('xs')]: {
            display: 'none'
        }
    }
}))

export default LeftBottomBlockSales;
