import React, {useEffect, useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import PlusRoundButton from '../../../../components/common/PlusRoundButton';
import IconButton from '@material-ui/core/IconButton';
import axios from 'axios';
import {useDispatch, useSelector} from 'react-redux';
import {
  deleteNewMaterialStore,getSavedMaterialStore,setCurrentMaterialStoreCategory
} from '../../../../actions/ingredients/materialStore/materialStoreActions';
import ModalAdd from '../../../../components/common/ModalAdd';
import AddCategory from '../../../../components/common/AddCategory';
import {
  StyledMenu,
  StyledMenuItem,
} from '../../../../components/materialize/StyledCustomMenu';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import DeleteIcon from '@material-ui/icons/Delete';
import ListItemText from '@material-ui/core/ListItemText';
import DialogModal from '../../../../components/common/DialogModal';
import DeleteCategoryList from '../../../../components/common/DeleteCategoryList';
import TransitionUp from '../../../../components/common/TransitionUp';
import ButtonMore from '../../../../components/common/Icons/ButtonMore';

const Transition = TransitionUp;

const MaterialStoreLeftBlock = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);
  const [openDelete, setOpenDelete] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [clickedCategory, setClickedCategory] = useState(null);
  let [materialStoreCategories, setMaterialStoreCategories] = useState([]);
  const currentMaterialStoreCategory = useSelector(
      state => state.ingredients.materialStoreList.currentMaterialStoreCategory);

  const getCategories = () => {
    try {
      //setLoading(true);
      axios.get('/categories',
          {
            params: {
              'direction_name': 'material',
            },
          }).then(data => {
        setMaterialStoreCategories(data.data);
      });
    } catch (e) {
      console.error(e);
    }
  };

  useEffect( () => {
    getCategories();
  }, []);

  const setCategory = (el) => {
    dispatch(deleteNewMaterialStore());
    dispatch(getSavedMaterialStore(el.id));
    dispatch(setCurrentMaterialStoreCategory(el));
  };

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpenDelete = () => {
    setOpenDelete(true);
  };

  const handleCloseDelete = () => {
    setOpenDelete(false);
  };

  const handleClickDeletePrompt = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleCloseDeletePrompt = () => {
    setAnchorEl(null);
  };

  const addFunction = (title) => {
    AddCategory({
      title:title,
      parent_id: null, // –ø–æ–∫–∞ –Ω–µ—è—Å–Ω–æ –∫–∞–∫–æ–π parent_id
      direction_name: "material" //  material
    },function() {
      getCategories();
      handleCloseDeletePrompt();
    })
  };

  return (
      <div className={classes.materialsLeftBlock}>
        {materialStoreCategories.map((el, index) =>
            <div className={classes.lineOne} key={el.id}>

              <div className={el.id === currentMaterialStoreCategory.id ? classes.columnOneChoosen : classes.columnOne}
                   onClick={() => setCategory(el)}>{el.title}</div>
              <div className={classes.columnLine}></div>
              <div className={classes.columnTwo}>{el.count ? el.count: 0}</div>
              {/*–ø–æ–∫–∞–∑—ã–≤–∞—Ç—å —Ç—Ä–∏ —Ç–æ—á–∫–∏(–≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç—å —É–¥–∞–ª–µ–Ω–∏—è —Ç–æ–ª—å–∫–æ –¥–ª—è –∫–∞—Ç–µ–≥–æ—Ä–∏–π –Ω–∞—á–∏–Ω–∞—è —Å —Ç—Ä–µ—Ç—å–µ–π)*/}
              {index > 1 ?
                  <ButtonMore onClick={(event) => {setClickedCategory(el.id); return handleClickDeletePrompt(event)}}/> :
                  <div className={classes.emptySpace}></div>}
            </div>,
        )}
        {materialStoreCategories.length < 6 ? <IconButton onClick={handleOpen}><PlusRoundButton/></IconButton> : ''}

        <ModalAdd open={open} handleClose={handleClose}  addFunction={addFunction}
                  windowTitle="–î–æ–±–∞–≤–∏—Ç—å –º–∞—Ç–µ—Ä–∏–∞–ª" />
        <StyledMenu
            TransitionComponent={Transition}
            id="customized-menu"
            anchorEl={anchorEl}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={handleCloseDeletePrompt}
            onClick={handleCloseDeletePrompt}
        >
          <StyledMenuItem onClick={handleOpenDelete}>
            <ListItemIcon color="primary">
              <DeleteIcon fontSize="small"/>
            </ListItemIcon>
            <ListItemText primary="–£–¥–∞–ª–∏—Ç—å –∫–∞—Ç–µ–≥–æ—Ä–∏—é"/>
          </StyledMenuItem>
          {/*<StyledMenuItem>
                     <ListItemIcon>
                        <ReceiptSharp fontSize="small"/>
                     </ListItemIcon>
                     <ListItemText primary="–ü–µ—Ä–µ–∏–º–µ–Ω–æ–≤–∞—Ç—å –∫–∞—Ç–µ–≥–æ—Ä–∏—é"/>
                 </StyledMenuItem> */}
        </StyledMenu>

        <DialogModal open={openDelete} Transition={Transition}
                     handleCloseNo={handleCloseDelete}
                     handleCloseYes={() => DeleteCategoryList(clickedCategory,
                         getCategories, handleCloseDelete)}
                     text="–£–¥–∞–ª–∏—Ç—å —ç—Ç—É –∫–∞—Ç–µ–≥–æ—Ä–∏—é?"/>
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  materialsLeftBlock: {
    marginBottom: '40px',
    [theme.breakpoints.down('xs')]: {
      height: '100vh',
    },
  },
  lineOne: {
    display: 'flex',
    justifyContent: 'space-between',
    fontWeight: 'bold',
    alignItems: 'center',
    marginBottom: '5px',
  },
  columnOneChoosen: {
    cursor: 'pointer',
    width: '45%',
    color: 'red'
  },
  columnOne: {
    cursor: 'pointer',
    width: '45%',
  },
  columnLine: {
    height: '2px',
    borderBottom: '1px dotted #000',
    flexBasis: '36%',
    cursor: 'pointer',
  },
  emptySpace: {
    width: '23px'
  }
}));

export default MaterialStoreLeftBlock;
